<template>
  <div>
    <div class="table-scroll inventory-table">
      <table class="table table-bordered table-sm table-hover table-items">
        <thead class="thead-dark">
          <tr style="background-color: #eeeeee;">
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="name"
              class="inventory-th"
            >Name of items</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Main or parent category of items"
              class="inventory-th"
            >Category</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="subcategory"
              class="inventory-th"
            >SubCategory</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Co category is visible in tab activity of app"
              class="inventory-th"
            >Co-category</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Product ID"
              class="inventory-th"
            >Product Id</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Stock depicts the number of particular item in inventory"
              class="inventory-th"
            >Stock</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Quantity"
              class="inventory-th"
            >Qty</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Unit(kg, gram, dozens, etc)"
              class="inventory-th"
            >Unit</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Printed Price of an item in rupees"
              class="inventory-th"
            >₹ MRP</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Current price of items set by us"
              class="inventory-th"
            >Current Price ( ₹ )</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Discount set by us"
              class="inventory-th"
            >Dis(%)</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Discounted Value Visibility in App"
              class="inventory-th"
            >Discounted Value in App</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Barcode of the product"
              class="inventory-th"
            >Barcode</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Image"
              class="inventory-th"
            >Image</th>

            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Uploaded At"
              class="inventory-th"
            >Upload Info</th>
            <th
              data-toggle="tooltip"
              data-placement="bottom"
              title="Action on items"
              class="inventory-th"
            >Actions</th>
          </tr>
        </thead>
        <tbody v-if="items.length > 0 && user" class="table-hover">
          <tr v-for="(item, index) in items" :key="index" :class="getRowClass(index)">
            <!-- @mouseenter="showHideImage(index)"
            @mouseleave="showHideImage(index)"-->
            <td style="font-size: 12px;">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'name' &&
                  editItemProductId == item.productId
                "
              />
              <span @dblclick="editProperty(item.productId, 'name', item.name)" v-else>
                <i class="fas fa-circle text-success" aria-hidden="true" v-if="item.active == 1"></i>
                <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                {{ item.name }}
              </span>
            </td>
            <td
              class="font-weight-medium"
              colspan="3"
              v-if="
                editItemProperty == 'cocat' &&
                editItemProductId == item.productId
              "
            >
              <span>{{ item.itemId }}</span>
              <button
                type="button"
                class="btn btn-outline-danger btn-sm btn-block font-weight-bold"
                data-toggle="modal"
                data-target="#itemCategories"
                @click="fetchCategories"
              >Edit Item Code</button>

              <div v-if="cocategory != null">
                New ItemId: {{ cocategories[index].itemId }}
                <button
                  type="button"
                  class="btn btn-success btn-sm mt-2 float-right"
                  @click="editPropertyEnd(index)"
                >
                  <i class="fa fa-check pr-2"></i>Save
                </button>
              </div>
            </td>
            <td class="font-weight-medium" v-else>
              <span
                @dblclick="editProperty(item.productId, 'cocat', item.itemId)"
              >{{ item.catName }}</span>
            </td>
            <td
              class="font-weight-medium"
              v-if="
                !(
                  editItemProperty == 'cocat' &&
                  editItemProductId == item.productId
                )
              "
            >
              <span
                @dblclick="editProperty(item.productId, 'cocat', item.itemId)"
              >{{ item.subcatName }}</span>
            </td>
            <td
              class="font-weight-medium"
              v-if="
                !(
                  editItemProperty == 'cocat' &&
                  editItemProductId == item.productId
                )
              "
            >
              <span
                @dblclick="editProperty(item.productId, 'cocat', item.itemId)"
              >{{ item.cocatName }}</span>
            </td>
            <td class="font-weight-bold small">
              {{ item.productId }}
              <i
                class="far fa-2x fa-copy pl-1"
                style="cursor: pointer;"
                @click="copyProductId(item.productId)"
              ></i>
              <input type="hidden" :value="item.productId" :id="item.productId" />
            </td>
            <td class="font-weight-bold">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'stock' &&
                  editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'stock', item.stock)"
                v-else
              >{{ item.stock }}</span>
            </td>
            <td class="font-weight-bold">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'qty' &&
                  editItemProductId == item.productId
                "
              />
              <span @dblclick="editProperty(item.productId, 'qty', item.qty)" v-else>{{ item.qty }}</span>
            </td>
            <td class="font-weight-normal">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'unit' &&
                  editItemProductId == item.productId
                "
              />
              <span
                @dblclick="editProperty(item.productId, 'unit', item.unit)"
                v-else
              >{{ item.unit }}</span>
            </td>
            <td class="font-weight-bold">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'mrp' &&
                  editItemProductId == item.productId
                "
              />
              <span @dblclick="editProperty(item.productId, 'mrp', item.mrp)" v-else>{{ item.mrp }}</span>
            </td>
            <td class="font-weight-bold">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'currentPrice' &&
                  editItemProductId == item.productId
                "
              />
              <span
                @dblclick="
                  editProperty(
                    item.productId,
                    'currentPrice',
                    item.currentPrice
                  )
                "
                v-else
              >{{ item.currentPrice }}</span>
            </td>
            <td class="font-weight-bold">{{ item.discount }}</td>
            <td class="font-weight-bold">
              <span>
                <input
                  type="checkbox"
                  id="disVal"
                  v-model="item.disVal"
                  true-value="1"
                  false-value="0"
                  @change="editDisValState(index)"
                  title="This checkbox will change the active state"
                />
              </span>
            </td>

            <td class="font-weight-bold">
              <input
                type="text"
                v-model="editItemPropertyValue"
                @blur="editPropertyEnd(index)"
                v-if="
                  editItemProperty == 'barcode' &&
                  editItemProductId == item.productId
                "
              />
              <span
                @dblclick="
                  editProperty(item.productId, 'barcode', item.barcode)
                "
                v-else
              >
                <span v-if="item.barcode != null">{{ item.barcode }}</span>
                <span v-else>N/A</span>
              </span>
            </td>
            <td class="small">
              <a :href="item.image" v-if="item.image" target="_blank" class="imageName">
                <img :src="item.image" class="img-fluid" />
                <!-- v-if="item.showImage == 1" -->
                <!-- <span v-if="item.showImage == 0">{{ item.imageName }}</span> -->
              </a>
              <span v-else>No image</span>
            </td>
            <td style="font-size: 10px;">
              <span>
                {{
                item.uploadedByName
                }}
              </span>
              <br />
              {{ item.uploadedAt }}
            </td>
            <td>
              <span class="d-inline-block">
                <a
                  class="fa fa-trash text-dark"
                  href="#"
                  data-toggle="modal"
                  data-target="#deleteItem"
                  data-placement="bottom"
                  title="delete this item"
                  @click="deleteItem(index)"
                ></a>

                <router-link
                  :to="{
                    name: 'editItem',
                    params: { productId: item.productId },
                  }"
                  tag="a"
                  target="_blank"
                >
                  <i class="fa fa-pencil-alt text-dark"></i>
                </router-link>

                <input
                  type="checkbox"
                  id="active"
                  v-model="item.active"
                  @click.prevent="editActiveState(index)"
                  title="This checkbox will change the active state"
                />
              </span>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="10" class="border-0">No Item to show!</td>
          </tr>
        </tbody>
        <!-- loader -->
      </table>
    </div>

    <Loader :disable="loading" :success="success" :message="message" />
    <!-- Modal -->
    <div id="itemCategories" class="modal fade" role="dialog">
      <div class="modal-dialog modal-dialog-scrollable">
        <!-- Modal content-->
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold">
              <i class="fas fa-clipboard-list pr-3"></i>Choose Item Id
              <small>(Item id is predefined)</small>
            </h5>
            <button type="button" class="close" data-dismiss="modal">&times;</button>
          </div>
          <div class="modal-body" style="position: relative;">
            <div>
              <label>Choose Category</label>
              <select
                v-model="category"
                class="form-control form-control-sm"
                @change="getSubCategories"
              >
                <option
                  v-for="(category, index) in categories"
                  :key="index"
                  :value="index"
                >{{ category.name }}</option>
              </select>
            </div>
            <div>
              <label>Choose Sub Category</label>
              <select
                aria-placeholder="This will get filled when you select category"
                v-model="subcategory"
                class="form-control form-control-sm"
                :disabled="category == null && !ModalContentLoading"
                @change="getCoCategories"
                v-if="subcategories"
              >
                <option
                  v-for="(subcategory, index) in subcategories"
                  :key="index"
                  :value="index"
                >{{ subcategory.name }}</option>
              </select>
              <span v-else>There are no SubCategories in this yet</span>
            </div>
            <div>
              <label>Choose Item Id</label>
              <select
                v-model="cocategory"
                class="form-control form-control-sm"
                :disabled="subcategory == null && !ModalContentLoading"
                @change="setCocategory()"
                v-if="cocategories"
              >
                <option
                  v-for="(cocategory, index) in cocategories"
                  :key="index"
                  :value="index"
                >{{ cocategory.itemId }} {{ cocategory.name }}</option>
              </select>
              <span v-else>This have no items yet</span>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger btn-sm" data-dismiss="modal">
              <i class="fas fa-times-circle pr-2"></i>Close
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import firebase from "firebase";
import db from "@/firebaseInit";
import { bus } from "@/main";
import { firestorage } from "@/firebaseInit";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import axios from "axios";

import { inventoryDataMixin } from "@/components/Mixins/Inventory/inventoryDataMixin.js";

export default {
  name: "InventoryDashboard",
  mixins: [inventoryDataMixin],
  props: {
    products: Array,
    disable: Boolean,
    pageSize: {
      type: Number,
      required: false,
      default: 10
    }
  },

  data() {
    return {
      tdClass: null,
      deleteClass: [],
      editClass: [],
      message: null,
      success: false,

      editItemProperty: null,
      editItemPropertyValue: null,
      editItemProductId: null,
      editItemPropertyOrgValue: null
    };
  },

  computed: {
    items: {
      get() {
        return this.products;
      },
      set(newValue) {
        this.products = newValue;
      }
    },

    loading: {
      get() {
        return this.disable;
      },
      set(newValue) {
        this.disable = newValue;
      }
    }
  },

  created() {
    bus.$on("searchedItems", data => {
      this.setItems(data["items"]);
    });
  },

  mounted() {
    Vue.nextTick(function() {
      $('[data-toggle="tooltip"]').tooltip();
    });
  },

  methods: {
    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },

    deleteItem(index) {
      if (
        this.user &&
        this.getUserPermission(
          this.user.permissions.inventoryProducts,
          "delete"
        )
      ) {
        if (confirm("You are about to delete " + this.items[index].name)) {
          this.loading = true;
          let url = domain + "item/" + this.items[index].productId;
          axios
            .post(
              url,
              {
                _method: "delete"
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              if (res.data == "done") {
                this.message = "Deleted successfully";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
                this.items.splice(index, 1);
              } else {
                alert(res.data);
              }
            })
            .catch(err => {
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      } else {
        alert("You are not allowed to delete this");
      }
    },

    editActiveState(index) {
      if (
        this.user &&
        this.getUserPermission(this.user.permissions.inventoryProducts, "put")
      ) {
        if (confirm("You are about to change activation of item")) {
          if (
            this.items[index].catActive == "1" &&
            this.items[index].subcatActive == "1" &&
            this.items[index].cocatActive == "1"
          ) {
            this.loading = true;
            let url = domain + "item/" + this.items[index].productId;

            let setActive = 0;
            if (this.items[index].active == 1) {
              setActive = 0;
            } else {
              setActive = 1;
            }

            axios
              .post(
                url,
                {
                  _method: "put",
                  active: setActive
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                if (res.data) {
                  Vue.set(this.items[index], "active", res.data.active);
                }

                this.message = "Updated successfully";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                this.message = "Some error occured";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          } else {
            if (this.items[index].catActive != "1") {
              alert(
                "It's category is inactive. So, You can't activate the item."
              );
            } else if (this.items[index].subcatActive != "1") {
              alert(
                "It's subcategory is inactive. So, You can't activate the item."
              );
            } else if (this.items[index].cocatActive != "1") {
              alert(
                "It's cocategory is inactive. So, You can't activate the item."
              );
            }

            Vue.set(this.items[index], active, 0);
          }
        }
      }
    },

    editDisValState(index) {
      if (
        this.user &&
        this.getUserPermission(this.user.permissions.inventoryProducts, "put")
      ) {
        if (
          confirm(
            "You are about to change discount visibility of discount value in app"
          )
        ) {
          if (this.user) {
            this.loading = true;
            let url = domain + "item/" + this.items[index].productId;

            axios
              .post(
                url,
                {
                  _method: "put",
                  dis_val: this.items[index].disVal
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                if (res.data) {
                  Vue.set(this.items[index], "disVal", res.data.dis_val);
                }

                this.message = "Updated successfully";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                this.message = "Some error occured";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          }
        }
      }
    },

    editProperty(productId, property, value) {
      if (
        this.user &&
        this.getUserPermission(this.user.permissions.inventoryProducts, "put")
      ) {
        this.editItemProperty = property;
        this.editItemPropertyValue = value;
        this.editItemPropertyOrgValue = value;
        this.editItemProductId = productId;
      }
    },

    editPropertyEnd(index) {
      if (this.editItemProperty == "cocat") {
        this.editItemPropertyValue = this.cocategories[this.cocategory].itemId;
      }
      if (
        this.user &&
        this.editItemPropertyOrgValue != this.editItemPropertyValue &&
        this.editItemPropertyValue != null &&
        this.editItemPropertyValue != ""
      ) {
        this.loading = true;
        this.message = "Uploading Data, Kripya Shanti banaye rkhe...";
        let url = domain + "item/" + this.editItemProductId;
        if (this.editItemProperty == "name") {
          axios
            .post(
              url,
              {
                _method: "put",
                name: this.editItemPropertyValue
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.items[index].name = res.data.name;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "stock") {
          axios
            .post(
              url,
              {
                _method: "put",
                stock: this.editItemPropertyValue
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.items[index].stock = res.data.stock;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "qty") {
          axios
            .post(
              url,
              {
                _method: "put",
                qty: this.editItemPropertyValue
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.items[index].qty = res.data.qty;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "mrp") {
          axios
            .post(
              url,
              {
                _method: "put",
                MRP: this.editItemPropertyValue
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              Vue.set(this.items[index], "mrp", res.data.MRP);
              Vue.set(this.items[index], "currentPrice", res.data.currentPrice);
              Vue.set(this.items[index], "discount", res.data.discount);
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "currentPrice") {
          if (this.editItemPropertyValue <= this.items[index].mrp) {
            axios
              .post(
                url,
                {
                  _method: "put",
                  currentPrice: this.editItemPropertyValue
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                Vue.set(this.items[index], "mrp", res.data.MRP);
                Vue.set(
                  this.items[index],
                  "currentPrice",
                  res.data.currentPrice
                );
                Vue.set(this.items[index], "discount", res.data.discount);
                this.editItemProperty = null;
                this.editItemPropertyValue = null;
                this.editItemProductId = null;
                this.editItemPropertyOrgValue = null;

                this.message = "Data Update Success";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                //// to show
                this.message = "Some Error Occured!!";
                this.success = true;
                this.loading = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          } else {
            alert("Your current price is exceeding over MRP");
            this.loading = false;
          }
        } else if (this.editItemProperty == "unit") {
          axios
            .post(
              url,
              {
                _method: "put",
                unit: this.editItemPropertyValue
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.items[index].unit = res.data.unit;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "barcode") {
          axios
            .post(
              url,
              {
                _method: "put",
                barcode: this.editItemPropertyValue
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.items[index].barcode = res.data.barcode;
              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else if (this.editItemProperty == "cocat") {
          axios
            .post(
              url,
              {
                _method: "put",
                itemId: this.cocategories[this.cocategory].itemId
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              this.items[index].itemId = this.cocategories[
                this.cocategory
              ].itemId;
              this.items[index].cocatName = this.cocategories[
                this.cocategory
              ].name;
              this.items[index].subcatName = this.subcategories[
                this.subcategory
              ].name;
              this.items[index].catName = this.subcategories[
                this.category
              ].name;

              this.category = null;
              this.subcategory = null;
              this.cocategory = null;

              this.editItemProperty = null;
              this.editItemPropertyValue = null;
              this.editItemProductId = null;
              this.editItemPropertyOrgValue = null;

              this.message = "Data Update Success";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              //// to show
              this.message = "Some Error Occured!!";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      } else {
        if (this.editItemPropertyOrgValue == this.editItemPropertyValue) {
          this.editItemProperty = null;
          this.editItemPropertyValue = null;
          this.editItemProductId = null;
          this.editItemPropertyOrgValue = null;
        }
      }
    },

    getRowClass(index) {
      if (this.items[index].stock < 5) {
        this.deleteClass[index] = "fa fa-trash modal-trigger text-white";
        this.editClass[index] = "fa fa-pencil-alt text-white";
        return "bg-danger text-white";
      } else {
        this.deleteClass[index] = "fa fa-trash modal-trigger text-red";
        this.editClass[index] = "fa fa-pencil-alt text-red";
        return;
      }
    },

    setItems(items) {
      this.items = items;
    },

    showHideImage(index) {
      if (this.items[index].showImage == 0) {
        Vue.set(this.items[index], "showImage", 1);
      } else {
        Vue.set(this.items[index], "showImage", 0);
      }
    },

    updateSize(size) {
      if (size > 0) {
        this.size = size;

        //updating page counter
        let count = Math.ceil(this.products.length / this.size);
        store.dispatch("patchinventoryPageCount", count);
      }
    }
  }
};
</script>

<style scoped>
.loading {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.inventory-table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 60px;
  height: 80vh;
}

.inventory-table tbody td {
  font-weight: 400;
}

.footer {
  height: 50px;
}

.inventory-th {
  position: sticky;
  background-color: #000000;
  font-size: 14px;
  opacity: 1;
  font-weight: 500;
  z-index: 4;
}

.imageName {
  text-decoration: none;
  color: inherit;
}

.table-items {
  font-size: small;
}
/*
@media (max-width: 768px) {
  th {
    position: sticky;
    top: 50px;
  }
} */
</style>



