<script>
import Vue from "vue";
import $ from "jquery";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith,
  thresholdShippingCost
} from "@/store/variable";
import store from "@/store/store";
import cartRightPanel from "./cartRightPanel.vue";
import firebase from "firebase";
import { placeOrderMixin } from "@/components/Mixins/placeOrderMixin.js";
import axios from "axios";

export default {
  name: "PlaceOrderDashboard",
  mixins: [placeOrderMixin],
  components: {
    cartRightPanel
  },

  data() {
    return {
      success: false,
      message: null
    };
  },

  methods: {
    addOrder(ordererId, deliveryAddress) {
      console.log(ordererId);
      let orderedItems = null;
      let quantity = "";

      this.cart.forEach((item, index) => {
        if (orderedItems == null) {
          orderedItems = item.productId;
        } else {
          orderedItems = orderedItems + "," + item.productId;
        }

        if (quantity == null || quantity == "") {
          quantity = item.quantityToBuy;
        } else {
          quantity = quantity + "," + item.quantityToBuy;
        }
      });

      let url = domain + "order";
      axios
        .post(
          url,
          {
            userId: ordererId,
            orderedItems: orderedItems,
            quantity: quantity,
            paidAmount: 0,
            paymentMode: this.$refs.cartRightPanel.paymentMode,
            deliveryAddress: deliveryAddress,
            placedFrom: this.$refs.cartRightPanel.placedFrom,
            transationId: null
          },
          {
            withCredentials: true,
            headers: {
              from: "webapp",
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          if (res.data) {
            //order placed ka message

            this.items = [];
            this.subTotal = 0;
            this.totalPayableAmount = 0;
            this.shippingCharges = 0;
            this.$refs.cartRightPanel.deliveryAddress = 0;
            this.$refs.cartRightPanel.ordererId = null;
            this.$refs.cartRightPanel.ordererFound = false;
            this.$refs.cartRightPanel.deliveryAddress = [];

            this.message = "Order has been Placed successfully";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          }
        })
        .catch(err => {
          this.message = "Some error occured";
          this.success = true;
          this.loading = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },
    async placeOrder() {
      this.loading = true;
      let deliveryAddress = this.$refs.cartRightPanel.deliveryAddress;
      let ordererId = this.$refs.cartRightPanel.ordererId;

      if (
        (ordererId == null ||
          this.$refs.cartRightPanel.ordererFound == false) &&
        (this.$refs.cartRightPanel.mobileNumber ||
          this.$refs.cartRightPanel.email) &&
        this.$refs.cartRightPanel.name
      ) {
        //add user first
        await this.$refs.cartRightPanel
          .addOrderer()
          .then(res => {
            ordererId = res.data.uuid;
          })
          .catch(err => {
            this.message = "There's some problem in storing user";
            this.success = true;
            this.loading = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }

      if (
        (this.$refs.cartRightPanel.mobileNumber ||
          this.$refs.cartRightPanel.email) &&
        this.$refs.cartRightPanel.name
      ) {
        //adding delivery address first
        if (
          deliveryAddress <= 0 &&
          ordererId &&
          this.$refs.cartRightPanel.pincode
        ) {
          await this.$refs.cartRightPanel
            .addAddress(ordererId)
            .then(res => {
              deliveryAddress = res.data.id;
            })
            .catch(err => {
              this.message = "There's some problem in storing address";
              this.success = true;
              this.loading = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        } else {
          if (deliveryAddress <= 0 && !this.$refs.cartRightPanel.pincode) {
            this.loading = false;
            alert("Please Choose a Valid Address");
            return;
          }
        }

        // add order
        if (
          deliveryAddress > 0 &&
          ordererId != null &&
          this.cart.length > 0 &&
          (this.$refs.cartRightPanel.mobileNumber ||
            this.$refs.cartRightPanel.email) &&
          this.$refs.cartRightPanel.name
        ) {
          this.addOrder(ordererId, deliveryAddress);
        } else {
          this.loading = false;
          if (!ordererId) {
            alert("Have you checked the user first?");
          } else if (deliveryAddress <= 0) {
            alert("Please select a valid address");
          } else if (this.cart.length <= 0) {
            alert("Your Cart is empty");
          } else if (!ordererId) {
            alert("Please check user's details");
          }
        }
      } else {
        alert("User's details are incomplete");
      }
    }
  }
};
</script>

<style scoped>
.add-item-button {
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 800;
}

.button-trans {
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
}

.inventoryFooter {
  background: #e0e0e0;
  bottom: 0;
  /* margin-left:220px */
}

.place-fixed {
  right: 10px;
}

.product img {
  width: 72px;
  height: 72px;
}

.product span {
  font-size: 0.9em;
}

.search_icon {
  height: 40px;
  width: 40px;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: white;
}

.search-bar {
  background: #eae7e7;
}

.search-bar:focus {
  background: #eeeeee;
}

.cart {
  position: relative;
}

.cart-items {
  position: absolute;
  top: 0.5px;
  right: -0.5px;
}
</style>



