<template>
  <div class="container-fluid">
    <DashBoard :newOrders="orders" :disable="disable" @fetchOrders="fetchOrders" />
  </div>
</template>
<script>
import DashBoard from "./DashBoard.vue";
import Vue from "vue";
import db from "@/firebaseInit";
import firebase from "firebase";
import { firestorage } from "@/firebaseInit";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";
import store from "@/store/store";
import axios from "axios";

export default {
  name: "NewOrders",
  components: {
    DashBoard
  },

  data() {
    return {
      orders: [],
      disable: false
    };
  },

  computed: {
    size() {
      return store.state.orderPageSize;
    }
  },

  mounted() {
    this.fetchOrders();
  },

  methods: {
    fetchOrders(page) {
      if (!page) {
        page = 1;
      }

      let url = domain + "order";
      this.disable = true;
      axios
        .get(url, {
          params: {
            user: "budhbak",
            products: "budhbak",
            payment_mode: "budhbak",
            userAddress: "budhbak",
            order: "updated_at",
            orderWay: "desc",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let ordersList = [];
          res.data.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            dateTime.setHours(dateTime.getHours() + utcHours);
            dateTime.setMinutes(dateTime.getMinutes() + utcMins);
            let month = dateTime.getMonth() + 1;
            let date =
              dateTime.getDate() + "/" + month + "/" + dateTime.getFullYear();
            var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
            var hours = dateTime.getHours() % 12 || 12;
            let time =
              hours +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds() +
              " " +
              AmOrPm;

            let order = {
              orderId: doc.id,
              products: doc.products,
              orderedItemsCount: doc.orderedItemsCount,
              paymentMode: doc.payment_mode,
              orderDate: date,
              orderTime: time,
              payment: doc.totalPayableAmount,
              paidAmount: doc.paidAmount,
              orderStatus: doc.order_status,
              user: doc.user,
              deliveryAddress: doc.user_address,
              refundId: doc.refundId,
              checked: false
            };
            ordersList.push(order);

            store.dispatch("patchorderPageCount", res.data.last_page);
            store.dispatch("patchorderPageNumber", res.data.current_page);
            store.dispatch("patchtotalOrders", res.data.total);
          });

          this.orders = ordersList;
        });

      this.disable = false;
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



