import Vue from "vue";
import {
    domain,
    ContentType,
    AccessControlAllowOrigin,
    XRequestedWith,
    thresholdShippingCost
} from "@/store/variable";
import _ from "lodash";
import axios from "axios";


export const placeOrderMixin = {

    data() {
        return {
            itemSearched: false,
            typing: false,
            searchInput: null,
            loading: false,
            itemsOrCart: true,
            items: [],
            selectedAvailbilityOfItem: [],
            cart: [],
            subTotal: 0,
            totalPayableAmount: 0,
            shippingCharges: 0,

            thresholdForDeliveryCharge: 0,
            deliveryCharges: 0,

            searchBy: "barcode",
            itemSearched: false,

        }
    },

    mounted() {
        this.fetchThreshold();
    },

    beforeRouteLeave(to, from, next) {
        if (this.cart.length == 0) {
            next();
        } else {
            if (
                confirm(
                    "You have some items in cart. Leaving might loose them.Wanna Leave?"
                )
            ) {
                next();
            } else {
                next(false);
            }
        }
    },

    methods: {
        addToCart(index) {
            //adding to cart
            if (
                this.items[index][this.selectedAvailbilityOfItem[index]].quantityToBuy >
                0
            ) {
                if (
                    this.items[index][this.selectedAvailbilityOfItem[index]].inCart ==
                    false
                ) {
                    this.cart.unshift(
                        this.items[index][this.selectedAvailbilityOfItem[index]]
                    );
                    Vue.set(
                        this.items[index][this.selectedAvailbilityOfItem[index]],
                        "inCart",
                        true
                    );
                } else {
                    this.cart.forEach(element => {
                        if (
                            element.productId ==
                            this.items[index][this.selectedAvailbilityOfItem[index]].productId
                        ) {
                            element = this.items[index][
                                this.selectedAvailbilityOfItem[index]
                            ];
                        }
                    });
                }
                this.total();
            } else {
                if (
                    this.items[index][this.selectedAvailbilityOfItem[index]]
                    .quantityToBuy == 0
                ) {
                    alert("Please add some quantity to add it in cart");
                }
            }
        },

        calculateQuantity(index, type) {
            if (type == "items") {
                var quantity = (
                    this.items[index][this.selectedAvailbilityOfItem[index]].priceToBuy /
                    this.items[index][this.selectedAvailbilityOfItem[index]].currentPrice
                ).toFixed(2);

                if (
                    this.items[index][this.selectedAvailbilityOfItem[index]].stock <
                    quantity
                ) {
                    if (
                        confirm(
                            "Only " +
                            this.items[index][this.selectedAvailbilityOfItem[index]].stock +
                            " are available right now"
                        )
                    ) {
                        quantity = this.items[index][this.selectedAvailbilityOfItem[index]]
                            .stock;
                    } else {
                        quantity = 0;
                    }
                }

                Vue.set(
                    this.items[index][this.selectedAvailbilityOfItem[index]],
                    "quantityToBuy",
                    quantity
                );
            } else {
                var quantity = (
                    this.cart[index].priceToBuy / this.cart[index].currentPrice
                ).toFixed(2);

                if (this.cart[index].stock < quantity) {
                    if (
                        confirm(
                            "Only " + this.cart[index].stock + " are available right now"
                        )
                    ) {
                        quantity = this.cart[index].stock;
                    } else {
                        quantity = 0;
                    }
                }

                Vue.set(this.cart[index], "quantityToBuy", quantity);
            }
        },

        changeQuantity(index, array) {
            if (array == "cart") {
                this.total();
            }
        },

        copyProductId(id) {
            let prodID = "#" + id;
            let idToCopy = document.querySelector(prodID);
            idToCopy.setAttribute("type", "text");
            idToCopy.select();

            try {
                let successful = document.execCommand("copy");
                let message = successful ? "Copied!" : "Unable to Copy!";
                alert(message);
            } catch (err) {
                alert("Whoops! unable to copy");
            }

            idToCopy.setAttribute("type", "hidden");
            window.getSelection().removeAllRanges();
        },

        removeFromCart(index) {
            this.items.forEach(element => {
                element.forEach(item => {
                    if (item.productId == this.cart[index].productId) {
                        Vue.set(item, "quantityToBuy", 0);
                        Vue.set(item, "inCart", false);
                    }
                });
            });
            this.cart.splice(index, 1);
            this.total();
        },

        fetchThreshold() {
            let url = domain + "aboutUs";
            axios
                .get(url, {
                    params: {
                        name: 'thresholdForDeliveryCharge'
                    }
                })
                .then(res => {
                    this.thresholdForDeliveryCharge = parseInt(res.data);
                })
                .catch(err => {
                    // err
                });

            axios
                .get(url, {
                    params: {
                        name: 'deliveryCharges'
                    }
                })
                .then(res => {
                    this.deliveryCharges = parseInt(res.data);
                })
                .catch(err => {
                    // err
                });
        },

        searchItems() {
            this.loading = true;
            this.items = [];
            this.itemSearched = false;

            // loading content

            if (this.searchBy == "barcode") {
                this.searchByBarCode();
            } else {
                this.searchByName();
            }
        },

        searchByBarCode() {
            let url = domain + "item";
            axios
                .get(url, {
                    params: {
                        barcode: this.searchInput
                    },
                    headers: {
                        from: "webappOrder"
                    }
                })
                .then(res => {
                    this.itemSearched = true;
                    if (res.data) {
                        this.items = [];
                        this.selectedAvailbilityOfItem = [];
                        this.itemSearched = true;
                        res.data.forEach(doc => {
                            {
                                var itemArray = new Array();
                                let itemUrl = domain + "item";
                                axios
                                    .get(itemUrl, {
                                        params: {
                                            similarProductId: doc.similarProductId
                                        }
                                    })
                                    .then(query => {
                                        if (query.data) {
                                            query.data.forEach(similarDoc => {
                                                {
                                                    let inCart = false;
                                                    this.cart.forEach(element => {
                                                        if (element.productId == similarDoc.productId) {
                                                            inCart = true;
                                                        }
                                                    });
                                                    var item = new Array();
                                                    // image name
                                                    let imageName = null;
                                                    if (similarDoc.image) {
                                                        let start =
                                                            similarDoc.image.indexOf("items%2F") + 8;
                                                        let end = similarDoc.image.indexOf("?");
                                                        imageName = similarDoc.image.substring(start, end);
                                                        imageName = this.truncate(imageName);
                                                    }
                                                    item = {
                                                        productId: similarDoc.productId,
                                                        itemId: similarDoc.itemId,
                                                        name: similarDoc.name,
                                                        cat: similarDoc.allcat,
                                                        stock: similarDoc.stock,
                                                        unit: similarDoc.unit,
                                                        mrp: similarDoc.MRP,
                                                        currentPrice: similarDoc.currentPrice,
                                                        discount: similarDoc.discount,
                                                        image: similarDoc.image,
                                                        imageName: imageName,
                                                        qty: similarDoc.qty,
                                                        quantityToBuy: 0,
                                                        priceToBuy: 0,
                                                        inCart: inCart,
                                                        item_brand: similarDoc.item_brand,
                                                        active: similarDoc.active
                                                    };

                                                    itemArray.push(item);
                                                }
                                            });
                                            this.items.push(itemArray);
                                            this.selectedAvailbilityOfItem.push(0);
                                        }
                                    });
                            }
                        });

                        this.loading = false;
                    }
                })
                .catch(err => {
                    console.log(err);
                    this.loading = false;
                });
        },

        searchByName() {
            let url = domain + "search";

            axios
                .get(url, {
                    params: {
                        data: this.searchInput,
                        table: "items",
                        offset: "0",
                        category: "fetch"
                    },
                    headers: {
                        from: "webappOrder"
                    }
                })
                .then(res => {
                    this.itemSearched = true;
                    if (res.data) {
                        this.items = [];
                        this.selectedAvailbilityOfItem = [];
                        this.itemSearched = true;
                        res.data.forEach(doc => {
                            {
                                var itemArray = new Array();
                                let itemUrl = domain + "item";
                                axios
                                    .get(itemUrl, {
                                        params: {
                                            similarProductId: doc.similarProductId
                                        }
                                    })
                                    .then(query => {
                                        if (query.data) {
                                            query.data.forEach(similarDoc => {
                                                {
                                                    let inCart = false;
                                                    this.cart.forEach(element => {
                                                        if (element.productId == similarDoc.productId) {
                                                            inCart = true;
                                                        }
                                                    });
                                                    var item = new Array();
                                                    // image name
                                                    let imageName = null;
                                                    if (similarDoc.image) {
                                                        let start =
                                                            similarDoc.image.indexOf("items%2F") + 8;
                                                        let end = similarDoc.image.indexOf("?");
                                                        imageName = similarDoc.image.substring(start, end);
                                                        imageName = this.truncate(imageName);
                                                    }
                                                    item = {
                                                        productId: similarDoc.productId,
                                                        itemId: similarDoc.itemId,
                                                        name: similarDoc.name,
                                                        cat: similarDoc.allcat,
                                                        stock: similarDoc.stock,
                                                        unit: similarDoc.unit,
                                                        mrp: similarDoc.MRP,
                                                        currentPrice: similarDoc.currentPrice,
                                                        discount: similarDoc.discount,
                                                        image: similarDoc.image,
                                                        imageName: imageName,
                                                        qty: similarDoc.qty,
                                                        quantityToBuy: 0,
                                                        priceToBuy: 0,
                                                        inCart: inCart,
                                                        item_brand: similarDoc.item_brand,
                                                        active: similarDoc.active
                                                    };

                                                    itemArray.push(item);
                                                }
                                            });
                                            this.items.push(itemArray);
                                            this.selectedAvailbilityOfItem.push(0);
                                        }
                                    });
                            }
                        });

                        this.loading = false;
                    }
                })
                .catch(err => {
                    console.log(err);
                    this.loading = false;
                });
        },

        selectSize(index, innerIndex) {
            Vue.set(this.selectedAvailbilityOfItem, index, innerIndex);
        },

        total() {
            this.subTotal = 0;
            this.shippingCharges = 0;
            this.totalPayableAmount = 0;
            this.cart.forEach(doc => {
                this.subTotal = this.subTotal + doc.quantityToBuy * doc.currentPrice;
            });

            this.subTotal = parseFloat(this.subTotal.toFixed(2));

            if (this.subTotal > this.thresholdForDeliveryCharge && this.subTotal > 0) {
                this.shippingCharges = 0;
            } else {
                this.shippingCharges = this.deliveryCharges;
            }

            this.shippingCharges = parseFloat(this.shippingCharges.toFixed(2));

            this.totalPayableAmount = parseFloat(
                this.subTotal + this.shippingCharges
            );
        },

        truncate(value) {
            const length = 10;
            return value.length <= length ?
                value :
                value.substring(0, length) + "...";
        }
    },
}



