<template>
  <div>
    <Dashboard :products="items" :disable="loading" v-if="items"></Dashboard>
  </div>
</template>

<script>
import db from "@/firebaseInit";
import Dashboard from "./DashBoard.vue";
import { bus } from "@/main";
import { domain } from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";
import { invLayoutMixin } from "@/components/Mixins/Inventory/invLayoutMixin.js";
import axios from "axios";

export default {
  name: "searchItem",
  mixins: [invLayoutMixin],
  data() {
    return {
      items: [],
      loading: false
    };
  },

  components: {
    Dashboard
  },

  created() {
    bus.$on("searchItem", data => {
      this.searchItem(data["searched"][0], data["searched"][1]);
    });
  },

  methods: {
    searchItem(searched, by) {
      if (searched.length > 0) {
        this.loading = true;
        this.items = [];

        // loading content

        if (by == "barcode") {
          let url = domain + "item";
          axios
            .get(url, {
              params: {
                barcode: searched,
                user: "aajaBhai",
                category: "fetch"
              },
              headers: {
                from: "webapp"
              }
            })
            .then(res => {
              if (res.data && (res.data != null || res.data.length > 0)) {
                res.data.forEach(doc => {
                  let item = this.dataFormat(doc);
                  this.items.push(item);
                });

                this.loading = false;
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
            });
        } else if (by == "productId") {
          let url = domain + "item";
          axios
            .get(url, {
              params: {
                productId: searched,
                user: "aajaBhai",
                category: "fetch"
              },
              headers: {
                from: "webapp"
              }
            })
            .then(res => {
              if (res.data && (res.data != null || res.data.length > 0)) {
                res.data.forEach(doc => {
                  var item = this.dataFormat(doc);
                  this.items.push(item);
                });

                this.loading = false;
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
            });
        } else {
          let url = domain + "search";
          axios
            .get(url, {
              params: {
                data: searched,
                table: "items",
                offset: "0",
                category: "fetch"
              },
              headers: {
                from: "webapp"
              }
            })
            .then(res => {
              if (res.data && (res.data != null || res.data.length > 0)) {
                res.data.forEach(doc => {
                  var item = this.dataFormat(doc);
                  this.items.push(item);
                });
                this.loading = false;
              }
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
            });
        }
      }
    }
  }
};
</script>



