<template>
  <div class="card-body">
    <!-- <div class="row pt-2">
      <div class="col-6">
        <form class="form-inline">
          <div class="input-group content-search border">
            <div class="input-group-prepend">
              <span class="input-group-text" id="basic-addon1">
                <i class="fa fa-search"></i>
              </span>
            </div>
            <input
              type="text"
              v-model="search"
              class="form-control hz-input"
              placeholder="Search Here..."
              aria-label="Search Here..."
              aria-describedby="basic-addon1"
              @keyup="searching"
            />
          </div>
        </form>
      </div>

    </div>-->
    <!-- header -->
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <div class="pl-4">
          <i class="far fa-list-alt"></i>
          <h5 class="pl-2 d-inline-block">
            <strong>
              <span v-if="status == 3">READY FOR PICKUP</span>
              <span v-if="status == 4">OUT FOR DELIVERIES</span>
              <span v-if="status == 5">SUCCESSFUL</span>
              <span v-if="status == 7">RETURN PICKED</span>
              <span v-if="status == 6">RETURNED</span>
              <span v-if="status == 7">FAILED</span>
              DELIVERIES
            </strong>
          </h5>
        </div>
      </div>
      <!-- <div class="col-6 content-header small">
                <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">
                    dashboard
                  </router-link>
                  >
                  <router-link
                    to="/order/dashboard"
                    class="cursor-pointer"
                    tag="span"
                  >
                    sales order
                  </router-link>
                  >
                  <router-link
                    to="/delivery/ready"
                    class="cursor-pointer"
                    tag="span"
                  >
                    ready
                  </router-link>
                </span>
      </div>-->
      <div class="col-6">
        <form
          class="form-inline float-right"
          v-if="
            status && (status == 4 || status == 3 || status == 5 || status == 6)
          "
        >
          <span class="font-weight-semibold pr-3">Select Delivery Status</span>
          <select v-model="action" name="status" class="custom-select" @change="updateAction()">
            <option disabled="disabled" selected="selected">Choose Action</option>

            <option value="o1">Cancel</option>
            <option value="4" v-if="status == 3">Out For Delivery</option>
            <option value="5" v-if="status == 4">Delivered</option>
            <option value="o2" v-if="status == 5">Reuqest Return</option>
            <option value="7" v-if="status == 6">Return Picked</option>
            <option value="8">Failed</option>
            <option value="delete">Delete</option>
          </select>
        </form>
      </div>
    </div>
    <hr class="pl-4 pr-4" />
    <div class="table-responsive table-scroll mt-4">
      <table class="table table-hover table-sm table-borderless table-content text-left">
        <thead>
          <tr>
            <th
              v-if="
                status &&
                  (status == 4 || status == 3 || status == 5 || status == 6)
              "
            >
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    id="customCheck"
                    @click="selectAll()"
                    v-model="select"
                  />
                  <label class="custom-control-label" for="customCheck"></label>
                </div>
              </form>
            </th>
            <th style="width:30px">Order Id</th>
            <th>Order By</th>
            <th>Products</th>
            <th>Deliver Items</th>
            <th>Amount</th>
            <th>Payment Status</th>
            <th>Delivery Status</th>
            <th>Delivery By</th>
            <th v-if="status == 4 || status == 5">Expected Delivery By</th>
            <!-- <th>Inventory Address</th> -->
            <th>Delivery Address</th>
            <th>At</th>
            <th></th>
          </tr>
        </thead>
        <tbody v-if="deliveries.length > 0">
          <tr v-for="delivery in deliveries" :key="delivery.id">
            <td
              v-if="
                status &&
                  (status == 4 || status == 3 || status == 5 || status == 6)
              "
            >
              <form>
                <div class="custom-control custom-checkbox">
                  <input
                    type="checkbox"
                    class="custom-control-input"
                    :id="delivery.deliveryId"
                    v-model="delivery.checked"
                    :value="delivery.deliveryId"
                  />
                  <label class="custom-control-label" :for="delivery.deliveryId"></label>
                </div>
              </form>
            </td>
            <td style="width:30px">
              {{ delivery.order.id.substring(0, 4) + "..." }}
              <i
                class="far fa-copy pl-1"
                style="cursor:pointer"
                @click="copyOrderId(delivery.order.id)"
              ></i>
              <input type="hidden" :value="delivery.order.id" :id="delivery.order.id" />
            </td>
            <td>
              <span>{{ delivery.order.user_address.name }}</span>
              <span>{{ delivery.order.user_address.mobileNumber }}</span>
            </td>
            <td>
              <div v-if="delivery.order.products.length > 2">
                <span
                  v-for="product in delivery.order.products.slice(0, 2)"
                  :key="product.productId"
                >
                  <img :src="product.image" class="img-fluid content-image" /> +
                </span>
                <span>
                  <b>{{ delivery.order.products.length - 2 }}</b>
                </span>
              </div>
              <div v-else>
                <span v-for="product in delivery.order.products" :key="product.productId">
                  <img :src="product.image" class="img-fluid content-image" />
                </span>
              </div>
            </td>
            <td>{{ delivery.order.availableItems }}</td>
            <td>{{ delivery.order.totalPayableAmount }}</td>
            <td>
              <span
                class="bagde badge-waiting pl-1 pr-1"
                v-if="delivery.order.paidAmount == 0"
              >Not Paid</span>
              <span
                class="bagde badge-paid pl-1 pr-1"
                v-else-if="
                  delivery.order.paidAmount == delivery.order.totalPayableAmount
                "
              >Paid</span>
              <span class="bagde badge-failed pl-1 pr-1" v-else>
                Pending :
                {{
                delivery.order.totalPayableAmount - delivery.order.paidAmount
                }}
              </span>
            </td>
            <td>
              <span class="badge badge-cancelled pl-2 pr-2" v-if="delivery.status == 1">Cancelled</span>
              <span
                class="badge badge-processing pl-2 pr-2"
                v-if="delivery.status == 2"
              >Not yet Ready</span>
              <span class="badge badge-packed pl-2 pr-2" v-if="delivery.status == 3">Ready To Pickup</span>
              <span class="badge badge-shipping pl-2 pr-2" v-if="delivery.status == 4">Shipping</span>
              <span class="badge badge-delivered pl-2 pr-2" v-if="delivery.status == 5">Delivered</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-if="delivery.status == 6"
              >Return PickUp</span>
              <span class="badge badge-return pl-2 pr-2" v-if="delivery.status == 7">Returned</span>
              <span class="badge badge-failed pl-2 pr-2" v-if="delivery.status == 8">Failed</span>
            </td>
            <td class="text-dark">{{ delivery.deliveryBy }}</td>
            <td
              class="text-dark"
              v-if="status == 4 || status == 5"
            >{{ delivery.estDeliveryTime[1] }}</td>
            <!-- <td class="text-dark">
              {{ delivery.order.inventory_address.name }}
            </td>-->
            <td class="text-dark">
              <!-- {{ delivery.order.user_address.addressName }}, -->
              {{ delivery.order.user_address.houseOfficeNo }},
              {{ delivery.order.user_address.street }},
              {{ delivery.order.user_address.city }},
              {{ delivery.order.user_address.landmark }},
              {{ delivery.order.user_address.pincode }}
            </td>
            <td>
              {{ delivery.date }}
              <small>{{ delivery.time }}</small>
            </td>
            <td class="text-dark">
              <div class="dropdown dropleft dropleft">
                <a
                  href="#"
                  class="float-right"
                  role="button"
                  :id="'dropdownMenuLink'.index"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  <i class="fa fa-lg fa-ellipsis-v" aria-hidden="true"></i>
                </a>
                <div class="dropdown-menu" :aria-labelledby="'dropdownMenuLink'.index">
                  <router-link
                    :to="{
                      name: 'OrderReturnSlip',
                      params: {
                        slipType: 'returnSlip',
                        orderId: delivery.order.id
                      }
                    }"
                    tag="a"
                    class="pr-1 dropdown-item"
                    target="_blank"
                    v-if="status == 2 || status == 9"
                  >
                    <i class="fa fa-print pr-2" aria-hidden="true"></i>
                    <span class="pl-2">Return Slip</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'OrderBill',
                      params: { slipType: 'bill', orderId: delivery.order.id }
                    }"
                    tag="a"
                    target="_blank"
                    class="pr-1 dropdown-item"
                    v-else
                  >
                    <i class="fa fa-print pr-2" aria-hidden="true"></i>
                    <span class="pl-2">Generate Bill</span>
                  </router-link>
                  <router-link
                    :to="{
                      name: 'ViewOrder',
                      params: { orderId: delivery.order.id }
                    }"
                    target="_blank"
                    class="pr-1 dropdown-item"
                  >
                    <i class="fa fa-eye pr-2"></i>
                    <span class="pl-2 text-center">View</span>
                  </router-link>
                  <a
                    :href="
                      'https://www.google.com/maps/search/' +
                        delivery.order.ordererLocation
                    "
                    target="_blank"
                    class="dropdown-item"
                  >
                    <i class="fa fa-map-pin pr-2" aria-hidden="true"></i>
                    <span class="pl-2 text-center">Locate</span>
                  </a>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
        <tbody v-else>
          <tr>
            <td colspan="9" class="text-center">
              <div>
                <img
                  class="card-img-top img-fluid"
                  src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                  alt="Card image cap"
                  style="width:20em"
                />
                <div class="card-body">
                  <p class="card-text">Oops! There ain't any delivery Made</p>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <DeliveryFooter />
    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>
<script>
import firebase from "firebase";
import Vue from "vue";
import DeliveryFooter from "./DeliveryFooter.vue";
import { bus } from "@/main";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import axios from "axios";

export default {
  name: "DashBoard",
  components: {
    DeliveryFooter
  },
  props: {
    status: Number,
    newDeliveries: Array,
    disable: Boolean
  },

  data() {
    return {
      deliveryStatus: [],
      search: null,
      searchArray: [],

      select: false,
      action: null,

      message: null,
      success: null,
      loading: this.disable
    };
  },

  computed: {
    deliveries() {
      this.searchArray = this.newDeliveries;
      return this.newDeliveries;
    }
  },

  created() {
    //this.getOrderStatus();
    bus.$on("paginateDelivery", data => {
      this.paginateDelivery(data["page"]);
    });
  },

  methods: {
    copyOrderId(id) {
      let orderID = "#" + id;
      let idToCopy = document.querySelector(orderID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },
    updateAction() {
      if (
        this.user.designation == "admin" ||
        this.user.designation == "deliverer"
      ) {
        this.deliveries.forEach((delivery, index) => {
          if (delivery.checked) {
            if (this.action == "delete") {
              if (
                confirm("You are about to delete " + delivery.order.orderId)
              ) {
                this.loading = true;
                let url = domain + "order/" + delivery.order.orderId;
                axios
                  .post(
                    url,
                    {
                      _method: "DELETE"
                    },
                    {
                      withCredentials: true,
                      headers: {
                        "Content-Type": "application/json",
                        "X-Requested-With": "XMLHttpRequest",
                        "X-api-token": firebase.auth().currentUser.uid
                      }
                    }
                  )
                  .then(res => {
                    if (res.data) {
                      this.message = "Order successfully deleted";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      this.deliveries.splice(index, 1);

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    }
                  })
                  .catch(err => {
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            } else if (this.action == "o1" || this.action == "o2") {
              this.loading = true;
              let url = domain + "order/" + deliver.order.id;
              let action = 0;
              if (this.action == "o1") {
                action = 1;
              } else {
                action = 2;
              }
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    reason: "Done from webapp",
                    preferredPaymentMode: 1,
                    status: action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.message = "Successfully Updated and moved";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  //remove that order after save
                  if (res.data == "Success") {
                    this.deliveries.splice(index, 1);
                  }

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            } else {
              this.loading = true;
              let url = domain + "delivery/" + delivery.deliveryId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.message = "Successfully Updated and moved";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  //remove that order after save
                  if (res.data == "Success") {
                    this.deliveries.splice(index, 1);
                  }

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  console.log(err.message);

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            }
          }
        });
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    paginateDelivery(page) {
      this.$emit("fetchDeliveries", page);
    },

    searching() {
      if (this.search) {
        this.deliveries = this.searchArray.filter(
          v => v.orderId.indexOf(this.search) != -1
        );
      } else {
        this.deliveries = this.searchArray;
      }
    },

    selectAll() {
      this.deliveries.forEach(delivery => {
        if (!this.select) {
          delivery.checked = true;
          //Vue.set(order, checked, true);
        } else {
          delivery.checked = false;
        }
      });
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



