<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- content -->
            <DashBoard
              :newOrders="orders"
              :disable="disable"
              :status="status"
              @fetchOrders="fetchOrders"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DashBoard from "./DashBoard.vue";

import { orderMixin } from "@/components/Mixins/orderMixin.js";

export default {
  name: "CancelledOrders",
  mixins: [orderMixin],

  components: {
    DashBoard
  },

  data() {
    return {
      status: 4
    };
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



