import axios from "axios";
import Vue from "vue";
import {
    imageControlMixin
} from "./imageControlMixin.js";
import {
    domain,
} from "@/store/variable";
import {
    inventoryDataMixin
} from "./Inventory/inventoryDataMixin.js";
import {
    firestorage
}
from "@/firebaseInit";

export const pushNotificationMixin = {
    mixins: [inventoryDataMixin, imageControlMixin],

    data() {
        return {
            schedule: 0,
            interval: 0,
            image: null,
            sendTo: 0,
            type: 0,
            content: {
                title: null,
                subtitle: null,
                message: null,
                actitle: null,
                orderid: null,
                proid: null,
                itemId: null
            },
            startDate: null,
            expiryDate: null,

            imageOption: true,
            errors: [],

            types: [],
            receivers: [],
            schedules: [],
            intervals: []
        }
    },

    beforeRouteLeave(to, from, next) {
        if (confirm("Are you sure, you wanna leave?")) {
            next();
        } else {
            next(false);
        }
    },

    created() {
        this.getNotificationTypes();
        this.getNotificationSchedule();
        this.getNotificationInterval();
        this.getNotificationReciever();
    },

    methods: {
        checkDates() {
            var date = new Date();
            if (this.interval == 1) {
                this.startDate = this.formatDate(date, 0);
                this.expiryDate = this.formatDate(date, 0);
            } else if (this.interval == 18) {
                this.startDate = this.formatDate(date, 0);
                this.expiryDate = this.formatDate(date, 1);
            } else if (this.interval == 19) {
                this.startDate = this.formatDate(date, 1);
                this.expiryDate = this.formatDate(date, 2);
            }
        },
        getNotificationTypes() {
            let url = domain + "user/notification/push/type";
            axios
                .get(url)
                .then(res => {
                    res.data.forEach(doc => {
                        const data = {
                            id: doc.id,
                            name: doc.name
                        };
                        this.types.push(data);
                    });
                })
                .catch(err => {
                    console.log(err);
                });
        },

        getNotificationSchedule() {
            let url = domain + "user/notification/push/schedule";
            axios
                .get(url)
                .then(res => {
                    res.data.forEach(doc => {
                        const data = {
                            id: doc.id,
                            name: doc.name,
                            at: doc.at
                        };
                        this.schedules.push(data);
                    });
                })
                .catch(err => {
                    console.log(err);
                });
        },

        getNotificationInterval() {
            let url = domain + "user/notification/push/interval";
            axios
                .get(url)
                .then(res => {
                    res.data.forEach(doc => {
                        const data = {
                            id: doc.id,
                            name: doc.name
                        };
                        this.intervals.push(data);
                    });
                })
                .catch(err => {
                    console.log(err);
                });
        },

        getNotificationReciever() {
            let url = domain + "user/notification/push/receiver";
            axios
                .get(url)
                .then(res => {
                    res.data.forEach(doc => {
                        const data = {
                            id: doc.id,
                            name: doc.name
                        };
                        this.receivers.push(data);
                    });
                })
                .catch(err => {
                    console.log(err);
                });
        },

        uploadImage(type) {
            if (type == "image") {
                let file = this.$refs.image.files[0];
                this.image = null;
                return firestorage
                    .ref("images/" + file.name)
                    .put(file)
                    .then(res => {
                        //downloading url
                        let mediaURL = res.ref.getDownloadURL();
                        return mediaURL;
                    })
                    .catch(err => {
                        return null;
                    });
            }
        },

        validateData() {
            this.errors = [];
            if (
                (this.content.title &&
                    this.content.subtitle &&
                    this.content.message &&
                    this.type > 0 &&
                    this.schedule > 0 &&
                    this.interval > 0 &&
                    this.startDate &&
                    (this.expiryDate == null || this.expiryDate >= now()),
                    this.sendTo > 0)
            ) {
                return true;
            } else {
                if (!this.title) {
                    Vue.set(this.errors, 0, "Add Title");
                }
                if (!this.subtitle) {
                    Vue.set(this.errors, 1, "Add Subtitle");
                }
                if (!this.message) {
                    Vue.set(this.errors, 2, "Add Message");
                }
                // if (!this.message) {
                //   Vue.set(this.errors, 3, "Add Message");
                // }
                if (this.schedule <= 0) {
                    Vue.set(this.errors, 4, "Choose valid schedule");
                }
                if (this.interval <= 0) {
                    Vue.set(this.errors, 5, "Choose valid interval");
                }
                if (this.type <= 0) {
                    Vue.set(this.errors, 6, "Choose valid type");
                }
                if (this.sendTo <= 0) {
                    Vue.set(this.errors, 7, "Choose valid receivers");
                }

                Vue.set(
                    this.errors,
                    10,
                    "Something is missing!! Check out what you left"
                );
                return false;
            }
        }
    },

}



