<template>
  <div>
    <Dashboard :products="items" :disable="loading" v-if="items"></Dashboard>

    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import db from "@/firebaseInit";
import Dashboard from "./DashBoard.vue";
import Footer from "../Footer.vue";
import { bus } from "@/main";
import { domain } from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";
import { invLayoutMixin } from "@/components/Mixins/Inventory/invLayoutMixin.js";
import axios from "axios";

export default {
  name: "current_inventory",
  mixins: [invLayoutMixin],
  data() {
    return {
      items: [],
      loading: false
    };
  },

  components: {
    Footer,
    Dashboard
  },

  computed: {
    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    setTimeout(() => {
      if (this.user) {
        this.fetchItems();
      }
    }, 4000);
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.items = [];

      // loading content
      let url = domain + "item";
      if (!page) {
        page = 1;
      }

      if (this.user.designation == "admin") {
        axios
          .get(url, {
            params: {
              user: "aajaBhai",
              category: "fetch",
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              {
                let item = this.dataFormat(doc);
                this.items.push(item);
              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
          });
      } else if (this.user.designation == "webappUploader") {
        axios
          .get(url, {
            params: {
              user: "aajaBhai",
              category: "fetch",
              page: page,
              limit: this.size,
              order: this.orderItems,
              orderWay: this.orderWay,
              uploadedBy: firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.data.forEach(doc => {
              {
                let item = this.dataFormat(doc);
                this.items.push(item);
              }
            });

            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
            this.loading = false;
          })
          .catch(err => {
            this.loading = false;
          });
      } else if (this.user.designation == "vegetableVendor") {
        axios
          .get(url, {
            params: {
              user: "aajaBhai",
              category: "fetch",
              itemId: "FR0200",
              order: this.orderItems,
              orderWay: this.orderWay
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              {
                let item = this.dataFormat(doc);
                this.items.push(item);
              }
            });

            axios
              .get(url, {
                params: {
                  user: "aajaBhai",
                  category: "fetch",
                  itemId: "FR0201",
                  order: this.orderItems,
                  orderWay: this.orderWay
                }
              })
              .then(innerRes => {
                innerRes.data.forEach(doc => {
                  let item = this.dataFormat(doc);
                  this.items.push(item);
                });

                store.dispatch("patchinventoryPageCount", 1);
                store.dispatch("patchinventoryPageNumber", 1);
                store.dispatch(
                  "patchinventoryTotalItems",
                  res.data.count + innerRes.data.count
                );
                this.loading = false;
              });
          })
          .catch(err => {
            this.loading = false;
          });
      }
    }
  },

  sockets: {
    message(data) {
      console.log(data);
    }
  }
};
</script>



