<script>
export default {
  name: "orderStatus",

  props: {
    orderStatus: Array,
    deliveryStatus: Array
  },

  computed: {
    status() {
      var bluprint = [
        {
          id: 3,
          status: "Placed",
          image:
            "https://external-content.duckduckgo.com/iu/?u=http%3A%2F%2Fsamurai-us.com%2Fwp-content%2Fuploads%2F2017%2F07%2Fplaceorder.png&f=1&nofb=1",
          check: 0,
          from: "order"
        },
        {
          id: 4,
          status: "Packed",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fcdn.iconscout.com%2Ficon%2Ffree%2Fpng-256%2Fbox-package-parcels-logistic-delivery-packed-shipping-1-20269.png&f=1&nofb=1",
          check: 0,
          from: "order"
        },
        {
          id: 3,
          status: "Ready For Pickup",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fwww.royalmail.com%2Fbusiness%2Fsites%2Fdefault%2Ffiles%2FRM-Icons-PNGs-2%2FLocal-Collect-Service-Icon-Grey-230x230.png&f=1&nofb=1",
          check: 0,
          from: "delivery"
        },
        {
          id: 4,
          status: "Out For Delivery",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fcdn3.iconfinder.com%2Fdata%2Ficons%2Fpayment-shopping-e-commerce%2F100%2FArtboard_31-512.png&f=1&nofb=1",
          check: 0,
          from: "delivery"
        },
        {
          id: 1,
          status: "Cancelled",
          image:
            "https://external-content.duckduckgo.com/iu/?u=http%3A%2F%2Fcdn.onlinewebfonts.com%2Fsvg%2Fimg_456347.png&f=1&nofb=1",
          check: 0,
          from: "order"
        },
        {
          id: 7,
          status: "Delivered",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fcdn4.iconfinder.com%2Fdata%2Ficons%2Flogistic-13%2F1024%2Forder_confirm2-512.png&f=1&nofb=1",
          check: 0,
          from: "order"
        },
        {
          id: 2,
          status: "Requested Return",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fs7d9.scene7.com%2Fis%2Fimage%2FSpirit%2Fguest_services%2FCustomerServiceIcons-03.svg&f=1&nofb=1",
          check: 0,
          from: "order"
        },
        {
          id: 6,
          status: "Return Pickup",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Ftse2.mm.bing.net%2Fth%3Fid%3DOIP.AF1QLXyroP1Y2B30IxHTgwHaHa%26pid%3DApi&f=1",
          check: 0,
          from: "delivery"
        },
        {
          id: 10,
          status: "Returned",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fcdn3.iconfinder.com%2Fdata%2Ficons%2Fsales-and-delivery%2F128%2Fconfermation-256.png&f=1&nofb=1",
          check: 0,
          from: "order"
        },
        {
          id: 8,
          status: "Failed",
          image:
            "https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fs3.amazonaws.com%2Fkinlane-productions%2Fbw-icons%2Fbw-hazard.png&f=1&nofb=1",
          check: 0,
          from: "order"
        }
      ];

      this.orderStatus.forEach(element => {
        bluprint.forEach((state, index) => {
          if (state.id == element.id && state.from == "order") {
            Vue.set(bluprint[index], "check", 1);
          }
        });
      });

      // this.deliveryStatus.forEach(element => {
      //   bluprint.forEach((state, index) => {
      //     if (state.id == element.id && state.from == "delivery") {
      //       Vue.set(bluprint[index], "check", 1);
      //     }
      //   });
      // });

      return bluprint;
    }
  }
};
</script>

