<script>
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import $ from "jquery";
import Vue from "vue";
import db from "@/firebaseInit";
import firebase from "firebase";
import { firestorage } from "@/firebaseInit";
import axios from "axios";
import { inventoryMixin } from "@/components/Mixins/Inventory/inventoryMixin.js";

export default {
  name: "addItem",
  mixins: [inventoryMixin],
  data() {
    return {
      searchingItem: false,
      searchProductID: null,
      showEdit: -1
    };
  },

  created() {
    this.fetchCategories();
  },
  beforeRouteLeave(to, from, next) {
    if (
      this.name ||
      this.discount > 0 ||
      this.mrp > 0 ||
      this.msp > 0 ||
      this.stock > 0 ||
      this.disPriority > 0 ||
      this.description != "description" ||
      this.qty > 0 ||
      this.maxLimit > 0
    ) {
      if (confirm("Your changes will be lost.Do you want to leave the page?")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
  },

  methods: {
    async add() {
      // binging preloader on
      let flag = true;
      this.showEdit = -1;

      if (
        this.validate() &&
        this.duplicate == false &&
        (this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
        this.user.status == "active"
      ) {
        this.disable = true;
        this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

        // add brand
        if (this.enterBrand) {
          await this.addBrand();
          if (!isNaN(this.brand)) {
            flag = false;
          }
        }

        if (
          this.media[0] &&
          this.media[0] != null &&
          this.media[0] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active"
          ) {
            await this.uploadImage().then(downloadURL => {
              this.image = downloadURL;
            });
          } else {
            if (this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            }
            flag = false;
            this.disable = false;

            return;
          }
        }

        if (
          this.media[2] &&
          this.media[2] != null &&
          this.media[2] != undefined &&
          flag == true
        ) {
          if (
            (this.user.designation == "admin" ||
              (this.user.designation == "webappUploader" &&
                this.user.package == 3)) &&
            this.user.status == "active" &&
            this.image != null &&
            this.image != ""
          ) {
            let files = this.detailImagesFiles;
            let mediaURL = [];
            this.detailImages = [];
            for (var i = 0; i < files.length; i++) {
              let file = files[i];

              await this.uploadDetailImage(file).then(downloadURL => {
                this.detailImages.push(downloadURL);
              });
            }
          } else {
            if (this.user.package != 3 && this.user.designation != "admin") {
              alert("Your package does not allow you to upload image");
            } else if (this.user.status != "active") {
              alert("Aap se yeh adikar cheen liya gya hai");
            } else if (this.image == null || this.image == "") {
              alert("You need to upload the main image first");
            }
            flag = false;
            this.disable = false;
            return;
          }
        }

        if (flag == true) {
          if (
            (this.image && this.user.package == 3) ||
            this.user.package == 1 ||
            this.user.designation == "admin"
          ) {
            this.uploadData();
          } else {
            if (this.image && this.user.package != 3) {
              alert("Your package does not allow you to upload image");
            } else if (!this.image && this.user.package == 3) {
              alert("Your package demands to upload image with it");
            }
            this.disable = false;
          }
        }
      }
    },

    generateId() {
      let now = new Date();
      let nowTime = now.getTime();
      let nowDate =
        now.getUTCDate() + "" + now.getUTCMonth() + "" + now.getUTCFullYear();
      let id = "xxxxxxxx".replace(/[x]/g, function(c) {
        let r = (nowTime + Math.random() * 8) % 8 | 0;
        nowTime = Math.floor(nowTime / 16);
        return (c === "x" ? r : (r & 0x1) | 0x5).toString(8);
      });

      id =
        "PRO" +
        this.user.uuid.substring(0, 3).toUpperCase() +
        "-" +
        this.user.name.substring(0, 3).toUpperCase() +
        "-" +
        nowDate +
        "-" +
        id;
      return id;
    },

    uploadData() {
      let productId = this.generateId();
      this.sku = this.generateSKU();

      this.name = this.name.toLowerCase().replace(/(\s+.)|^\w/gi, function(v) {
        return v.toUpperCase();
      });
      if (!this.dropShip) {
        this.dropshipAddress = null;
      }

      let url = domain + "item";
      axios
        .post(
          url,
          {
            productId: productId,
            currentPrice: this.currentPrice,
            name: this.processContent("string", this.name),
            image: this.image,
            detailImages: this.detailImages,
            discount: this.discount,
            ingredients: this.processContent("html", this.ingredients),
            features: this.processContent("html", this.features),
            packingType: this.packingType,
            unit: this.unit,
            unit2: this.unit2,
            color: this.processContent("string", this.color),
            MRP: this.mrp,
            msp: this.msp,
            stock: this.stock,
            itemId: this.itemId,
            dis_val: this.disVal,
            disPriority: this.disPriority, //doubt
            description: this.processContent("string", this.description),
            qty: this.processContent("string", this.qty),
            sku: this.sku,
            mfgDate: this.processContent("string", this.mfgDate),
            expiryDate: this.processContent("string", this.expiryDate),
            shelfLife: this.processContent("string", this.shelfLife),
            brand: this.brand.name,
            brandName: this.brand.id,
            address: this.processContent("string", this.address),
            active: this.active,
            dropShipAddress: this.processContent(
              "string",
              this.dropShipAddress
            ),
            similarProductId: this.similarProductId,
            barcode: this.barcode,
            maxLimit: this.maxLimit,
            uploadedAt: new Date(),
            updatedAt: new Date()
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          this.showEdit = productId;
          //// to show
          this.message = "Data Upload Success";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        })
        .catch(err => {
          //// to show
          this.message = "Data Upload Failed";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    uploadImage() {
      if (this.media[0] != null && this.media[0] != undefined) {
        let file = this.$refs.image.files[0];
        this.image = null;
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "Image Upload Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    },

    uploadDetailImage(file) {
      if (file) {
        return firestorage
          .ref("items/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            console.log(err);

            //// to show
            this.message = "More Images Upload failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);

            return null;
          });
      }
    }
  }
};
</script>

<style scoped>
.header-row {
  top: 4em;
  padding-top: 1em;
  padding-bottom: 1em;
}
</style>



