</template>

<script>
import { domain } from "@/store/variable";
import jsPDF from "jsPDF";
import html2canvas from "html2canvas";
import VueBarcode from "vue-barcode";
import axios from "axios";

export default {
  name: "OrderBill",
  components: {
    barcode: VueBarcode
  },
  data() {
    return {
      orderId: null,
      products: [],
      slipType: "bill",
      orderDate: null,
      orderTime: null,
      barcode: null,
      reason: null,
      totalPayableAmount: 0,
      deliveryCharges: 0,
      totalDiscountedAmount: 0,
      user: [],
      deliveryAddress: [],
      paymentMode: null,
      number: "(+91)9123109983"
    };
  },

  beforeRouteEnter(to, from, next) {
    let url = domain + "order";
    if (to.params.slipType == "bill") {
      axios
        .get(url, {
          params: {
            id: to.params.orderId,
            products: "budhbak",
            payment_mode: "budhbak",
            user: "budhbak",
            userAddress: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            let month = dateTime.getMonth() + 1;
            let date =
              dateTime.getDate() + "/" + month + "/" + dateTime.getFullYear();
            let time =
              dateTime.getHours() +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds();
            next(vm => {
              vm.orderId = doc.id;
              vm.orderDate = date;
              vm.orderTime = time;
              vm.totalDiscountedAmount = doc.totalDiscountedAmount;
              vm.totalPayableAmount = doc.totalPayableAmount;
              vm.deliveryCharges = doc.deliveryCharges;
              vm.overWeightCharge = doc.overWeightCharge;
              vm.user = doc.user;
              vm.deliveryAddress = doc.user_address;
              vm.paymentMode = doc.payment_mode.mode;
              vm.barcode = doc.barcode;
              vm.slipType = "bill";

              //products
              doc.products.forEach(prod => {
                vm.products.push(prod);
              });
            });
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    } else {
      axios
        .get(url, {
          params: {
            id: to.params.orderId,
            products: "budhbak",
            payment_mode: "budhbak",
            user: "budhbak",
            returned: "budhbak",
            userAddress: "budhbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let dateTime = new Date(doc.orderTime);
            let month = dateTime.getMonth() + 1;
            let date =
              dateTime.getDate() + "/" + month + "/" + dateTime.getFullYear();
            let time =
              dateTime.getHours() +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds();
            next(vm => {
              vm.orderId = doc.id;
              vm.orderDate = date;
              vm.orderTime = time;
              vm.totalDiscountedAmount = doc.totalDiscountedAmount;
              vm.totalPayableAmount = doc.totalPayableAmount;
              vm.deliveryCharges = doc.deliveryCharges;
              vm.overWeightCharge = doc.overWeightCharge;
              vm.user = doc.user;
              vm.deliveryAddress = doc.user_address;
              vm.paymentMode = doc.payment_mode.mode;
              vm.barcode = doc.barcode;
              vm.reason = doc.returned[0].reason;
              vm.slipType = "returnSlip";

              //products
              doc.products.forEach(prod => {
                vm.products.push(prod);
              });
            });
          });
        })
        .catch(err => {
          console.log(err.message);
        });
    }
  },

  watch: {
    $route: "fetchOrder"
  },

  mounted() {
    window.html2canvas = html2canvas;
    setTimeout(() => {
      this.generatePdf();
    }, 5000);
  },

  methods: {
    fetchOrder() {
      let url = domain + "order";
      if (this.$route.params.slipType == "bill") {
        axios
          .get(url, {
            params: {
              id: this.$route.params.orderId,
              products: "budhbak",
              payment_mode: "budhbak",
              user: "budhbak",
              userAddress: "budhbak"
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              let dateTime = new Date(doc.orderTime);
              let month = dateTime.getMonth() + 1;
              let date =
                dateTime.getDate() + "/" + month + "/" + dateTime.getFullYear();
              let time =
                dateTime.getHours() +
                ":" +
                dateTime.getMinutes() +
                ":" +
                dateTime.getSeconds();

              // putting data
              this.orderId = doc.id;
              this.orderDate = date;
              this.orderTime = time;
              this.totalDiscountedAmount = doc.totalDiscountedAmount;
              this.totalPayableAmount = doc.totalPayableAmount;
              this.deliveryCharges = doc.deliveryCharges;
              this.overWeightCharge = doc.overWeightCharge;
              this.user = doc.user;
              this.deliveryAddress = doc.user_address;
              this.paymentMode = doc.payment_mode.mode;
              this.barcode = doc.barcode;
              this.reason = doc.returned[0].reason;
              this.slipType = "bill";
              //products
              doc.products.forEach(prod => {
                this.products.push(prod);
              });
            });
          })
          .catch(err => {
            console.log(err.message);
          });
      } else {
        axios
          .get(url, {
            params: {
              id: this.$route.params.orderId,
              products: "budhbak",
              payment_mode: "budhbak",
              user: "budhbak",
              userAddress: "budhbak"
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              let dateTime = new Date(doc.orderTime);
              let month = dateTime.getMonth() + 1;
              let date =
                dateTime.getDate() + "/" + month + "/" + dateTime.getFullYear();
              let time =
                dateTime.getHours() +
                ":" +
                dateTime.getMinutes() +
                ":" +
                dateTime.getSeconds();

              // putting data
              this.orderId = doc.id;
              this.orderDate = date;
              this.orderTime = time;
              this.totalDiscountedAmount = doc.totalDiscountedAmount;
              this.totalPayableAmount = doc.totalPayableAmount;
              this.deliveryCharges = doc.deliveryCharges;
              this.overWeightCharge = doc.overWeightCharge;
              this.user = doc.user;
              this.deliveryAddress = doc.user_address;
              this.paymentMode = doc.payment_mode.mode;
              this.barcode = doc.barcode;
              this.reason = doc.returned[0].reason;
              this.slipType = "returnSlip";
              //products
              doc.products.forEach(prod => {
                this.products.push(prod);
              });
            });
          })
          .catch(err => {
            console.log(err.message);
          });
      }
    },

    async generatePdf() {
      let pdfName = this.orderId;
      let pdf = new jsPDF("p", "mm", "a4", true);
      let contentHTML = this.$refs.content.innerHTML;

      html2canvas(document.querySelector("#bill")).then(canvas => {
        var imgData = canvas.toDataURL("Image/jpeg", 1.0);

        var imgWidth = 190;
        var pageHeight = 297;
        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;
        var position = 10;
        pdf.addImage(
          canvas.toDataURL("Image/png", 1.0),
          "PNG",
          10,
          position,
          imgWidth,
          imgHeight
        );
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight + 10;
          pdf.addPage();
          pdf.addImage(
            canvas.toDataURL("Image/png", 1.0),
            "PNG",
            10,
            position,
            imgWidth,
            imgHeight
          );
          heightLeft -= pageHeight;
        }

        pdf.save(pdfName + ".pdf");
        window.open(pdf.output("bloburl")); // to debug
      });

      //don't print ansything
      // pdf.html(contentHTML, {
      //   callback: function(pdfSave) {
      //     pdf.save(pdfName + ".pdf");
      //   }
      // });
    },

    convertNumberToWords(amount) {
      var words = new Array();
      words[0] = "";
      words[1] = "One";
      words[2] = "Two";
      words[3] = "Three";
      words[4] = "Four";
      words[5] = "Five";
      words[6] = "Six";
      words[7] = "Seven";
      words[8] = "Eight";
      words[9] = "Nine";
      words[10] = "Ten";
      words[11] = "Eleven";
      words[12] = "Twelve";
      words[13] = "Thirteen";
      words[14] = "Fourteen";
      words[15] = "Fifteen";
      words[16] = "Sixteen";
      words[17] = "Seventeen";
      words[18] = "Eighteen";
      words[19] = "Nineteen";
      words[20] = "Twenty";
      words[30] = "Thirty";
      words[40] = "Forty";
      words[50] = "Fifty";
      words[60] = "Sixty";
      words[70] = "Seventy";
      words[80] = "Eighty";
      words[90] = "Ninety";
      amount = amount.toString();
      var atemp = amount.split(".");
      var number = atemp[0].split(",").join("");
      var n_length = number.length;
      var words_string = "";
      if (n_length <= 9) {
        var n_array = new Array(0, 0, 0, 0, 0, 0, 0, 0, 0);
        var received_n_array = new Array();
        for (var i = 0; i < n_length; i++) {
          received_n_array[i] = number.substr(i, 1);
        }
        for (var i = 9 - n_length, j = 0; i < 9; i++, j++) {
          n_array[i] = received_n_array[j];
        }
        for (var i = 0, j = 1; i < 9; i++, j++) {
          if (i == 0 || i == 2 || i == 4 || i == 7) {
            if (n_array[i] == 1) {
              n_array[j] = 10 + parseInt(n_array[j]);
              n_array[i] = 0;
            }
          }
        }
        var value = "";
        for (var i = 0; i < 9; i++) {
          if (i == 0 || i == 2 || i == 4 || i == 7) {
            value = n_array[i] * 10;
          } else {
            value = n_array[i];
          }
          if (value != 0) {
            words_string += words[value] + " ";
          }
          if (
            (i == 1 && value != 0) ||
            (i == 0 && value != 0 && n_array[i + 1] == 0)
          ) {
            words_string += "Crores ";
          }
          if (
            (i == 3 && value != 0) ||
            (i == 2 && value != 0 && n_array[i + 1] == 0)
          ) {
            words_string += "Lakhs ";
          }
          if (
            (i == 5 && value != 0) ||
            (i == 4 && value != 0 && n_array[i + 1] == 0)
          ) {
            words_string += "Thousand ";
          }
          if (
            i == 6 &&
            value != 0 &&
            n_array[i + 1] != 0 &&
            n_array[i + 2] != 0
          ) {
            words_string += "Hundred and ";
          } else if (i == 6 && value != 0) {
            words_string += "Hundred ";
          }
        }
        words_string = words_string.split("  ").join(" ");
      }
      return words_string;
    }
  }
};
</script>

<style>
#bill {
  color: cmyk(0%, 0%, 0%, 100%) !important;
  font-size: 18px;
}

.font20 {
  font-size: 20px;
}

.font24 {
  font-size: 22px;
}
</style>



