<template>
  <nav class="navbar fixed-bottom footer bg-dark shadow inventoryFooter">
    <div>
      <button
        class="fas fa-sync-alt fa-xs text-white pl-2 pr-4 button-trans"
        @click="refresh()"
        data-toggle="tooltip"
        data-placement="right"
        title="Click to Refresh this page"
      ></button>
      <!-- <div class="dropdown dropup d-inline-block" @change="order()">
        <i
          class="fas fa-filter fa-xs text-white"
          data-toggle="dropdown"
          data-target="#filterDropdown"
