<template>
  <div class="accordion" id="accordionCartPanel">
    <!--orderer details -->
    <div class="card" id="headingTwo">
      <div
        data-toggle="collapse"
        data-target="#collapseTwo"
        aria-expanded="true"
        aria-controls="collapseTwo"
        class="card-header"
      >
        <h2 class="mb-0">
          <button class="btn btn-link" type="button">Orderer Details</button>
        </h2>
      </div>
      <div
        id="collapseTwo"
        class="collapse show"
        aria-labelledby="headingTwo"
        data-parent="#accordionCartPanel"
      >
        <div class="card-body">
          <div>
            <form class="form pb-5 mb-4">
              <!-- phone number-->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Mobile Number</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model.number="mobileNumber"
                    required
                    disabled="true"
                    maxlength="10"
                  />
                </div>
              </div>

              <!-- email -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Email</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="email"
                    required
                    disabled="true"
                    data-length="10"
                  />
                </div>
              </div>

              <!-- name -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Name</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="name"
                    required
                    disabled="true"
                    data-length="10"
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <!--address-->
    <div class="card" id="headingThree">
      <div
        class="card-header collapsed"
        data-toggle="collapse"
        data-target="#collapseThree"
        aria-expanded="false"
        aria-controls="collapseThree"
      >
        <h2 class="mb-0">
          <button class="btn btn-link" type="button">Address</button>
        </h2>
      </div>
      <div
        id="collapseThree"
        class="collapse"
        aria-labelledby="headingThree"
        data-parent="#accordionCartPanel"
      >
        <div class="card-body">
          <form>
            <div v-if="ordererAddresses.length > 0">
              <div v-for="address in ordererAddresses" :key="address.id" class="d-flex">
                <input type="radio" :value="address.id" v-model="deliveryAddress" />
                <label class="pl-1">
                  <div>
                    <span>{{ address.name }} - {{ address.mobileNumber }}</span>
                    <span>
                      {{ address.houseOfficeNo }}, {{ address.street }},
                      {{ address.landmark }},{{ address.city }},{{
                      address.state
                      }},{{ address.pincode }}
                    </span>
                  </div>
                </label>
              </div>
            </div>
            <div>
              <input type="radio" value="-1" v-model="deliveryAddress" />
              <label>Add Address</label>
            </div>
          </form>
          <div v-if="deliveryAddress == '-1'">
            <form class="form pb-5 mb-4">
              <!-- addressBelonger -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Shipped To(Name):</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="addressBelonger"
                    required
                    :disabled="disable"
                    data-length="10"
                  />
                </div>
              </div>
              <!-- houseOfficeNo-->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>H.No/S.No.</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="houseOfficeNo"
                    required
                    :disabled="disable"
                    data-length="10"
                  />
                </div>
              </div>
              <!-- street -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Street</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="street"
                    :disabled="disable"
                    data-length="10"
                  />
                </div>
              </div>
              <!-- city -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>City</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="city"
                    required
                    :disabled="disable"
                    data-length="10"
                  />
                </div>
              </div>

              <!-- pincode number-->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Pincode</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="pincode"
                    required
                    :disabled="disable"
                    maxlength="6"
                  />
                </div>
              </div>

              <!-- landmark -->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Landmark</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="landmark"
                    :disabled="disable"
                    data-length="10"
                  />
                </div>
              </div>
              <!-- phone number-->
              <div class="form-row">
                <div class="col-sm-12">
                  <label>Mobile Number</label>
                  <input
                    type="text"
                    class="form-control form-control-sm"
                    v-model="mobileNumber"
                    required
                    disabled
                    data-length="10"
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <!--order detail-->
    <div class="card" id="headingOne">
      <div
        class="card-header show"
        data-toggle="collapse"
        data-target="#collapseOne"
        aria-expanded="true"
        aria-controls="collapseOne"
      >
        <h2 class="mb-0">
          <button class="btn btn-link" type="button">Order Details</button>
        </h2>
      </div>

      <div
        id="collapseOne"
        class="collapse"
        aria-labelledby="headingOne"
        data-parent="#accordionCartPanel"
      >
        <div class="card-body">
          <form class="form pb-5 mb-4">
            <!-- phone number-->
            <div class="form-row">
              <div class="col-sm-12">
                <label>Payment Mode</label>
                <select
                  name="paymentMode"
                  v-model="paymentMode"
                  class="form-control form-control-sm selectpicker"
                >
                  <option value="1">Cash On Delivery</option>
                  <option value="7" disabled>Fasdo Cash</option>
                </select>
              </div>
            </div>

            <!-- phone number-->
            <div class="form-row">
              <div class="col-sm-12">
                <label>Order Via</label>
                <select
                  name="paymentMode"
                  v-model="placedFrom"
                  class="form-control form-control-sm selectpicker"
                >
                  <option value="2">WhatsApp</option>
                  <option value="3">On Call</option>
                  <option value="4">SMS</option>
                </select>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import _ from "lodash";
import $ from "jquery";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";
import axios from "axios";

export default {
  name: "EditCartRightPanel",
  data() {
    return {
      paymentMode: 1,
      placedFrom: 3,

      name: null,
      email: null,
      mobileNumber: null,
      ordererId: null,
      ordererFound: false,

      ordererAddresses: [],
      deliveryAddress: 0,
      addressBelonger: null,
      houseOfficeNo: null,
      street: null,
      city: null,
      landmark: null,
      pincode: null,
      city: null,
      addressName: "Default",
      disable: false,
      message: null
    };
  },
  methods: {
    addAddress(ordererId) {
      if (ordererId) {
        let url = domain + "user/address";
        return axios.post(
          url,
          {
            userId: ordererId,
            name: this.addressBelonger,
            houseOfficeNo: this.houseOfficeNo,
            street: this.street,
            landmark: this.landmark,
            pincode: this.pincode,
            city: this.city,
            mobileNumber: this.mobileNumber,
            addressName: this.addressName
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        );
        // .then(res => {
        //     if (res.data) {
        //       this.disable = false;
        //       this.deliveryAddress = res.data.id;
        //     }
        //     return res.data;
        //   });
      }
    },

    clearData() {
      this.ordererId = null;
      this.name = null;
      // this.mobileNumber = null;
      // this.email = null;
      this.ordererFound = false;
      this.ordererAddresses = [];
    },

    fetchAddresses() {
      if (this.ordererFound) {
        this.ordererAddresses = [];
        let url = domain + "user/address";
        this.disable = true;
        axios
          .get(url, {
            params: {
              userId: this.ordererId,
              active: 1
            }
          })
          .then(res => {
            if (res.data.length > 0 && res.data != null) {
              res.data.forEach(doc => {
                this.ordererAddresses.push(doc);
              });
            }
            this.disable = false;
          });
      }
    },
    searchOrderer(userId) {
      let url = domain + "user";
      this.disable = true;
      axios
        .get(url, {
          params: {
            uuid: userId
          }
        })
        .then(res => {
          if (res.data != null) {
            this.ordererId = res.data.uuid;
            this.name = res.data.name;
            this.mobileNumber = res.data.mobileNumber;
            this.email = res.data.email;
            this.ordererFound = true;

            this.fetchAddresses();

            setTimeout(() => {
              this.message = null;
            }, 3000);
          } else {
            this.message = "User not found";
            this.clearData();

            setTimeout(() => {
              this.message = null;
            }, 3000);
          }
          this.disable = false;
        });
    }
  }
};
</script>



