<template>
  <div class="container">
    <div class="table-filter">
      <nav class="navbar">
        <div class="col-sm-4">
          <h5 class="pl-2 d-inline-block nav-item">
            <i class="fa fa-truck" aria-hidden="true"></i>
            <strong>TRACK ORDER</strong>
          </h5>
        </div>
        <div class="col-sm-8">
          <div class="float-right">
            <div class="btn-group">
              <a class="filter-icon d-inline-block pr-2 text-dark" data-toggle="dropdown" href="#">
                <i class="fa fa-filter"></i>
              </a>
              <ul class="dropdown-menu">
                <li class="dropdown-item" @click="trackBy = 'email'">Email</li>
                <li class="dropdown-item" @click="trackBy = 'Name'">Name</li>
                <li class="dropdown-item" @click="trackBy = 'orderId'">Order Id</li>
                <li class="dropdown-item" @click="trackBy = 'mobileNumber'">Mobile Number</li>
              </ul>
            </div>
            <div class="filter-group d-inline-block">
              <label v-if="trackBy == 'email'">Email</label>
              <label v-if="trackBy == 'Name'">Name</label>
              <label v-if="trackBy == 'mobileNumber'">Mobile Number</label>
              <label v-if="trackBy == 'orderId'">Order Id</label>
              <input type="text" v-model="trackData" class="form-control" placeholder="Track Bar" />
            </div>
            <button
              type="button"
              class="btn btn-success btn-sm"
              @click="getData()"
              v-if="trackBy != 'orderId'"
            >Track</button>
            <router-link
              :to="{ name: 'ViewOrder', params: { orderId: trackData } }"
              class="btn btn-success btn-sm"
              tag="a"
              target="_blank"
              v-else
            >Track</router-link>
          </div>
        </div>
      </nav>
    </div>

    <div v-if="trackData">
      <!--  user -->
      <div class="row" v-if="users.length > 0">
        <div class="col-12">
          <div class="card content border-1 shadow-sm mt-4">
            <div class="card-body">
              <div class="row mt-4 mb-4">
                <div class="content-header">
                  <div class="pl-4">
                    <i class="fas fa-users"></i>
                    <h5 class="pl-2 d-inline-block">
                      <strong>Users</strong>
                    </h5>
                  </div>
                </div>
              </div>

              <div class="table-responsive pt-2">
                <table class="table table-hover table-sm table-borderless table-content text-left">
                  <thead>
                    <tr>
                      <th>S.No.</th>
                      <th>Name</th>
                      <th>Mobile Number</th>
                      <th>View</th>
                    </tr>
                  </thead>
                  <tbody v-if="users.length > 0">
                    <tr v-for="(user, index) in users" :key="index + 1">
                      <td>{{ index + 1 }}</td>
                      <td>{{ user.name }}</td>
                      <td>{{ user.mobileNumber }}</td>
                      <td>
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          @click="getUserOrder(index)"
                        >Track</button>
                      </td>
                    </tr>
                  </tbody>

                  <tbody v-else>
                    <tr>
                      <td colspan="8" class="text-center">
                        <div>
                          <img
                            class="card-img-top img-fluid"
                            src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                            alt="Card image cap"
                            style="width:20em"
                          />
                          <div class="card-body">
                            <p class="card-text">Oops! There ain't any Orders Made</p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--  user orders -->
      <div class="row" v-if="trackedUser != null || userOrders.length > 0">
        <div class="col-12">
          <div class="card content border-1 shadow-sm mt-4">
            <div class="card-body">
              <div class="row mt-4 mb-4">
                <div class="content-header">
                  <div class="pl-4">
                    <i class="far fa-folder-open"></i>
                    <h5 class="pl-2 d-inline-block">
                      <strong>Orders</strong>
                    </h5>
                  </div>
                </div>
              </div>

              <div class="table-responsive pt-2">
                <table class="table table-hover table-sm table-borderless table-content text-left">
                  <thead>
                    <tr>
                      <th>S.No.</th>
                      <th>OrderId</th>
                      <th>Description</th>
                      <th>Amount</th>
                      <th>Payment Method</th>
                      <th>Status</th>
                      <th>Placed On</th>
                      <th>View</th>
                    </tr>
                  </thead>
                  <tbody v-if="userOrders.length > 0">
                    <tr v-for="(order, index) in userOrders" :key="index + 1">
                      <td>{{ index + 1 }}</td>
                      <td>{{ order.orderId }}</td>
                      <td>
                        <div v-if="order.products.length > 2">
                          <span
                            v-for="product in order.products.slice(0, 2)"
                            :key="product.productId"
                          >{{ product.name }} +</span>
                          <span>
                            <b>{{ order.products.length - 2 }} more</b>
                          </span>
                        </div>
                        <div v-else>
                          <span
                            v-for="product in order.products"
                            :key="product.productId"
                          >{{ product.name }}</span>
                        </div>
                      </td>
                      <td>{{ order.totalPayableAmount }}</td>
                      <td>{{ order.paymentMode }}</td>
                      <td>{{ order.status }}</td>
                      <td>{{ order.orderDate }} at {{ order.orderTime }}</td>
                      <td>
                        <router-link
                          :to="{
                            name: 'ViewOrder',
                            params: { orderId: order.orderId }
                          }"
                        >
                          <i class="fa fa-eye"></i>
                        </router-link>
                        <router-link
                          :to="{
                            name: 'OrderEdit',
                            params: { orderId: order.orderId }
                          }"
                        >
                          <i class="fa fa-pencil-alt pl-2"></i>
                        </router-link>
                      </td>
                    </tr>
                  </tbody>

                  <tbody v-else>
                    <tr>
                      <td colspan="8" class="text-center">
                        <div>
                          <img
                            class="card-img-top img-fluid"
                            src="https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/c23778b9-5990-4216-a73c-caa91ab40615/d74k79-e85ac748-18aa-499e-8ea7-159209e3714a.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..guiz9Ehkmrl3p_6osxJfyGNSrOnZxS6TcfaRgS8bIzQ"
                            alt="Card image cap"
                            style="width:20em"
                          />
                          <div class="card-body">
                            <p class="card-text">Oops! There ain't any Orders Made</p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="track-default" v-else>
      <i class="fa fa-truck" aria-hidden="true"></i>
      <h5>Enter something in track bar to track any order</h5>
    </div>

    <Loader :disable="loading" :success="success" :message="message" />
  </div>
</template>

<script>
import Vue from "vue";
import { domain } from "@/store/variable";
import store from "@/store/store";
import OrderStatus from "./orderStatus.vue";
import axios from "axios";

export default {
  name: "TrackOrder",
  components: {
    OrderStatus
  },
  data() {
    return {
      trackBy: "mobileNumber",
      trackData: null,
      order: null,
      trackedUser: null,
      userOrders: [],
      users: [],

      loading: false,
      success: false,
      message: "Searching"
    };
  },

  methods: {
    getData() {
      if (this.user && this.user.designation == "admin") {
        let url = domain + "order";
        let userURL = domain + "user";
        this.order = null;
        this.users = [];

        if (this.trackBy == "email") {
          this.loading = true;
          axios
            .get(userURL, {
              params: {
                email: this.trackData.trim()
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.trackedUser = doc;
              });

              if (this.trackedUser) {
                axios
                  .get(url, {
                    params: {
                      userId: this.trackedUser["uuid"]
                    },
                    headers: {
                      from: "android"
                    }
                  })
                  .then(res => {
                    this.userOrders = res.data;
                    this.loading = false;
                  });
              } else {
                this.success = true;
                this.loading = false;
                this.message = "User not found";

                setTimeout(() => {
                  this.success = false;
                  this.message = "Searching";
                }, 2000);
              }
            });
        } else if (this.trackBy == "mobileNumber") {
          this.loading = true;
          axios
            .get(userURL, {
              params: {
                mobileNumber: this.trackData.trim()
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.trackedUser = doc;
              });

              if (this.trackedUser) {
                axios
                  .get(url, {
                    params: {
                      userId: this.trackedUser["uuid"]
                    },
                    headers: {
                      from: "android"
                    }
                  })
                  .then(res => {
                    this.userOrders = res.data;
                    this.loading = false;
                  });
              } else {
                this.success = true;
                this.loading = false;
                this.message = "User not found";

                setTimeout(() => {
                  this.success = false;
                  this.message = "Searching";
                }, 2000);
              }
            });
        } else if (this.trackBy == "Name") {
          this.loading = true;
          axios
            .get(userURL, {
              params: {
                name: this.trackData.trim()
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.users.push(doc);
              });

              this.loading = false;

              setTimeout(() => {
                this.message = "Searching";
              }, 2000);
            });
        }
      }
    },

    getOrder(index) {
      if (this.userOrders) {
        this.order = this.userOrders[index];
      }
    },

    getUserOrder(index) {
      this.trackBy = "mobileNumber";
      this.trackData = this.users[index].mobileNumber;
      this.getData();
    }
  }
};
</script>

<style scoped>
.order-by-header h6 {
  color: rgba(47, 48, 61, 1);
  font-weight: 700;
  margin-bottom: 0.2em;
}

.order-by-header .heading {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
}

.order-by-header .location {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 600;
  font-size: smaller;
}

.order-content {
  font-size: small;
}

.order-content img {
  width: 4em;
}

.response {
  color: rgba(47, 48, 61, 0.82);
  font-weight: 700;
}

.table-filter input,
.table-filter select {
  height: 34px;
  border-radius: 3px;
  border-color: #ddd;
  box-shadow: none;
}
.table-filter {
  padding: 5px 0 15px;
  border-bottom: 1px solid #e9e9e9;
}
.table-filter .btn {
  height: 34px;
}
.filter-group select.form-control {
  width: 110px;
}
.filter-icon i {
  font-size: 18px;
  opacity: 0.7;
}
.filter-icon {
  display: inline-block;
}

.track-default {
  position: absolute;
  left: 60%;
  top: 60%;
  transform: translate(-50%, -50%);
  text-align: center;
  opacity: 0.4;
}

.track-default i {
  font-size: 4em;
  padding-bottom: 0.6em;
}
</style>



