import {
    domain,
    utcHours,
    utcMins,
    deliveryTime
} from "@/store/variable";
import store from "@/store/store";
import axios from "axios";


export const deliveryMixin = {

    data() {
        return {
            disable: false,
            deliveries: [],
        }
    },

    computed: {
        size() {
            return store.state.deliveryPageSize;
        }
    },

    mounted() {
        this.fetchDeliveries();
    },

    methods: {

        fetchDeliveries(page) {
            let url = domain + "delivery";

            if (!page) {
                page = 1;
            }

            this.loading = true;
            axios
                .get(url, {
                    params: {
                        status: this.status,
                        userAddress: "with",
                        by: "with",
                        inventoryAddress: "with",
                        order: "updated_at",
                        orderWay: "desc",
                        page: page,
                        limit: this.size
                    }
                })
                .then(res => {
                    let deliveryList = [];
                    res.data.data.forEach(doc => {
                        if (doc.order) {
                            let orderTime = this.getDateTime(doc.created_at);
                            let date = orderTime[0];
                            let time = orderTime[1];
                            let estDeliveryTime = this.getExpectedDeliveryTime(doc.order.orderTime);

                            let delivery = {
                                deliveryId: doc.id,
                                order: doc.order,
                                status: doc.status,
                                deliveryBy: doc.by.user.name,
                                date: date,
                                time: time,
                                estDeliveryTime: estDeliveryTime,
                                checked: false
                            };

                            deliveryList.push(delivery);

                            store.dispatch("patchdeliveryPageCount", res.data.last_page);
                            store.dispatch("patchdeliveryPageNumber", res.data.current_page);
                            store.dispatch("patchtotalDeliveries", res.data.total);
                        }
                    });

                    this.deliveries = deliveryList;
                });

            this.loading = false;
        },

        getExpectedDeliveryTime(orderTime) {
            if (orderTime) {
                let dateTime = new Date(orderTime);
                dateTime.setHours(dateTime.getHours() + utcHours);
                dateTime.setMinutes(dateTime.getMinutes() + utcMins + deliveryTime);
                let month = dateTime.getMonth() + 1;
                let date =
                    dateTime.getDate() +
                    "/" +
                    month +
                    "/" +
                    dateTime.getFullYear();
                var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                var hours = dateTime.getHours() % 12 || 12;
                let time =
                    hours +
                    ":" +
                    dateTime.getMinutes() +
                    ":" +
                    dateTime.getSeconds() +
                    " " +
                    AmOrPm;

                return [date, time];
            }
        }

    },
}



