<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="far fa-list-alt"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>CONTACT QUERIES</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small">
                <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">dashboard</router-link>>
                  <router-link to="/user/all" class="cursor-pointer" tag="span">users</router-link>>
                  <router-link
                    to="/user/contact/query"
                    class="cursor-pointer"
                    tag="span"
                  >contact queries</router-link>
                </span>
              </div>
            </div>
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-12 content-header">
                <div class="float-right">Total: {{ totalQueries }}</div>
              </div>
            </div>
            <!-- content -->
            <table class="table table-hover text-left">
              <thead>
                <tr>
                  <th>S.No.</th>
                  <th>By</th>
                  <th>Name</th>
                  <th>Subject</th>
                  <th>At</th>
                  <th>Replied</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="(query, index) in queries"
                  :key="query.id"
                  @click="selectQuery(index)"
                  style="cursor:pointer;"
                  data-toggle="modal"
                  data-target="#queries"
                >
                  <td>{{ index + 1 }}</td>
                  <td>
                    <span>{{ query.from }}</span>
                  </td>
                  <td>{{ query.name }}</td>
                  <td>{{ query.subject }}</td>
                  <td>
                    {{ query.date }}
                    <small>{{ query.time }}</small>
                  </td>
                  <td>
                    <span v-if="query.reply">
                      <i class="fa fa-check" aria-hidden="true"></i>
                    </span>
                    <span v-else>
                      <i class="fa fa-times" aria-hidden="true"></i>
                    </span>
                  </td>
                </tr>
              </tbody>
            </table>
            <!-- pagination -->
            <div class="row">
              <div class="col-12 d-flex justify-content-center">
                <ul class="pagination mt-2">
                  <li class="page-item" @click.prevent="changePage('prev')">
                    <a class="page-link" href="#"><</a>
                  </li>
                  <li class="page-item pl-2 pr-2">
                    <a class="page-link" href="#">{{ currentPage }}</a>
                  </li>
                  <li class="page-item" @click.prevent="changePage('next')">
                    <a class="page-link" href="#">></a>
                  </li>
                </ul>
              </div>
            </div>

            <!-- modal -->
            <div id="queries" class="modal fade" role="dialog">
              <div class="modal-dialog modal-dialog-scrollable">
                <!-- Modal content-->
                <div class="modal-content">
                  <div class="modal-header">
                    <h5 class="modal-title font-weight-bold">
                      <i class="fas fa-clipboard-list pr-3"></i>Query
                    </h5>
                    <button type="button" class="close" data-dismiss="modal">&times;</button>
                  </div>
                  <div class="modal-body" style="position:relative;">
                    <div>
                      <label>Query By</label>
                      <div>{{ selectedQuery.name }}</div>
                    </div>
                    <div>
                      <label>Email</label>
                      <div>{{ selectedQuery.from }}</div>
                    </div>
                    <div>
                      <label>Subject</label>
                      <div>{{ selectedQuery.subject }}</div>
                    </div>
                    <div>
                      <label>Query</label>
                      <div>{{ selectedQuery.query }}</div>
                    </div>
                    <div v-if="selectedQuery.reply">
                      <label>Reply</label>
                      <div>{{ selectedQuery.reply }}</div>
                    </div>
                    <div v-else>
                      <label>Reply</label>
                      <textarea class="form-control form-control-sm" v-model="reply" rows="3"></textarea>
                      <button
                        type="submit"
                        @click.prevent="replyQuery()"
                        class="btn btn-success btn-sm mt-2"
                      >Send</button>
                    </div>
                  </div>
                  <div class="modal-footer">
                    <button type="button" class="btn btn-danger btn-sm" data-dismiss="modal">
                      <i class="fas fa-times-circle pr-2"></i>Close
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Loader :disable="disable" :success="success" :message="message" />
  </div>
</template>

<script>
import Vue from "vue";
import {
  domain,
  utcHours,
  utcMins,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";
import axios from "axios";

export default {
  name: "UserFeedbacks",
  data() {
    return {
      queries: [],
      currentPage: 1,
      lastPage: 1,
      totalQueries: 0,
      disable: false,
      success: false,
      message: null,
      selectedQuery: {},
      reply: null
    };
  },

  mounted() {
    this.fetchQueries(1);
  },

  methods: {
    fetchQueries(page) {
      let url = domain + "contact/query";

      this.disable = true;
      axios
        .get(url, {
          params: {
            order: "created_at",
            orderWay: "desc",
            page: page,
            limit: 10
          }
        })
        .then(res => {
          this.queries = [];
          this.currentPage = res.data.current_page;
          this.lastPage = res.data.last_page;
          this.totalQueries = res.data.total;
          res.data.data.forEach(doc => {
            var dateData = this.getDateTime(doc.created_at);

            let query = {
              id: doc.id,
              from: doc.from,
              name: doc.name,
              subject: doc.subject,
              query: doc.query,
              reply: doc.reply,
              repliedBy: doc.repliedBy,
              date: dateData[0],
              time: dateData[1]
            };
            this.queries.push(query);
            this.disable = false;
          });
        });
    },

    changePage(change) {
      if (change == "prev" && this.currentPage > 1) {
        let newPage = this.currentPage - 1;
        this.fetchQueries(newPage);
      } else {
        if (this.currentPage < this.lastPage) {
          let newPage = this.currentPage + 1;
          this.fetchQueries(newPage);
        }
      }
    },

    selectQuery(index) {
      this.selectedQuery = this.queries[index];
    },

    replyQuery() {
      if (
        this.user &&
        this.user.designation == "admin" &&
        this.reply &&
        this.reply != this.selectedQuery.reply
      ) {
        let url = domain + "contact/query/" + this.selectedQuery.id;
        this.disable = true;
        axios
          .post(
            url,
            {
              _method: "put",
              reply: this.reply
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data == "Success") {
              this.message = "Updated successfully";
              this.selectedQuery.reply = this.reply;
            } else {
              this.message = "Update Failed";
            }
            this.reply = null;

            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    }
  }
};
</script>



