<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <DashBoard
              :newDeliveries="deliveries"
              :disable="disable"
              :status="status"
              @fetchDeliveries="fetchDeliveries"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DashBoard from "./DashBoard.vue";
import { deliveryMixin } from "@/components/Mixins/deliveryMixin.js";

export default {
  name: "ReturnPickedDelivery",
  mixins: [deliveryMixin],

  components: {
    DashBoard
  },
  data() {
    return {
      status: 7
    };
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



