 import {
     utcHours,
     utcMins
 } from "@/store/variable";
 import store from "@/store/store";

 export const globalMixin = {
     computed: {
         user() {
             return store.state.user;
         }
     },
     methods: {
         goBack() {
             window.history.length > 1 ?
                 this.$router.go(-1) :
                 this.$router.push("/");
         },

         getDateTime(givenDate) {
             if (givenDate) {
                 let dateTime = new Date(givenDate);
                 dateTime.setHours(dateTime.getHours() + utcHours);
                 dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                 let month = dateTime.getMonth() + 1;
                 let date =
                     dateTime.getDate() +
                     "/" +
                     month +
                     "/" +
                     dateTime.getFullYear();
                 var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                 var hours = dateTime.getHours() % 12 || 12;
                 let time =
                     hours +
                     ":" +
                     dateTime.getMinutes() +
                     ":" +
                     dateTime.getSeconds() +
                     " " +
                     AmOrPm;

                 return [date, time];
             }
         },

         getUserPermission(index, type) {
             let permissions = {
                 1: {
                     get: 1,
                     post: 0,
                     put: 0,
                     delete: 0
                 },
                 2: {
                     get: 0,
                     post: 1,
                     put: 0,
                     delete: 0
                 },
                 3: {
                     get: 0,
                     post: 0,
                     put: 1,
                     delete: 0
                 },
                 4: {
                     get: 0,
                     post: 0,
                     put: 0,
                     delete: 1
                 },
                 5: {
                     get: 1,
                     post: 1,
                     put: 0,
                     delete: 0
                 },
                 6: {
                     get: 1,
                     post: 0,
                     put: 1,
                     delete: 0
                 },
                 7: {
                     get: 1,
                     post: 0,
                     put: 0,
                     delete: 1
                 },
                 8: {
                     get: 0,
                     post: 1,
                     put: 1,
                     delete: 0
                 },
                 9: {
                     get: 0,
                     post: 1,
                     put: 0,
                     delete: 1
                 },
                 10: {
                     get: 0,
                     post: 0,
                     put: 1,
                     delete: 1
                 },
                 11: {
                     get: 1,
                     post: 1,
                     put: 1,
                     delete: 0
                 },
                 12: {
                     get: 1,
                     post: 1,
                     put: 0,
                     delete: 1
                 },
                 13: {
                     get: 0,
                     post: 1,
                     put: 1,
                     delete: 1
                 },
                 14: {
                     get: 1,
                     post: 1,
                     put: 0,
                     delete: 1
                 },
                 15: {
                     get: 1,
                     post: 1,
                     put: 1,
                     delete: 1
                 },
                 16: {
                     get: 0,
                     post: 0,
                     put: 0,
                     delete: 0
                 },
             };

             if (type == 'get') {
                 if (permissions[index].get == 1) {
                     return true;
                 }
             } else if (type == 'post') {
                 if (permissions[index].post == 1) {
                     return true;
                 }
             } else if (type == 'put') {
                 if (permissions[index].put == 1) {
                     return true;
                 }
             } else if (type == 'delete') {
                 if (permissions[index].delete == 1) {
                     return true;
                 }
             } else {
                 return false;
             }

             return false;
         },

         formatDate(date, days) {
             var d = new Date(date);
             d.setDate(d.getDate() + days);
             var month = '' + (d.getMonth() + 1),
                 day = '' + d.getDate(),
                 year = d.getFullYear();


             if (month.length < 2)
                 month = '0' + month;
             if (day.length < 2)
                 day = '0' + day;

             return [year, month, day].join('-');
         }
     },
 }


