<template>
  <div class="container-fluid">
    <div class="row mt-4 mb-4">
      <div class="col-6 content-header">
        <h4>FeedBacks</h4>
      </div>
      <div class="col-6 content-header small">
        <span>doorstep > users > feedback</span>
      </div>
    </div>

    <!-- totals -->
    <div class="row mt-4 mb-4">
      <div class="col-12 content-header small">
        <span>Total : {{ totalFeedbacks }}</span>
      </div>
    </div>

    <div class="row">
      <div class="col-12">
        <!-- <view/> -->
        <div class="card content border-1 shadow-sm">
          <div class="card-body">
            <div class="table-responsive p-4">
              <table class="table table-hover table-sm text-left">
                <thead>
                  <tr>
                    <th>S.No.</th>
                    <th>Feedback By</th>
                    <th>Subject</th>
                    <th>Feedback</th>
                    <th>At</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(feedback, index) in feedbacks"
                    :key="feedback.id"
                    @click="selectFeedback(index)"
                    style="cursor:pointer;"
                    data-toggle="modal"
                    data-target="#queries"
                  >
                    <td>{{ index + 1 }}</td>
                    <td>
                      <span v-if="feedback.user.name != 'Fasdorian'">
                        {{
                        feedback.user.name
                        }}
                      </span>
                      <span v-else-if="feedback.user.mobileNumber">
                        {{
                        feedback.user.mobileNumber
                        }}
                      </span>
                      <span>{{ feedback.user.email }}</span>
                    </td>
                    <td>{{ feedback.subject }}</td>
                    <td>{{ feedback.content }}</td>
                    <td>
                      {{ feedback.date }}
                      <small>{{ feedback.time }}</small>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="row">
      <div class="col-12 d-flex justify-content-center">
        <ul class="pagination mt-2">
          <li class="page-item" @click.prevent="changePage('prev')">
            <a class="page-link" href="#"><</a>
          </li>
          <li class="page-item pl-2 pr-2">
            <a class="page-link" href="#">{{ currentPage }}</a>
          </li>
          <li class="page-item" @click.prevent="changePage('next')">
            <a class="page-link" href="#">></a>
          </li>
        </ul>
      </div>
    </div>

    <!-- modal -->
    <div id="queries" class="modal fade" role="dialog" v-if="selectedFeedback">
      <div class="modal-dialog modal-dialog-scrollable">
        <!-- Modal content-->
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold">
              <i class="fas fa-clipboard-list pr-3"></i>Feedback
            </h5>
            <button type="button" class="close" data-dismiss="modal">&times;</button>
          </div>
          <div
            class="modal-body"
            style="position:relative;"
            v-if="selectedFeedback && selectedFeedback != {}"
          >
            <div>
              <label>Feedback By</label>
              <div
                v-if="selectedFeedback && !selectedFeedback.user.email"
              >{{ selectedFeedback.user.name }}</div>
              <div
                v-else-if="selectedFeedback && selectedFeedback.user.email"
              >{{ selectedFeedback.user.email }}</div>
              <div v-else>N/A</div>
            </div>
            <div>
              <label>Subject</label>
              <div>{{ selectedFeedback.subject }}</div>
            </div>
            <div>
              <label>Feedback</label>
              <div>{{ selectedFeedback.content }}</div>
            </div>
            <div>
              <label>At</label>
              <div>{{ selectedFeedback.date }} {{ selectedFeedback.time }}</div>
            </div>
            <div v-if="selectedFeedback.reply">
              <label>Reply</label>
              <div>{{ selectedFeedback.reply }}</div>
            </div>
            <div v-else-if="selectedFeedback.user.email">
              <label>Reply</label>
              <textarea class="form-control form-control-sm" v-model="reply" rows="3"></textarea>
              <button
                type="submit"
                @click.prevent="replyQuery()"
                class="btn btn-success btn-sm mt-2"
              >Send</button>
            </div>
            <div v-else>
              <label>You can't reply to this</label>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger btn-sm" data-dismiss="modal">
              <i class="fas fa-times-circle pr-2"></i>Close
            </button>
          </div>
        </div>
      </div>
    </div>

    <Loader :disable="disable" :success="success" :message="message" />
  </div>
</template>

<script>
import Vue from "vue";
import {
  domain,
  utcHours,
  utcMins,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import firebase from "firebase";
import axios from "axios";

export default {
  name: "UserFeedbacks",
  data() {
    return {
      feedbacks: [],
      currentPage: 1,
      lastPage: 1,
      reply: null,
      totalFeedbacks: 0,
      disable: false,
      success: false,
      message: null,
      selectedFeedback: null
    };
  },

  mounted() {
    this.fetchFeedbacks(1);
  },

  methods: {
    fetchFeedbacks(page) {
      let url = domain + "user/feedback";

      this.disable = true;
      axios
        .get(url, {
          params: {
            order: "created_at",
            users: "budbak",
            orderWay: "desc",
            page: page,
            limit: 10
          }
        })
        .then(res => {
          this.feedbacks = [];
          this.currentPage = res.data.current_page;
          this.lastPage = res.data.last_page;
          this.totalFeedbacks = res.data.total;
          res.data.data.forEach(doc => {
            let dateTime = new Date(doc.created_at);
            dateTime.setHours(dateTime.getHours() + utcHours);
            dateTime.setMinutes(dateTime.getMinutes() + utcMins);
            let date =
              dateTime.getDate() +
              "/" +
              dateTime.getMonth() +
              "/" +
              dateTime.getFullYear();
            var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
            var hours = dateTime.getHours() % 12 || 12;
            let time =
              hours +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds() +
              " " +
              AmOrPm;

            let feedback = {
              id: doc.id,
              user: doc.user,
              subject: doc.subject,
              content: doc.content,
              reply: doc.reply,
              date: date,
              time: time
            };
            this.feedbacks.push(feedback);
            this.disable = false;
          });
        });
    },

    changePage(change) {
      if (change == "prev" && this.currentPage > 1) {
        let newPage = this.currentPage - 1;
        this.fetchFeedbacks(newPage);
      } else {
        if (this.currentPage < this.lastPage) {
          let newPage = this.currentPage + 1;
          this.fetchFeedbacks(newPage);
        }
      }
    },

    selectFeedback(index) {
      this.selectedFeedback = this.feedbacks[index];
    },

    replyQuery() {
      if (
        this.user &&
        this.user.designation == "admin" &&
        this.reply &&
        this.reply != this.selectedFeedback.reply
      ) {
        let url = domain + "user/feedback/" + this.selectedFeedback.id;
        this.disable = true;
        axios
          .post(
            url,
            {
              _method: "put",
              reply: this.reply
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data == "Success") {
              this.message = "Updated successfully";
              this.selectedFeedback.reply = this.reply;
            } else {
              this.message = "Update Failed";
            }
            this.reply = null;

            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    }
  }
};
</script>



