<template>
  <nav
    class="navbar navbar-expand-md sticky-top bg-white border-vh-b justify-content-between"
    v-if="getUserPermission(user.permissions.webappAccess, 'get')"
  >
    <div>
      <button
        class="nav-item pl-0 pr-1 navbar-toggler sidebar-toggler"
        role="presentation"
        id="sidebarCollapse"
        data-toggle="collapse"
        data-target="#usersidebar"
        aria-expanded="false"
        aria-controls="navbar"
      >
        <span class="navbar-toggler-icon">
          <i class="fa fa-align-left"></i>
        </span>
      </button>

      <router-link to="/" class="navbar-brand brand pl-2" tag="a">
        <img
          src="../assets/appLogo.svg"
          alt="FasDo"
          style="width: 2.5em;"
          class="align-self-center pr-2"
        />
        <span>फasDo</span>
        <small style="font-size: 0.475em;">Fasdo kiya kya?</small>
      </router-link>
    </div>

    <!-- middle -->
    <div
      class="ml-auto mr-auto pl-4 dropdown"
      v-if="isLoggedIn && showSearchBar && getUserPermission(user.permissions.inventoryProducts, 'get')"
    >
      <div class="input-group">
        <div
          class="input-group-prepend"
          id="dropdownMenuLink"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          <span class="input-group-text nav-form-bg" id="basic-addon1">
            <i class="fa fa-filter" aria-hidden="true"></i>
          </span>
          <div class="dropdown-menu" aria-labelledby="dropdownMenuLink">
            <span class="dropdown-item cursor-pointer" @click="changeSearchBy('name')">
              <b v-if="searchby == 'name'">Name</b>
              <span v-else>Name</span>
            </span>
            <span class="dropdown-item cursor-pointer" @click="changeSearchBy('barcode')">
              <b v-if="searchby == 'barcode'">Barcode</b>
              <span v-else>Barcode</span>
            </span>
            <span class="dropdown-item cursor-pointer" @click="changeSearchBy('productId')">
              <b v-if="searchby == 'productId'">Product Id</b>
              <span v-else>Product Id</span>
            </span>
          </div>
        </div>
        <input
          type="text"
          class="form-control form-control-sm hz-input nav-form-bg p-4 w-custom"
          :placeholder="'Search by ' + searchby"
          aria-label="Search Here..."
          aria-describedby="basic-addon1"
          v-model="input"
          @keyup.enter="searching()"
        />
        <div
          class="input-group-append"
          id="dropdownMenuLink"
          data-toggle="dropdown"
          aria-haspopup="true"
          aria-expanded="false"
        >
          <span class="input-group-text nav-form-bg" id="basic-addon1">
            <i class="fa fa-search"></i>
          </span>
        </div>
      </div>
    </div>

    <div>
      <button
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarSupportedContent"
        aria-controls="navbarSupportedContent"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon">
          <i class="fa fa-align-right"></i>
        </span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav">
          <li
            class="nav-item align-self-center pl-2 pr-4 pb-1"
            v-if="!showSearchBar && getUserPermission(user.permissions.inventoryProducts, 'get')"
          >
            <button
              class="d-block btn"
              role="presentation"
              aria-expanded="true"
              @click="goToSearch"
            >
              <i class="fa fa-search"></i>
            </button>
          </li>

          <li
            class="nav-item align-self-center pr-4 pb-1"
            v-if="showAddItem && getUserPermission(user.permissions.inventory, 'post')"
          >
            <div>
              <router-link to="/inventory/add">
                <i class="fas fa-lg fa-cart-plus"></i>
              </router-link>
            </div>
          </li>

          <li class="nav-item align-self-center pr-4 pb-1 dropdown notification">
            <div
              id="navbarDropdown"
              class="unread-notification"
              role="button"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img src="../assets/bell.svg" style="width: 17px;" />
              <span class="badge badge-danger">
                {{
                countUnreadNotification
                }}
              </span>
            </div>
            <ul class="dropdown-menu">
              <li class="head text-light bg-dark">
                <div class="row">
                  <div class="col-lg-12 col-sm-12 col-12">
                    <span
                      class="float-right text-light pr-2"
                      @click.prevent="markAsRead()"
                    >Mark all as read</span>
                  </div>
                </div>
              </li>
              <div class="dropdown-content">
                <li
                  class="notification-box"
                  v-for="notification in notifications"
                  :key="notification.id"
                >
                  <router-link
                    :to="{
                      name: 'ViewOrder',
                      params: { orderId: notification.data.orderId },
                    }"
                  >
                    <div class="col-lg-12">
                      <strong
                        class="text-dark"
                        v-if="
                          notification.data.status &&
                          notification.data.status == 1
                        "
                      >Order Cancelled</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 2
                        "
                      >Order request return</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 3
                        "
                      >Order Placed</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 4
                        "
                      >Order Processing</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 5
                        "
                      >Order Packed</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 6
                        "
                      >Order Dispatched</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 7
                        "
                      >Order Delivered</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 8
                        "
                      >Order Failed</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 9
                        "
                      >Order Return Approved</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 10
                        "
                      >Order Returned</strong>
                      <strong
                        class="text-dark"
                        v-else-if="
                          notification.data.status &&
                          notification.data.status == 'Items not available'
                        "
                      >Order Availability</strong>
                      <div>{{ notification.data.message }}</div>
                      <small
                        class="text-dark float-right"
                      >{{ notification.date }}, {{ notification.time }}</small>
                    </div>
                  </router-link>
                </li>
              </div>
            </ul>
            <!-- /dropdown-container -->
          </li>
          <li class="nav-item align-self-center pl-2 pr-4 pb-1">
            <button
              class="d-block navbar-toggler sidebar-toggler"
              role="presentation"
              id="sidebarCollapse"
              data-toggle="collapse"
              data-target="#usersettingSidebar"
              aria-expanded="true"
              aria-controls="navbar"
              @click="switchClass"
            >
              <img src="../assets/gear.svg" style="width: 17px;" />
            </button>
          </li>
          <li class="nav-item small">
            <router-link to="/login" class="nav-link" v-if="!isLoggedIn">
              <i class="fas fa-power-off pr-1"></i>
              <span class="d-none d-lg-block">Login</span>
            </router-link>
          </li>

          <li class="nav-item small" v-if="user">
            <span class="nav-link d-none d-lg-block" v-if="isLoggedIn">Hi,{{ user.name }}</span>
          </li>

          <li class="nav-item small">
            <a
              role="button"
              tabindex="0"
              class="nav-link d-flex align-items-center"
              @click.prevent="logout"
              v-if="isLoggedIn"
            >
              <i class="fas fa-power-off pr-1"></i>
              <span class="d-none d-lg-block">Logout</span>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </nav>
</template>

<script>
import db from "@/firebaseInit";
import firebase from "firebase";
import store from "@/store/store";
import {
  domain,
  utcHours,
  utcMins,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import { bus } from "@/main";
import axios from "axios";

export default {
  name: "navbar",
  data() {
    return {
      isLoggedIn: false,
      currentUser: false,
      search: [],
      input: null,
      notifications: [],
      countUnreadNotification: 0,
      searchby: "name"
      // stockLess: 0,
      // outOfStock: 0
    };
  },

  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
    }
  },

  mounted() {
    setTimeout(() => {
      this.getNotifications();
    }, 2000);
  },

  computed: {
    showAddItem() {
      if (
        this.$route.name != "login" &&
        this.$route.name != "Policies" &&
        this.$route.name != "UnAuthorized" &&
        this.$route.name != "invalidUser" &&
        this.$route.name != "underMaintenance"
      ) {
        return true;
      } else {
        return false;
      }
    },
    showSearchBar() {
      if (this.$route.name == "searchItem") {
        return true;
      } else {
        return false;
      }
    }
  },

  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    },

    changeSearchBy(by) {
      this.searchby = by;
    },

    goToSearch() {
      if (this.$route.name != "searchItem") {
        if (confirm("You'll be taken to search result page.")) {
          this.$router.push({ name: "searchItem" });
        }
      }
    },

    getNotifications() {
      if (this.user) {
        this.notifications = [];
        let url = domain + "user/notification";
        axios
          .get(url, {
            headers: {
              "X-api-token": firebase.auth().currentUser.uid
            }
          })
          .then(res => {
            res.data.forEach(doc => {
              let dateTime = new Date(doc.created_at);
              dateTime.setHours(dateTime.getHours() + utcHours);
              dateTime.setMinutes(dateTime.getMinutes() + utcMins);
              let date =
                dateTime.getDate() +
                "/" +
                dateTime.getMonth() +
                "/" +
                dateTime.getFullYear();
              var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
              var hours = dateTime.getHours() % 12 || 12;
              let time =
                hours +
                ":" +
                dateTime.getMinutes() +
                ":" +
                dateTime.getSeconds() +
                " " +
                AmOrPm;

              let notify = {
                data: doc.data,
                date: date,
                time: time,
                read_at: doc.read_at
              };
              this.notifications.unshift(notify);
              if (doc.read_at == null) {
                this.countUnreadNotification++;
              }
            });
          });
      }
    },

    markAsRead() {
      let url = domain + "user/notification/" + this.user.uuid;
      axios
        .post(
          url,
          {
            _method: "put",
            markAsRead: 1
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          if (res.data) {
            console.log(res.data);
            Vue.set(this.items[index], "active", res.data.active);
          }
        });
    },

    searching() {
      if (this.input) {
        var searched = [this.input, this.searchby];
        bus.$emit("searchItem", { searched });
      }
    },

    switchClass() {
      bus.$emit("switchClass");
    }
  }
};
</script>

<style scoped>
.brand {
  font-weight: 800;
  color: #004d40;
}

.nav-form-bg {
  background-color: #fbfbfb;
}

.w-custom {
  width: 32rem;
}

.unread-notification {
  position: relative;
  cursor: pointer;
}

.unread-notification span {
  position: absolute;
  top: -6px;
  left: 6px;
}

a {
  text-decoration: none;
  color: #263238;
}

.notification .dropdown-menu {
  top: 60px;
  right: 0px;
  left: unset;
  width: 500px;
  box-shadow: 0px 5px 7px -1px #c1c1c1;
  padding-bottom: 0px;
  padding: 0px;
}
.notification .dropdown-menu:before {
  content: "";
  position: absolute;
  top: -20px;
  right: 12px;
  border: 10px solid #343a40;
  border-color: transparent transparent #343a40 transparent;
}

.dropdown-content {
  height: 500px;
  overflow-y: scroll;
}

.notification-box {
  padding: 1px 1px;
  border-radius: 3%;
}

@media (max-width: 640px) {
  .notification .dropdown-menu {
    top: 50px;
    left: -16px;
    width: 290px;
  }
}

a:hover {
  color: #263238;
}
</style>



