<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>ADD NEW COCATEGORY
          </h5>
          <div class="row">
            <div
              class="col-sm-12"
              style="display: block; height: 75vh; max-height: 75vh; overflow-y: auto;"
            >
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields
                    marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>COID*</label>
                        {{ coid }}
                        <select
                          v-model="coid"
                          class="form-control form-control-sm"
                          :disabled="disable"
                        >
                          <option
                            :value="subcategory.coid"
                            class="font-weight-bold"
                            v-for="subcategory in subcategories"
                            :key="subcategory.coid"
                          >
                            {{ subcategory.name }} -
                            {{ subcategory.coid }}
                          </option>
                        </select>
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{ errors[2] }}</span>
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Name</label>
                        <input
                          type="text"
                          v-model="name"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[3]"
                        >{{ errors[3] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Item ID</label>
                        <input
                          type="text"
                          v-model="item_id"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable || edit"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[4]"
                        >{{ errors[4] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Priority Order</label>
                        <input
                          type="number"
                          v-model.number="serialNum"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Display Offer</label>
                        <input
                          type="number"
                          v-model.number="displayOffer"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[4]"
                        >{{ errors[4] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Price Band Value</label>
                        <input
                          type="number"
                          v-model.number="priceBandValue"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Active</label>
                        <input
                          type="checkbox"
                          v-model="active"
                          class="form-control-sm align-self-center"
                          true-value="1"
                          false-value="0"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click.prevent="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <router-link to="/inventory" class="btn btn-sm btn-secondary">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </router-link>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{ errors[1] }}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 card shadow p-0 ml-2">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-list pl-1 pr-3"></i>CO CATEGORIES
          </h5>
          <div class="row">
            <div
              class="col-sm-12"
              style="display: block; height: 75vh; max-height: 75vh; overflow-y: auto;"
            >
              <div class="row p-3">
                <div class="col-sm-12">
                  <div
                    class="card pb-2 border-0 mb-2"
                    v-for="(category, catIndex) in cocategories"
                    :key="category.subcatcode"
                  >
                    <div data-toggle="collapse" :data-target="'#collapse' + catIndex">
                      <span
                        class="font-weight-bold justify-content-center"
                        style="background:#ffffff"
                      >{{ category.name }}</span>
                      <i class="fa fa-angle-down float-right"></i>
                    </div>
                    <div
                      class="card-body collapse"
                      v-for="(subcategory, subIndex) in category.subcategories"
                      :key="subcategory.coid"
                      :id="'collapse' + catIndex"
                    >
                      <div data-toggle="collapse" :data-target="'#collapse' + catIndex + subIndex">
                        <span>{{ subcategory.name }}</span>
                        <i class="fa fa-angle-down float-right"></i>
                      </div>
                      <div class="collapse" :id="'collapse' + catIndex + subIndex">
                        <hr />
                        <table>
                          <thead>
                            <tr>
                              <th></th>
                              <th>Priority</th>
                              <th>Item Id</th>
                              <th>Co Id</th>
                              <th class="pl-2">Name</th>
                              <th class="pl-2">Actions</th>
                            </tr>
                          </thead>
                          <tbody v-if="subcategory.cocategories.length > 0">
                            <tr
                              v-for="(cocategory,
                              index) in subcategory.cocategories"
                              :key="cocategory.item_id"
                            >
                              <td>
                                <i
                                  class="fas fa-circle text-success"
                                  aria-hidden="true"
                                  v-if="cocategory.active == 1"
                                ></i>
                                <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                              </td>
                              <td class="text-center">{{ cocategory.serialNum }}</td>
                              <td>{{ cocategory.item_id }}</td>
                              <td>{{ cocategory.coid }}</td>
                              <td class="pl-2">{{ cocategory.name }}</td>
                              <td class="pl-2">
                                <a
                                  style="color:#424242;"
                                  href="#"
                                  title="delete this item"
                                  @click.prevent="
                                    deleteCo(catIndex, subIndex, index)
                                  "
                                >
                                  <i class="fa fa-trash"></i>
                                </a>
                                <a
                                  style="color:#424242;"
                                  href="#"
                                  title="delete this item"
                                  @click.prevent="
                                    editCoCategory(catIndex, subIndex, index)
                                  "
                                >
                                  <i class="fa fa-pencil-alt"></i>
                                </a>
                              </td>
                            </tr>
                          </tbody>
                          <tbody v-else>
                            <td colspan="6" class="text-center">No Cocat Available</td>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import db from "@/firebaseInit";
import firebase from "firebase";
import axios from "axios";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import { firestorage } from "@/firebaseInit";

export default {
  name: "cocategory",
  data() {
    return {
      coid: null,
      item_id: null,
      name: null,
      active: "0",
      serialNum: 0,
      priceBandValue: 10,
      displayOffer: 0,

      subcategories: [],
      cocategories: [],
      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false
    };
  },

  created() {
    this.fetchSubCategories();
    this.fetchCoCategories();
  },

  methods: {
    add() {
      if (this.validate()) {
        this.disable = true;
        let url = domain + "cocat";

        let flag = true;
        //check if serialNum is taken by any other number
        this.cocategories.forEach((cat, catIndex) => {
          cat.subcategories.forEach((subcat, subIndex) => {
            if (subcat.coid == this.coid) {
              subcat.cocategories.forEach((cocat, coIndex) => {
                if (
                  this.serialNum == cocat.serialNum &&
                  this.item_id != cocat.item_id
                ) {
                  alert(
                    "Please Change the Priority Order. The one you used already exists"
                  );
                  flag = false;
                }
              });
            }
          });
        });

        if (flag == true) {
          if (this.edit == false) {
            axios
              .post(
                url,
                {
                  coid: this.coid,
                  item_id: this.item_id,
                  name: this.name,
                  priceBandValue: this.priceBandValue,
                  active: this.active,
                  displayOffer: this.displayOffer,
                  serialNum: this.serialNum
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                this.cocategories.forEach((cat, catIndex) => {
                  cat.subcategories.forEach((subcat, subcatIndex) => {
                    if (subcat.coid == this.coid) {
                      let data = {
                        coid: res.data.coid,
                        item_id: res.data.item_id,
                        name: res.data.name,
                        image: res.data.image,
                        active: res.data.image,
                        priceBandValue: res.data.priceBandValue,
                        serialNum: res.data.serialNum,
                        displayOffer: res.data.displayOffer
                      };
                      this.cocategories[catIndex].subcategories[
                        subcatIndex
                      ].cocategories.unshift(data);
                    }
                  });
                });

                this.coid = null;
                this.item_id = null;
                this.name = null;
                this.priceBandValue = 10;
                this.serialNum = 0;
                this.displayOffer = 0;

                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                //// to show
                this.message = "Data Upload Failed";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
                console.log(err);
              });
          } else {
            url = domain + "cocat/" + this.item_id;
            axios
              .post(
                url,
                {
                  _method: "PUT",
                  coid: this.coid,
                  name: this.name,
                  priceBandValue: this.priceBandValue,
                  active: this.active,
                  displayOffer: this.displayOffer,
                  serialNum: this.serialNum
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                this.cocategories.forEach((cat, catIndex) => {
                  cat.subcategories.forEach((subcat, subcatIndex) => {
                    subcat.cocategories.forEach((doc, coIndex) => {
                      if (doc.item_id == res.data.item_id) {
                        let data = {
                          coid: res.data.coid,
                          item_id: res.data.item_id,
                          name: res.data.name,
                          image: res.data.image,
                          active: res.data.image,
                          displayOffer: res.data.displayOffer,
                          serialNum: res.data.serialNum
                        };

                        Vue.set(
                          this.cocategories[catIndex].subcategories[subcatIndex]
                            .cocategories,
                          coIndex,
                          data
                        );
                      }
                    });
                  });
                });

                this.coid = null;
                this.item_id = null;
                this.name = null;
                this.priceBandValue = 10;
                this.active = "0";
                this.serialNum = 0;
                this.displayOffer = 0;
                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                });

                this.edit = false;
              })
              .catch(err => {
                /// to show
                this.message = "Data Upload In MYSql Failed";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          }
        } else {
          this.disable = false;
        }
      }
    },

    deleteCo(catIndex, subIndex, index) {
      if (
        confirm(
          "You are about to delete " +
            this.cocategories[catIndex].subcategories[subIndex].cocategories[
              index
            ].name
        )
      ) {
        let url =
          domain +
          "cocat/" +
          this.cocategories[catIndex].subcategories[subIndex].cocategories[
            index
          ].item_id;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              db.collection("co category")
                .where(
                  "item_id",
                  "==",
                  this.cocategories[catIndex].subcategories[subIndex]
                    .cocategories[index].item_id
                )
                .get()
                .then(res => {
                  res.forEach(doc => {
                    doc.ref.delete();
                  });
                });
            }
            this.cocategories[catIndex].subcategories[
              subIndex
            ].cocategories.splice(index, 1);
          });
      }
    },

    editCoCategory(catIndex, subIndex, index) {
      if (this.cocategories) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.item_id = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].item_id;

        this.name = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].name;

        this.coid = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].coid;

        this.priceBandValue = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].priceBandValue;

        this.active = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].active;

        this.displayOffer = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].displayOffer;

        this.serialNum = this.cocategories[catIndex].subcategories[
          subIndex
        ].cocategories[index].serialNum;
      }
    },

    fetchCoCategories() {
      let url = domain + "cat";
      axios
        .get(url, {
          params: {
            cocategories: "budbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let subcategories = [];
            doc.subcategories.forEach(subcat => {
              let cocategories = [];
              subcat.cocategories.forEach(cocat => {
                let data = {
                  coid: cocat.coid,
                  item_id: cocat.item_id,
                  name: cocat.name,
                  image: cocat.image,
                  active: cocat.active,
                  displayOffer: cocat.displayOffer,
                  priceBandValue: cocat.priceBandValue,
                  serialNum: cocat.serialNum
                };

                cocategories.push(data);
              });

              let set = {
                coid: subcat.coid,
                name: subcat.name,
                image: subcat.image,
                active: subcat.active,
                displayOffer: subcat.displayOffer,
                cocategories: cocategories
              };

              subcategories.push(set);
            });

            let category = {
              subcatcode: doc.subcatcode,
              name: doc.name,
              active: doc.active,
              subcategories: subcategories
            };
            this.cocategories.push(category);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchSubCategories() {
      let url = domain + "subcat";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            this.subcategories.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    validate() {
      this.errors = [];
      if (this.item_id && this.name && this.coid) {
        return true;
      } else {
        if (!this.item_id) {
          Vue.set(this.errors, 4, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 3, "Fill Name");
        }
        if (!this.coid) {
          Vue.set(this.errors, 2, "Choose Subcategory");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>



