import Vue from "vue";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import store from "@/store/store";
import {
  inventoryDataMixin
} from './inventoryDataMixin.js';
import {
  imageControlMixin
} from '../imageControlMixin.js';
import $ from "jquery";
import firebase from "firebase";
import axios from "axios";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";

export const inventoryMixin = {
  mixins: [inventoryDataMixin, imageControlMixin],
  data() {
    return {
      active: "1",
      address: null,
      availableBrands: [],
      barcode: null,
      brand: null,
      disable: false,
      disPriority: 0,
      description: "<p></p>",
      dropShip: true,
      dropShipAddress: null,
      disVal: 0,
      detailImages: [],
      // ckeditor
      editor: ClassicEditor,
      editorData: "<p>Description</p>",
      editorConfig: {},

      enterBrand: false,
      expiryDate: null,
      errors: [],
      errorClass: 'small text-danger font-weight-bold',
      features: "<p></p>",
      ingredients: "<p></p>",
      image: null,
      itemId: null,
      maxLimit: 0,
      mfgDate: null,
      name: null,
      priceBand: 10,
      profitLoss: 0,
      profitLossPer: 0,
      searchingItem: null,
      searchProductID: null,
      shelfLife: null,
      sku: null,
      stock: 0,
      qty: 0,
      unit: null,
      unit2: null,

      message: null,
      disableMessage: null,
      success: false,
      similarItems: [],

      duplicate: true,
      othersimilarProduct: false,
      mrp: 0,
      msp: 0,
      discount: 0,
      currentPrice: 0,
      similarProductId: null,

      units: [],
      category: null,
      subcategory: null,
      cocategory: null,
      categories: [],
      subcategories: [],
      cocategories: [],

      Discalculate: false, // disabling text field to calculate discount value
    }
  },

  created() {
    this.getBrands();
    this.getUnits();

  },

  methods: {

    addBrand() {
      if (
        (this.user.designation == "admin" ||
          this.user.designation == "webappUploader") &&
        this.user.status == "active"
      ) {
        let url = domain + "item/brand";
        axios
          .post(
            url, {
              name: this.brand
            }, {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            //// to show
            if (res.data) {
              let brand = {
                id: res.data.id,
                name: res.data.name
              };
              this.availableBrands.unshift(brand);
              this.enterBrand = false;

              this.brand = brand;
            }
          })
          .catch(err => {
            // delete in firebase
            /// to show
            this.message = "Uploading Brand Failed";
            this.success = true;
            this.disable = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    addNewBrand() {
      this.enterBrand = true;
      this.brand = null;
    },

    checkActivationItem(event) {
      if (
        (this.subcategories[this.subcategory].active != "1" ||
          this.categories[this.category].active != "1" ||
          this.cocategories[this.cocategory].active != "1")) {
        this.active = 0;
        if (this.categories[this.category].active != "1") {
          alert("This item will remain inactive as it's category is inactive.");
        } else if (this.subcategories[this.subcategory].active != "1") {
          alert("This item will remain inactive as it's subcategory is inactive.");
        } else if (this.cocategories[this.cocategory].active != "1") {
          alert("This item will remain inactive as it's cocategory is inactive.");
        }
      }

    },

    checkItem() {
      let url = domain + "item";
      axios
        .get(url, {
          params: {
            name: this.name,
            unit: this.unit,
            qty: this.qty,
            count: "laoDo"
          }
        })
        .then(res => {
          if (res.data) {
            /// to show
            this.message =
              this.name +
              " exists";
            this.success = true;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
            this.duplicate = true;
          } else {
            this.duplicate = false;
          }
        });
      return this.duplicate;
    },

    checkSimilarName() {
      this.searchingItem = true;
      let url = domain + "search";
      this.similarItems = [];
      axios
        .get(url, {
          params: {
            table: "items",
            data: this.processContent("string", this.name)
          }
        })
        .then(res => {
          if (res.data.exactSearch != "" && res.data.exactSearch != null) {
            /// to show
            res.data.exactSearch.forEach(doc => {
              this.similarItems.push(doc);
            });
          } else {
            this.similarItems.push({
              name: "No item Available"
            });

            this.setSimilarId("no", "item");
          }

          this.searchingItem = false;
        });
    },
    discountCal() {
      if (
        this.mrp != null &&
        this.currentPrice != null &&
        this.mrp != "" &&
        this.currentPrice != "" &&
        this.currentPrice <= this.mrp
      ) {
        this.discount = (
          ((this.mrp - this.currentPrice) / this.mrp) *
          100
        ).toFixed(0);
      } else {
        this.discount = 0;
      }
    },

    generateSKU() {
      let nameCode = this.name.split(" ");
      let nameId = "";
      nameCode.forEach(word => {
        var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = initialNameLetter.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          nameId = nameId + initialNameLetter;
        }
      });

      let cat = this.categories[this.category].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let catId = "";

      cat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          catId = catId + word;
        }
      });

      let subcat = this.subcategories[this.subcategory].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let subcatId = "";

      subcat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          subcatId = subcatId + word;
        }
      });

      let cocat = this.cocategories[this.cocategory].name
        .substring(0, 3)
        .toUpperCase()
        .split("");
      let cocatId = "";

      cocat.forEach(word => {
        //var initialNameLetter = word.substring(0, 1).toUpperCase();

        // checking if word if alphanumeric
        let code = word.charCodeAt(0);
        if (!(code > 47 && code < 58) &&
          !(code > 64 && code < 91) &&
          !(code > 96 && code < 123)
        ) {
          // just ignore that letter to be added to the id
        } else {
          cocatId = cocatId + word;
        }
      });

      let id = catId + "-" + subcatId + "-" + cocatId; //"-" + nameId

      return id;
    },

    getBrands() {
      let url = domain + "item/brand";
      let flag = 0;
      axios.get(url, {
        params: {
          order: 'name',
          orderWay: 'ASC'
        }
      }).then(res => {
        res.data.forEach((doc, index) => {
          let brand = {
            id: doc.id,
            name: doc.name
          };
          this.availableBrands.push(brand);
        });
      });
    },

    getCoCategories() {
      this.ModalContentLoading = true;
      this.cocategories = [];
      let url = domain + "cocat";

      if (this.subcategories[this.subcategory].active == "0") {
        this.active = "0";
      }
      axios
        .get(url, {
          params: {
            coid: this.subcategories[this.subcategory].coid
          }
        })
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              const cocat = {
                name: doc.name,
                itemId: doc.item_id,
                priceBandValue: doc.priceBandValue
              };
              this.cocategories.push(cocat);
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.ModalContentLoading = false;
    },

    getNameDetails() {
      if (this.showEdit != -1) {
        this.showEdit = 0;
      }
      this.checkItem();
      this.checkSimilarName();
    },

    getSubCategories() {
      this.ModalContentLoading = true;
      this.subcategories = [];
      this.cocategories = [];
      let url = domain + "subcat";

      if (this.categories[this.category].active == "0") {
        this.active = "0";
      }

      axios
        .get(url, {
          params: {
            subcatid: this.categories[this.category].subcatid
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            const subcat = {
              name: doc.name,
              coid: doc.coid,
              active: doc.active
            };
            this.subcategories.push(subcat);
          });
        })
        .catch(err => {
          console.log(err);
        });
      this.ModalContentLoading = false;
    },

    getUnits() {
      this.units = [];
      let url = domain + "item/units";
      axios
        .get(url)
        .then(res => {
          if (res.data) {
            res.data.forEach(doc => {
              this.units.push(doc);
            });
          }
        })
        .catch(err => {
          this.units = [];
          console.log(err);
        });
    },

    fetchCategories() {
      let url = domain + "cat";
      let categories = [];
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            const cat = {
              name: doc.name,
              subcatid: doc.subcatcode,
              active: doc.active
            };
            categories.push(cat);
          });
          this.categories = categories;
        })
        .catch(err => {
          console.log(err);
        });
    },

    processContent(type, data) {
      if (data) {
        if (type == "string") {
          return data.trim();
        } else if (type == "html") {
          let lastIndex = data.lastIndexOf("<p>&nbsp;</p>");
          if (lastIndex != -1 && data.length - 14 == lastIndex) {
            // will remove everywhere
            // data = data.replace(
            //   new RegExp(this.escapeRegExp("<p>&nbsp;</p>"), "g"),
            //   ""
            // );
            data = data.substring(0, lastIndex);
          }
          return data;
        }
      }
      return data;
    },

    profitLossCal() {
      if (
        this.currentPrice != null &&
        this.msp != null &&
        this.currentPrice != "" &&
        this.msp != ""
      ) {
        this.profitLoss = this.currentPrice - this.msp;
        this.profitLossPer = ((this.profitLoss / this.msp) * 100).toFixed(0);
        if (this.profitLoss < 0) {
          this.profitLossPer = Math.abs(this.profitLossPer);
          this.profitLoss = Math.abs(this.profitLoss);
          $("#profitLossModal").modal("show");
        }
      }
    },

    priceBandCalculator(change) {
      //calculating price band
      // if (change == "priceBand" && this.msp != null && this.msp != "" && !this.currentPrice) {
      //     //calculating current price
      //     let add = Math.round(this.msp * (this.priceBand / 100));
      //     this.currentPrice = this.msp + add;

      // } else if (change == "msp" && this.priceBand != null) {
      //     //calculating current price
      //     let add = Math.round(this.msp * (this.priceBand / 100));
      //     this.currentPrice = this.msp + add;
      //     console.log(add);
      // }
      // console.log('add');

      this.discountCal();
    },

    searchItem() {
      if (this.searchProductID) {
        let url = domain + "item";
        this.disable = true;
        this.disableMessage = "Checking our Databases...";
        axios
          .get(url, {
            params: {
              productId: this.searchProductID.trim()
            }
          })
          .then(res => {
            if (res.data.length > 0) {
              res.data.forEach(doc => {
                this.productId = doc.productId;
                this.itemId = doc.itemId;
                this.currentPrice = doc.currentPrice;
                this.name = doc.name;
                this.image = doc.image;
                this.detailImages = doc.detailImages;
                this.discount = doc.discount;
                this.unit = doc.unit;
                this.unit2 = doc.unit2;
                this.color = doc.color;
                this.mrp = doc.MRP;
                this.stock = doc.stock;
                this.home = doc.home; // doubt;
                this.allcat = doc.allcat; //doubt;
                this.itemId = doc.itemId;
                this.sku = doc.sku;
                this.disVal = doc.dis_val;
                this.disPriority = doc.disPriority; //dou;
                this.description = doc.description;
                this.ingredients = doc.ingredients;
                this.features = doc.features;
                this.msp = doc.msp;
                this.qty = doc.qty;
                this.mfgDate = doc.mfgDate;
                this.expiryDate = doc.expiryDate;
                this.shelfLife = doc.shelfLife;
                this.brand = doc.item_brand;
                this.address = doc.address;
                this.similarProductId = doc.similarProductId;
                this.uploadedBy = doc.uploadedBy;
                this.uploadedAt = doc.uploadedAt;
                this.updatedAt = doc.updatedAt;
                this.maxLimit = doc.maxLimit;
                this.priceBand = Math.round(
                  ((this.currentPrice - this.msp) / this.currentPrice) * 100
                );
                this.active = doc.active;

                if (doc.dropShipAddress) {
                  this.dropShip = true;
                  this.dropShipAddress = doc.dropShipAddress;
                }
                //

                // fetching category cocat subcat
                let courl = domain + "cocat";
                axios
                  .get(courl, {
                    params: {
                      item_id: this.itemId
                    }
                  })
                  .then(res => {
                    if (res.data) {
                      this.cocategories = res.data;
                      // pusing just 1 cocategory to array and setting it's index
                      this.cocategory = 0;
                      this.priceBand = this.cocategories[
                        this.cocategory
                      ].priceBandValue;
                      //subcat
                      let suburl = domain + "subcat";
                      axios
                        .get(suburl, {
                          params: {
                            coid: res.data[0].coid
                          }
                        })
                        .then(ressub => {
                          if (ressub.data) {
                            this.subcategories = ressub.data;
                            this.subcategory = 0;
                            // fetching category
                            let caturl = domain + "cat";
                            axios
                              .get(caturl, {
                                params: {
                                  subcatcode: ressub.data[0].subcatid
                                }
                              })
                              .then(rescat => {
                                if (rescat.data) {
                                  this.categories = rescat.data;
                                  this.category = 0;
                                }
                              });
                          }
                        });
                    }
                  });
              });
              this.disable = false;
              this.disableMessage =
                "Uploading Data, Kripya Shanti banaye rkhe...";
            } else {
              this.message = "This product Don't exist in database";
              this.success = true;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);

              this.disable = false;
              this.disableMessage =
                "Uploading Data, Kripya Shanti banaye rkhe...";
            }
          });
      } else {
        if (
          this.itemId &&
          this.name &&
          this.discount >= 0 &&
          this.currentPrice > 0 &&
          this.unit &&
          this.mrp > 0 &&
          this.msp > 0 &&
          this.stock > 0 &&
          this.allcat &&
          this.disPriority > 0 &&
          this.description &&
          (this.disVal == "0" || this.disVal == "1") &&
          this.qty > 0
        ) {
          // agr koi data hai tho gayb na kre, dimag ka bhosda ho jaega agr delete hua itna mehnat se likha data//

          this.category = null;
          this.subcategory = null;
          this.cocategory = null;
          this.categories = [];
          this.subcategories = [];
          this.cocategories = [];
          this.fetchCategories();
        } else {
          ///////otherwise udha do sb///////
          this.currentPrice = 0;
          this.name = null;
          this.image = null;
          this.discount = 0;
          this.unit = null;
          this.unit2 = null;
          this.mrp = 0;
          this.msp = 0;
          this.stock = 0;
          this.home = "hcat"; // doubted
          this.allcat = "acat"; //doubted
          this.itemId = null;
          this.disPriority = 0; //doubt
          this.qty = 0;
          this.sku = null;
          this.description = null;
          this.media = [];
          this.mfgDate = null;
          this.expiryDate = null;
          this.shelfLife = null;
          this.brand = null;
          this.address = null;
          this.disVal = "0";
          this.dropShip = false;
          this.dropShipAddress = null;
          this.maxLimit = 0;
          this.ingredients = "<p></p>";
          this.packingType = null;
          this.features = "<p></p>";
          this.category = null;
          this.subcategory = null;
          this.cocategory = null;
          this.categories = [];
          this.subcategories = [];
          this.cocategories = [];
          this.fetchCategories();
        }
      }
    },

    setBrand(brand) {
      this.enterBrand = false;
      this.brand = brand;
    },

    setCocategory() {
      if (this.cocategories[this.cocategory].active == "0") {
        this.active = "0";
      }
      this.itemId = this.cocategories[this.cocategory].itemId;
      this.priceBand = this.cocategories[this.cocategory].priceBandValue;
    },

    setCurrentPrice() {
      this.discountCal();
      this.pricaBandCalculator("currentPrice");
    },

    setMsp() {
      this.profitLossCal();
      this.pricaBandCalculator("msp");
    },

    setMrp() {
      this.discountCal();
      this.pricaBandCalculator("mrp");
    },

    setSimilarId(type, data) {
      if (type) {
        if (type == "id") {
          this.similarProductId = this.similarItems[data].similarProductId;
        } else if (type == "productId") {
          let url = domain + "item";
          this.searchingItem = true;

          let id = data.target.value;
          axios
            .get(url, {
              params: {
                productId: id
              }
            })
            .then(res => {
              res.data.forEach(doc => {
                this.similarProductId = doc.similarProductId;
              });
              this.searchingItem = false;
            });
        } else {
          let url = domain + "item";
          this.searchingItem = true;
          axios
            .get(url, {
              params: {
                max: "similarProductId"
              }
            })
            .then(res => {
              this.similarProductId = Number(res.data) + 1;
              this.searchingItem = false;
            });
        }
      }
    },

    validate() {
      this.errors = [];
      if (
        this.itemId &&
        this.name &&
        this.discount >= 0 &&
        this.currentPrice > 0 &&
        this.unit &&
        this.mrp > 0 &&
        this.msp > 0 &&
        this.stock >= 0 &&
        (this.disVal == 1 || this.disVal == 0) &&
        this.qty &&
        this.similarProductId > 0 &&
        this.brand
      ) {
        return true;
      } else {
        Vue.set(
          this.errors,
          0,
          "Make sure you have added all the required fields. Contact the developer if you face any issue."
        );

        if (!this.name) {
          Vue.set(this.errors, 1, "Enter Valid Name");
        }

        if (!this.itemId) {
          Vue.set(this.errors, 2, "Choose Category");
        }

        if (!this.unit) {
          Vue.set(this.errors, 3, "Choose Valid Unit");
        }
        if (this.qty <= 0) {
          Vue.set(this.errors, 4, "Add a Quantity");
        }
        if (this.stock < 0) {
          Vue.set(this.errors, 5, "Add stock");
        }
        if (this.msp <= 0) {
          Vue.set(this.errors, 6, "MSP can't be 0");
        }
        if (this.currentPrice <= 0) {
          Vue.set(this.errors, 7, "Current Price can't be 0");
        }
        if (this.mrp <= 0) {
          Vue.set(this.errors, 8, " MRP can't be 0");
        }
        if (this.disVal != 1 && this.disVal != 0) {
          Vue.set(this.errors, 9, "Choose valid Discount Value");
        }
        if (!this.similarProductId) {
          Vue.set(this.errors, 11, "Choose it's variant");
        }
        if (this.brand == null || this.brand.length == 0) {
          Vue.set(this.errors, 12, "Choose a Brand");
        }

        return false;
      }
    }
  },
}



