<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="far fa-list-alt"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>DELIVERIES</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small">
                <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">dashboard</router-link>>
                  <router-link to="/order/dashboard" class="cursor-pointer" tag="span">sales order</router-link>>
                  <router-link to="/delivery/all" class="cursor-pointer" tag="span">all</router-link>
                </span>
              </div>
            </div>
            <hr class="pl-4 pr-4" />
            <!-- content -->
            <DashBoard
              :newDeliveries="deliveries"
              :disable="loading"
              @fetchDeliveries="fetchDeliveries"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";
import DashBoard from "./DashBoard.vue";
import store from "@/store/store";
import axios from "axios";

export default {
  name: "AllDeliveries",
  components: {
    DashBoard
  },
  data() {
    return {
      deliveries: [],
      loading: false
    };
  },

  computed: {
    size() {
      return store.state.deliveryPageSize;
    }
  },

  mounted() {
    this.fetchDeliveries();
  },

  methods: {
    fetchDeliveries(page) {
      let url = domain + "delivery";
      if (!page) {
        page = 1;
      }

      this.loading = true;
      axios
        .get(url, {
          params: {
            by: "with",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let deliveryList = [];
          res.data.data.forEach(doc => {
            if (doc.order) {
              let orderTime = this.getDateTime(doc.created_at);
              let date = orderTime[0];
              let time = orderTime[1];

              let delivery = {
                deliveryId: doc.id,
                orderId: doc.orderId,
                products: doc.order.products,
                amount: doc.order.totalPayableAmount,
                status: doc.status,
                paidAmount: doc.order.paidAmount,
                deliveryBy: doc.by.user.name,
                orderDate: date,
                orderTime: time,

                checked: false
              };

              deliveryList.push(delivery);

              store.dispatch("patchdeliveryPageCount", res.data.last_page);
              store.dispatch("patchdeliveryPageNumber", res.data.current_page);
              store.dispatch("patchtotalDeliveries", res.data.total);
            }
          });

          this.deliveries = deliveryList;
        });

      this.loading = false;
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



