<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- content -->
            <DashBoard
              :newDeliveries="deliveries"
              :disable="loading"
              :status="status"
              @fetchDeliveries="fetchDeliveries"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from "vue";
import { domain } from "@/store/variable";
import { utcHours } from "@/store/variable";
import { utcMins } from "@/store/variable";
import DashBoard from "./DashBoard.vue";
import store from "@/store/store";
import axios from "axios";

export default {
  name: "ReturnPickupDelivery",
  components: {
    DashBoard
  },
  data() {
    return {
      deliveries: [],
      loading: false,
      status: 6
    };
  },

  computed: {
    size() {
      return store.state.deliveryPageSize;
    }
  },

  mounted() {
    this.fetchDeliveries();
  },

  methods: {
    fetchDeliveries(page) {
      let url = domain + "delivery";

      if (!page) {
        page = 1;
      }

      this.loading = true;
      axios
        .get(url, {
          params: {
            status: this.status,
            userAddress: "with",
            by: "with",
            inventoryAddress: "with",
            returned: "with",
            order: "updated_at",
            orderWay: "desc",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let deliveryList = [];
          res.data.data.forEach(doc => {
            if (doc.order) {
              let date = null;
              let time = null;
              if (doc.deliveredAt) {
                let dateTime = new Date(doc.deliveredAt);
                dateTime.setHours(dateTime.getHours() + utcHours);
                dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                let month = dateTime.getMonth() + 1;
                date =
                  dateTime.getDate() +
                  "/" +
                  month +
                  "/" +
                  dateTime.getFullYear();
                time =
                  dateTime.getHours() +
                  ":" +
                  dateTime.getMinutes() +
                  ":" +
                  dateTime.getSeconds();
              }

              let delivery = {
                deliveryId: doc.id,
                order: doc.order,
                status: doc.status,
                deliveryBy: doc.by.user.name,
                date: date,
                time: time,

                checked: false
              };

              deliveryList.push(delivery);

              store.dispatch("patchdeliveryPageCount", res.data.last_page);
              store.dispatch("patchdeliveryPageNumber", res.data.current_page);
              store.dispatch("patchtotalDeliveries", res.data.total);
            }
          });

          this.deliveries = deliveryList;
        });

      this.loading = false;
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



