<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size: 28px; cursor: pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-2"></div>
        <div class="col-lg-8 col-md-12 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>Delivery Charge
            Calculator
          </h5>
          <div class="row p-3">
            <div class="col-sm-12">
              <form class="form pb-5 mb-4">
                <!-- address -->
                <div class="form-row">
                  <div class="col-sm-12">
                    <label class="active">Delivery Address</label>
                    <input
                      type="text"
                      v-model="deliveryAddess"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[0]"
                    >{{ errors[0] }}</span>
                  </div>
                </div>

                <!-- pincode -->
                <div class="form-row">
                  <div class="col-sm-12">
                    <label class="active">Pincode</label>
                    <input
                      type="text"
                      v-model="pincode"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[0]"
                    >{{ errors[0] }}</span>
                  </div>
                </div>

                <!-- products -->
                <div class="form-row">
                  <div class="col-sm-12">
                    <label class="active">Products</label>
                    <input
                      type="text"
                      v-model="products"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                  </div>
                </div>

                <!-- qty -->
                <div class="form-row">
                  <div class="col-sm-12">
                    <label class="active">Quantity</label>
                    <input
                      type="text"
                      v-model="quantity"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[0]"
                    >{{ errors[0] }}</span>
                  </div>
                </div>

                <!-- buttons -->
                <div class="form-row">
                  <div class="col text-center">
                    <button
                      class="btn btn-sm btn-go-green font-weight-bold d-inline"
                      type="submit"
                      @click.prevent="fetchData"
                      :disabled="disable"
                    >
                      <i class="fas fa-save pr-1"></i>
                      Check
                    </button>
                    <router-link to="#" class="btn btn-sm btn-secondary">
                      <i class="fas fa-ban pr-1"></i>
                      <div class="font-weight-bold d-inline">CANCEL</div>
                    </router-link>
                  </div>
                </div>

                <!-- dc -->
                <div class="row" v-if="orderDetails">
                  <div class="col-sm-12">
                    <h5 class="active">Order Data</h5>
                  </div>
                </div>
                <div class="row" v-if="orderDetails">
                  <div class="col-sm-6">
                    <span>Distance: {{ orderDetails.distance }} km</span>
                    <br />
                    <span>Product Total: ₹{{ orderDetails.discountedPrice }}</span>
                    <br />
                    <span>
                      Total Delivery Charge: ₹
                      {{ orderDetails.deliveryCharges }}
                    </span>
                    <br />
                    <span>
                      Total Pay: ₹
                      {{
                      orderDetails.discountedPrice +
                      orderDetails.deliveryCharges
                      }}
                    </span>
                  </div>
                  <div class="col-sm-6">
                    <span class="float-right">
                      Delivery Boy Charge: ₹
                      {{ orderDetails.deliveryBoyCharge }}
                    </span>
                    <br />
                    <span class="float-right">
                      Distance Charge: ₹
                      {{ orderDetails.distanceCharge }}
                    </span>
                    <br />
                    <span class="float-right">
                      Over Weight Charge: ₹
                      {{ orderDetails.overWeightCharge }}
                    </span>
                    <br />
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="col-lg-2"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import firebase from "firebase/app";
import axios from "axios";

export default {
  name: "DeliveryChargeCalculator",

  data() {
    return {
      quantity: "1,1,1,1,1,1,1,1,1",
      products:
        "GRO-WHE-ATT-AWWA1-21052137, PROSZS-ZEE-792019-32715717, GRO-PUL-CHA-BC1K-64165533, PRO9HK-ASH-132020-00005106, GRO-MAS-REA-ECM1G-65210071, GRO-MAS-POW-ETCP-64002620, GRO-MAS-POW-ECPJ1G-33606612, GRO-MAS-POW-ECPD2G-24372640, GRO-MAS-POW-EBPPG1G-72732154",
      deliveryAddess: "Mahtopara road, Jugsalai, Jamshedpur,  Jharkhand",
      orderDetails: null,
      pincode: "831006",
      disable: false,
      success: false,
      message: null,
      errors: []
    };
  },

  methods: {
    fetchData() {
      let url = domain + "test/order/checkout";
      this.disable = true;
      axios
        .get(url, {
          params: {
            quantity: this.quantity,
            products: this.products,
            loc: this.deliveryAddess,
            pincode: this.pincode
          },
          headers: {
            "X-api-token": firebase.auth().currentUser.uid
          }
        })
        .then(res => {
          this.orderDetails = res.data;
          //// to show
          this.message = "Success";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        })
        .catch(err => {
          //// to show
          this.message = "Data Check Failed";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    }
  }
};
</script>



