import {
    domain,
} from "@/store/variable";
import axios from "axios";

export const inventoryDataMixin = {
    data() {
        return {
            category: null,
            subcategory: null,
            cocategory: null,
            categories: [],
            subcategories: [],
            cocategories: [],
            ModalContentLoading: false

        }
    },



    methods: {
        getCoCategories() {
            this.ModalContentLoading = true;
            this.cocategories = [];
            let url = domain + "cocat";

            if (this.subcategories[this.subcategory].active == "0") {
                this.active = false;
            }
            axios
                .get(url, {
                    params: {
                        coid: this.subcategories[this.subcategory].coid
                    }
                })
                .then(res => {
                    if (res.data) {
                        res.data.forEach(doc => {
                            const cocat = {
                                name: doc.name,
                                itemId: doc.item_id,
                                coid: doc.coid,
                                serialNum: doc.serialNum,
                                priceBandValue: doc.priceBandValue,
                                active: doc.active,
                            };
                            this.cocategories.push(cocat);
                        });
                    }
                })
                .catch(err => {
                    console.log(err);
                });
            this.ModalContentLoading = false;
        },

        getSubCategories() {
            this.ModalContentLoading = true;
            this.subcategories = [];
            this.cocategories = [];
            let url = domain + "subcat";

            if (this.categories[this.category].active == "0") {
                this.active = false;
            }

            axios
                .get(url, {
                    params: {
                        subcatid: this.categories[this.category].subcatid
                    }
                })
                .then(res => {
                    res.data.forEach(doc => {
                        const subcat = {
                            name: doc.name,
                            coid: doc.coid,
                            active: doc.active
                        };
                        this.subcategories.push(subcat);
                    });
                })
                .catch(err => {
                    console.log(err);
                });
            this.ModalContentLoading = false;
        },

        fetchCategories() {
            let url = domain + "cat";
            let categories = [];
            this.categories = [];
            axios
                .get(url)
                .then(res => {
                    res.data.forEach(doc => {
                        const cat = {
                            name: doc.name,
                            subcatid: doc.subcatcode,
                            active: doc.active
                        };
                        categories.push(cat);
                    });
                    this.categories = categories;
                })
                .catch(err => {
                    console.log(err);
                });
        },

        setCocategory() {
            if (this.cocategories[this.cocategory].active == "0") {
                this.active = false;
            }
            this.itemId = this.cocategories[this.cocategory].itemId;
            this.priceBand = this.cocategories[this.cocategory].priceBandValue;
        },


    },
}



