              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-else-if="order.orderStatus.id == 2"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-processing pl-2 pr-2"
                v-else-if="order.orderStatus.id == 4"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-packed pl-2 pr-2"
                v-else-if="order.orderStatus.id == 5"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-shipping pl-2 pr-2"
                v-else-if="order.orderStatus.id == 6"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-delivered pl-2 pr-2"
                v-else-if="order.orderStatus.id == 7"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-failed pl-2 pr-2"
                v-else-if="order.orderStatus.id == 8"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-request-return pl-2 pr-2"
                v-else-if="order.orderStatus.id == 9"
              >{{ order.orderStatus.status }}</span>
              <span
                class="badge badge-return pl-2 pr-2"
