<script>
import firebase from "firebase";
import Vue from "vue";
import { bus } from "@/main";
import OrderFooter from "./OrderFooter.vue";
import {
  domain,
  utcHours,
  utcMins,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import { Datetime } from "vue-datetime";
import store from "@/store/store";
import axios from "axios";

export default {
  name: "DashBoard",
  components: {
    OrderFooter,
    datetime: Datetime
  },
  props: {
    status: Number,
    newOrders: Array,
    disable: Boolean
  },

  data() {
    return {
      orderStatus: [],
      search: null,

      select: false,
      action: null,

      message: null,
      success: null,
      loading: this.disable,

      paymentModes: [],
      refundTransactionId: null,
      refundAmount: 0,
      refundPaymentMode: 7,
      refundTransactionTime: null,
      refundOrder: null,
      refundIndex: 0
    };
  },

  computed: {
    orders() {
      return this.newOrders;
    }
  },

  created() {
    this.getOrderStatus();

    bus.$on("paginateOrder", data => {
      this.paginateOrder(data["page"]);
    });
  },

  methods: {
    showModal() {
      this.isRefundModalVisible = true;
    },
    closeModal() {
      this.isRefundModalVisible = false;
    },

    copyProductId(id) {
      let prodID = "#" + id;
      let idToCopy = document.querySelector(prodID);
      idToCopy.setAttribute("type", "text");
      idToCopy.select();

      try {
        let successful = document.execCommand("copy");
        let message = successful ? "Copied!" : "Unable to Copy!";
        alert(message);
      } catch (err) {
        alert("Whoops! unable to copy");
      }

      idToCopy.setAttribute("type", "hidden");
      window.getSelection().removeAllRanges();
    },

    updateAction() {
      if (this.user.designation == "admin") {
        this.orders.forEach((order, index) => {
          if (order.checked) {
            if (this.action == "delete") {
              if (confirm("You are about to delete " + order.orderId)) {
                this.loading = true;
                let url = domain + "order/" + order.orderId;
                axios
                  .post(
                    url,
                    {
                      _method: "DELETE"
                    },
                    {
                      withCredentials: true,
                      headers: {
                        "Content-Type": "application/json",
                        "X-Requested-With": "XMLHttpRequest",
                        "X-api-token": firebase.auth().currentUser.uid
                      }
                    }
                  )
                  .then(res => {
                    if (res.data) {
                      this.message = "Order successfully deleted";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      this.orders.splice(index, 1);

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    }
                  })
                  .catch(err => {
                    this.success = true;
                    this.loading = false;
                    this.action = null;

                    setTimeout(() => {
                      this.success = false;
                      this.message = null;
                    }, 2000);
                  });
              }
            } else if (this.action == "d5") {
              var action = 3;
              let url = domain + "delivery";
              this.loading = true;
              axios
                .get(url, {
                  params: {
                    orderId: order.orderId
                  }
                })
                .then(res => {
                  let url = domain + "delivery/" + res.data[0].id;
                  axios
                    .post(
                      url,
                      {
                        _method: "PUT",
                        status: action
                      },
                      {
                        withCredentials: true,
                        headers: {
                          "Content-Type": "application/json",
                          "X-Requested-With": "XMLHttpRequest",
                          "X-api-token": firebase.auth().currentUser.uid
                        }
                      }
                    )
                    .then(res => {
                      this.message = "Successfully Updated and moved";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      //remove that order after save
                      if (res.data == "Success") {
                        this.orders.splice(index, 1);
                      }

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    })
                    .catch(err => {
                      this.message = "Some error occured";
                      this.success = true;
                      this.loading = false;
                      this.action = null;

                      setTimeout(() => {
                        this.success = false;
                        this.message = null;
                      }, 2000);
                    });
                });
            } else if (this.action == "1" || this.action == "2") {
              this.loading = true;
              let url = domain + "order/" + order.orderId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    reason: "Done from webapp",
                    preferredPaymentMode: 1,
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": ContentType,
                      "X-Requested-With": XRequestedWith,
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.message = "Successfully Updated and moved";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  //remove that order after save
                  if (res.data == "Success") {
                    this.orders.splice(index, 1);
                  }

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            } else {
              this.loading = true;
              let url = domain + "order/" + order.orderId;
              axios
                .post(
                  url,
                  {
                    _method: "PUT",
                    status: this.action
                  },
                  {
                    withCredentials: true,
                    headers: {
                      "Content-Type": "application/json",
                      "X-Requested-With": "XMLHttpRequest",
                      "X-api-token": firebase.auth().currentUser.uid
                    }
                  }
                )
                .then(res => {
                  this.message = "Successfully Updated and moved";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  if (res.data == "Success") {
                    this.orders.splice(index, 1);
                  }

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Some error occured";
                  this.success = true;
                  this.loading = false;
                  this.action = null;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
            }
          }
        });
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    getOrderStatus() {
      let url = domain + "order/status";
      axios
        .get(url)
        .then(res => {
          this.orderStatus = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },

    paginateOrder(page) {
      this.$emit("fetchOrders", page);
    },

    refund(index) {
      this.refundIndex = index;
      this.refundOrder = this.orders[index];
      if (this.refundOrder.orderBack) {
        this.refundPaymentMode = this.refundOrder.orderBack.preferredPaymentMode;
      } else {
        this.refundPaymentMode = 7;
      }
      this.paymentModes = [];
      let url = domain + "payment/mode";
      axios
        .get(url, {
          headers: {
            "X-api-token": "itsaMatch"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.paymentModes.push(doc);
          });
        })
        .catch(err => {
          console.log("Payment Modes can't be fetched");
        });
    },

    refundingOrder() {
      if (this.user.designation == "admin" && this.refundAmount > 0) {
        this.loading = true;
        let url = domain + "order/" + this.refundOrder.orderId;
        axios
          .post(
            url,
            {
              _method: "PUT",
              refund: "yes",
              refundAmount: this.refundAmount,
              refundPaymentMode: this.refundPaymentMode,
              refundTransactionId: this.refundTransactionId,
              refundTransactionTime: this.refundTransactionTime
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": "application/json",
                "X-Requested-With": "XMLHttpRequest",
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data == "Success") {
              this.message = "Success";
              this.refundOrder = null;
              this.order[this.refundIndex].refund = {
                amount: this.refundAmount
              };
            } else {
              this.message = "Some Error Occured";
            }
            this.success = true;
            this.loading = false;

            var status = {};

            //colsing inputs
            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.loading = false;
            console.log(err.message);

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    searching() {
      if (this.search && this.search.toString().length == 10) {
        let url = domain + "order";
        let userUrl = domain + "user";

        this.loading = true;
        axios
          .get(userUrl, {
            params: {
              mobileNumber: this.search
            }
          })
          .then(user => {
            if (user.data[0] && user.data[0].uuid) {
              let ordersList = [];

              if (this.status) {
                axios
                  .get(url, {
                    params: {
                      status: this.status,
                      userId: user.data[0].uuid,
                      user: "budhbak",
                      products: "budhbak",
                      payment_mode: "budhbak",
                      userAddress: "budhbak",
                      order: "updated_at",
                      orderWay: "desc"
                    }
                  })
                  .then(res => {
                    res.data.forEach(doc => {
                      let dateTime = new Date(doc.orderTime);
                      dateTime.setHours(dateTime.getHours() + utcHours);
                      dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                      let date =
                        dateTime.getDate() +
                        "/" +
                        dateTime.getMonth() +
                        "/" +
                        dateTime.getFullYear();
                      var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                      var hours = dateTime.getHours() % 12 || 12;
                      let time =
                        hours +
                        ":" +
                        dateTime.getMinutes() +
                        ":" +
                        dateTime.getSeconds() +
                        " " +
                        AmOrPm;

                      let order = {
                        orderId: doc.id,
                        products: doc.products,
                        orderedItemsCount: doc.orderedItemsCount,
                        paymentMode: doc.payment_mode,
                        orderDate: date,
                        orderTime: time,
                        payment: doc.totalPayableAmount,
                        paidAmount: doc.paidAmount,
                        orderStatus: doc.order_status,
                        user: doc.user,
                        deliveryAddress: doc.user_address,
                        refundId: doc.refundId,
                        checked: false
                      };
                      ordersList.push(order);

                      store.dispatch("patchorderPageCount", 1);
                      store.dispatch("patchorderPageNumber", 1);
                      store.dispatch("patchtotalOrders", res.data.total);
                    });
                  });
              } else {
                axios
                  .get(url, {
                    params: {
                      userId: user.data[0].uuid,
                      user: "budhbak",
                      products: "budhbak",
                      payment_mode: "budhbak",
                      userAddress: "budhbak",
                      order: "updated_at",
                      orderWay: "desc"
                    }
                  })
                  .then(res => {
                    res.data.forEach(doc => {
                      let dateTime = new Date(doc.orderTime);
                      dateTime.setHours(dateTime.getHours() + utcHours);
                      dateTime.setMinutes(dateTime.getMinutes() + utcMins);
                      let date =
                        dateTime.getDate() +
                        "/" +
                        dateTime.getMonth() +
                        "/" +
                        dateTime.getFullYear();
                      var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                      var hours = dateTime.getHours() % 12 || 12;
                      let time =
                        hours +
                        ":" +
                        dateTime.getMinutes() +
                        ":" +
                        dateTime.getSeconds() +
                        " " +
                        AmOrPm;

                      let order = {
                        orderId: doc.id,
                        products: doc.products,
                        orderedItemsCount: doc.orderedItemsCount,
                        paymentMode: doc.payment_mode,
                        orderDate: date,
                        orderTime: time,
                        payment: doc.totalPayableAmount,
                        paidAmount: doc.paidAmount,
                        orderStatus: doc.order_status,
                        user: doc.user,
                        deliveryAddress: doc.user_address,
                        refundId: doc.refundId,
                        checked: false
                      };
                      ordersList.push(order);

                      store.dispatch("patchorderPageCount", 1);
                      store.dispatch("patchorderPageNumber", 1);
                      store.dispatch("patchtotalOrders", res.data.total);
                    });
                  });
              }

              this.newOrders = ordersList;
              this.loading = false;
            } else {
              this.loading = false;
            }
          });
      }
    },

    selectAll() {
      this.orders.forEach(order => {
        if (!this.select) {
          order.checked = true;
          //Vue.set(order, checked, true);
        } else {
          order.checked = false;
        }
      });
    }
  }
};
</script>



