<template>
  <div id="app">
    <!-- network issue -->
    <div
      id="slow-notice"
      class="alert alert-warning alert-dismissible fade show"
      role="alert"
      v-if="showBackOnline || !onLine"
    >
      <span v-if="!onLine">Trying to reconnect ...</span>
      <span v-if="showBackOnline">Horray!! Internet is Back!!</span>
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <!-- network issue -->
    <Navbar />
    <div class="wrapper">
      <SideBar v-if="user && showSideBar" />
      <div id="content">
        <router-view />
      </div>
      <Setting v-if="overlay == 'overlay active'" />
      <div :class="overlay"></div>
    </div>
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import SideBar from "./components/SideBar.vue";
import Setting from "./components/Setting.vue";
import { bus } from "./main";
import store from "./store/store";
import firebase from "firebase";

export default {
  name: "App",
  components: {
    Navbar,
    SideBar,
    Setting
  },

  data() {
    return {
      overlay: "overlay",
      onLine: navigator.onLine,
      showBackOnline: false
    };
  },

  created() {
    bus.$on("switchClass", data => {
      this.switchClass();
    });

    const messaging = firebase.messaging();

    navigator.serviceWorker
      .register("/static/firebase-messaging-sw.js")
      .then(registration => {
        Vue.prototype.$messaging.useServiceWorker(registration);
      })
      .catch(err => {
        console.log(err);
      });

    messaging
      .requestPermission()
      .then(() => firebase.messaging().getToken())
      .then(token => {
        console.log(token); // Receiver Token to use in the notification
      })
      .catch(function(err) {
        console.log("Unable to get permission to notify.", err);
      });

    messaging.onMessage(function(payload) {
      console.log("Message received. ", payload);
      // ...
    });
  },

  watch: {
    onLine(v) {
      if (v) {
        this.showBackOnline = true;
        setTimeout(() => {
          this.showBackOnline = false;
        }, 1000);
      }
    }
  },

  computed: {
    showSideBar() {
      if (
        this.$route.name != "login" &&
        this.$route.name != "UnAuthorized" &&
        this.$route.name != "invalidUser" &&
        this.$route.name != "underMaintenance"
      ) {
        return true;
      } else {
        return false;
      }
    }
  },

  mounted() {
    window.addEventListener("online", this.connectionCheck);
    window.addEventListener("offline", this.connectionCheck);
  },

  beforeDestroy() {
    window.removeEventListener("online", this.connectionCheck);
    window.removeEventListener("offline", this.connectionCheck);
  },

  methods: {
    connectionCheck(e) {
      const { type } = e;
      this.onLine = type === "online";
    },
    switchClass() {
      if (this.overlay == "overlay") {
        this.overlay = "overlay active";
      } else {
        this.overlay = "overlay";
      }
    }
  }
};
</script>

<style>
.wrapper {
  display: flex;
  width: 100%;
  align-items: stretch;
}

#content {
  width: 100%;
  margin-left: 260px;
}

.overlay {
  display: none;
  position: fixed;
  /* full screen */
  width: 100vw;
  height: 100vh;
  /* transparent black */
  background: rgba(0, 0, 0, 0.7);
  /* middle layer, i.e. appears below the sidebar */
  z-index: 998;
  opacity: 0;
  /* animate the transition */
  transition: all 0.5s ease-in-out;
}

/* display .overlay when it has the .active class */
.overlay.active {
  display: block;
  opacity: 1;
}

#dismiss {
  width: 35px;
  height: 35px;
  position: absolute;
  /* top right corner of the sidebar */
  top: 10px;
  right: 10px;
}

#slow-notice {
  width: 300px;
  position: fixed;
  top: 10px;
  left: 50%;
  margin-left: -160px;
  z-index: 1050;
  border-radius: 0.5em;
}

@media (max-width: 768px) {
  #content {
    margin-left: 0;
  }
}
</style>

<style src="@/assets/css/doorstep.css" scoped></style>



