<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4 pl-4 pr-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- header -->
            <div class="row mt-4 mb-4">
              <div class="col-6 content-header">
                <div class="pl-4">
                  <i class="fas fa-users"></i>
                  <h5 class="pl-2 d-inline-block">
                    <strong>Users</strong>
                  </h5>
                </div>
              </div>
              <div class="col-6 content-header small">
                <!-- <span class="float-right">
                  <router-link to="/" class="cursor-pointer" tag="span">dashboard</router-link>>
                  <router-link to="/order/dashboard" class="cursor-pointer" tag="span">users</router-link>>
                  <router-link to="/order/confirm" class="cursor-pointer" tag="span">all</router-link>
                </span>-->
              </div>
            </div>
            <hr class="pl-4 pr-4" />
            <!-- content -->
            <div class="table-responsive pt-4">
              <table class="table table-hover table-sm table-content text-left">
                <thead>
                  <tr>
                    <th class="border-top-0">S.No.</th>
                    <th class="border-top-0">Phone Number</th>
                    <th class="border-top-0">Name</th>
                    <th class="border-top-0">E-Mail</th>
                    <th class="border-top-0">Total Addresses</th>
                    <th class="border-top-0">Total Orders</th>
                    <th class="border-top-0">Created At</th>
                    <th class="border-top-0">Action</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(user, index) in users" :key="user.uuid">
                    <td>{{ index + 1 }}</td>
                    <td>{{ user.mobileNumber }}</td>
                    <td>
                      <span v-if="user.name">{{ user.name }}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <span v-if="user.email">{{ user.email }}</span>
                      <span v-else>N/A</span>
                    </td>
                    <td>
                      <div
                        data-toggle="modal"
                        data-target="#addressModal"
                        @click="showAddresses(index)"
                      >
                        <a v-if="user.addresses.length > 0">
                          {{
                          user.addresses.length
                          }}
                        </a>
                        <a v-else>0</a>
                      </div>
                    </td>
                    <td>
                      <span v-if="user.totalOrders > 0">
                        {{
                        user.totalOrders
                        }}
                      </span>
                      <span v-else>0</span>
                    </td>
                    <td>
                      {{ user.date }}
                      <small>{{ user.time }}</small>
                    </td>
                    <td>
                      <a
                        href="#"
                        v-if="user.status == 'active'"
                        @click.prevent="banUser(index, 'banned')"
                      >
                        <i class="fa fa-ban pr-2" aria-hidden="true"></i>
                      </a>
                      <a
                        href="#"
                        v-if="user.status == 'banned'"
                        @click.prevent="banUser(index, 'active')"
                      >
                        <i class="fa fa-street-view pr-2" aria-hidden="true"></i>
                      </a>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>

            <!-- address modal  -->

            <div
              class="modal fade"
              id="addressModal"
              tabindex="-1"
              role="dialog"
              aria-labelledby="addressModalLabel"
              aria-hidden="true"
              v-if="addresses"
            >
              <div class="modal-dialog" role="document">
                <div class="modal-content">
                  <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">Addresses</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                      <span aria-hidden="true">&times;</span>
                    </button>
                  </div>
                  <div class="modal-body">
                    <div class="pt-4">
                      <table
                        class="table table-hover table-sm table-borderless table-content text-left"
                      >
                        <thead>
                          <tr>
                            <th></th>
                            <th>S.No.</th>
                            <th>Phone Number</th>
                            <th>Name</th>
                            <th>Address Name</th>
                            <th>H/S.No</th>
                            <th>Street</th>
                            <th>City</th>
                            <th>State</th>
                            <th>LandMark</th>
                            <th>Pincode</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(address, index) in addresses" :key="address.id">
                            <td>
                              <i
                                class="fas fa-circle text-success"
                                aria-hidden="true"
                                v-if="address.active == 1"
                              ></i>
                              <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                            </td>
                            <td>{{ index + 1 }}</td>
                            <td>{{ address.mobileNumber }}</td>
                            <td>
                              <span v-if="address.name">
                                {{
                                address.name
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.addressName">
                                {{
                                address.addressName
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.houseOfficeNo">
                                {{
                                address.houseOfficeNo
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.street">
                                {{
                                address.street
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.city">
                                {{
                                address.city
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.state">
                                {{
                                address.state
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.landmark">
                                {{
                                address.landmark
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                            <td>
                              <span v-if="address.pincode">
                                {{
                                address.pincode
                                }}
                              </span>
                              <span v-else>N/A</span>
                            </td>
                          </tr>
                        </tbody>
                      </table>
                    </div>
                  </div>
                  <div class="modal-footer"></div>
                </div>
              </div>
            </div>
            <!-- address modal ends -->

            <UserFooter />
            <Loader :disable="loading" :success="success" :message="message" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import Vue from "vue";
import { bus } from "@/main";
import UserFooter from "./Footer.vue";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith,
  utcHours,
  utcMins
} from "@/store/variable";
import store from "@/store/store";
import axios from "axios";

export default {
  name: "DashBoard",
  components: {
    UserFooter
  },

  data() {
    return {
      users: [],
      addresses: [],
      message: null,
      success: null,
      loading: this.disable
    };
  },

  computed: {
    size() {
      return store.state.userPageSize;
    }
  },

  created() {
    bus.$on("paginateUser", data => {
      this.fetchUsers(data["page"]);
    });
  },

  mounted() {
    this.fetchUsers();
  },

  methods: {
    banUser(index, status) {
      if (this.user.designation == "admin") {
        this.loading = true;
        let url = domain + "user/" + this.users[index].uuid;
        axios
          .post(
            url,
            {
              _method: "put",
              status: status
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": "application/json",
                "X-Requested-With": "XMLHttpRequest",
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            // updating data
            if (res.data == "Success") {
              Vue.set(this.users[index], "status", status);
            }
            this.loading = false;
          })
          .catch(err => {
            console.log(err.message);
          });
      }
    },

    totalAddresses(uuid) {
      let url = domain + "user/address";
      return axios
        .get(url, {
          params: {
            userId: uuid
          }
        })
        .then(res => {
          return res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },

    countOrders(uuid) {
      let url = domain + "order";
      return axios
        .get(url, {
          params: {
            userId: uuid,
            count: "with"
          }
        })
        .then(res => {
          return res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },

    updateAction() {
      if (this.user.designation == "admin") {
        this.loading = true;
        let url = domain + "order/" + order.orderId;
        axios
          .post(
            url,
            {
              _method: "PUT",
              status: this.action
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": "application/json",
                "X-Requested-With": "XMLHttpRequest",
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            this.message = res.data;
            this.success = true;
            this.loading = false;
            this.action = null;

            var status = {};
            this.orderStatus.forEach(state => {
              if (state.id == this.action) {
                status = state;
              }
            });

            //colsing inputs
            Vue.set(this.orders[index], "orderStatus", status);

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          })
          .catch(err => {
            this.message = "Some error occured";
            this.success = true;
            this.loading = false;
            this.action = null;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      } else {
        confirm("You are not allowed to make changes");
      }
    },

    fetchUsers(page) {
      let url = domain + "user";
      if (!page) {
        page = 1;
      }
      this.disable = true;
      axios
        .get(url, {
          params: {
            designation: "user",
            order: "mobileNumber",
            orderWay: "asc",
            page: page,
            limit: this.size
          }
        })
        .then(res => {
          let userList = [];
          res.data.data.forEach(async doc => {
            let dateTime = new Date(doc.created_at);
            dateTime.setHours(dateTime.getHours() + utcHours);
            dateTime.setMinutes(dateTime.getMinutes() + utcMins);
            let date =
              dateTime.getDate() +
              "/" +
              dateTime.getMonth() +
              "/" +
              dateTime.getFullYear();
            var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
            var hours = dateTime.getHours() % 12 || 12;
            let time =
              hours +
              ":" +
              dateTime.getMinutes() +
              ":" +
              dateTime.getSeconds() +
              " " +
              AmOrPm;

            let totalOrders = await this.countOrders(doc.uuid);
            let totalAddresses = await this.totalAddresses(doc.uuid);

            let user = {
              uuid: doc.uuid,
              name: doc.name,
              mobileNumber: doc.mobileNumber,
              email: doc.email,
              totalOrders: totalOrders,
              addresses: totalAddresses,
              status: doc.status,
              date: date,
              time: time
            };
            userList.push(user);

            store.dispatch("patchuserPageCount", res.data.last_page);
            store.dispatch("patchuserPageNumber", res.data.current_page);
            store.dispatch("patchtotalUsers", res.data.total);
          });

          store.dispatch("patchuserPageCount", res.data.last_page);
          store.dispatch("patchuserPageNumber", res.data.current_page);
          store.dispatch("patchtotalUsers", res.data.total);

          this.users = userList;
          this.disable = false;
        });
    },

    showAddresses(index) {
      this.addresses = [];
      this.users[index].addresses.forEach(doc => {
        this.addresses.push(doc);
      });
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



