<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size: 28px; cursor: pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>ADD LOCATION
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields
                    marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Pincode</label>
                        <input
                          type="text"
                          v-model="pincode"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[1]"
                        >{{ errors[1] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Area Name</label>
                        <input
                          type="text"
                          v-model="areaName"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{ errors[2] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>
                          Distance
                          <small>(in km)</small>
                        </label>
                        <input
                          type="number"
                          v-model.number="distance"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[3]"
                        >{{ errors[3] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click.prevent="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <router-link to="/inventory" class="btn btn-sm btn-secondary">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </router-link>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[0]">
                      <div class="col text-center text-danger">{{ errors[0] }}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 card shadow p-0 ml-2">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-list pl-1 pr-3"></i>LOCATIONS
          </h5>
          <div class="row">
            <div
              class="col-sm-12"
              style="
                display: block;
                height: 75vh;
                max-height: 75vh;
                overflow-y: auto;
              "
            >
              <div class="row p-3">
                <div class="col-sm-12">
                  <table>
                    <thead>
                      <tr>
                        <th>Pincode</th>
                        <th>Area Name</th>
                        <th>Distance(in km)</th>
                      </tr>
                    </thead>
                    <tbody v-if="locations.length > 0">
                      <tr v-for="location in locations" :key="location.id">
                        <td class="text-center">{{ location.pincode }}</td>
                        <td>{{ location.areaName }}</td>
                        <td>{{ location.distance }}</td>
                      </tr>
                    </tbody>
                    <tbody v-else>
                      <td colspan="6" class="text-center">No Location Available</td>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import db from "@/firebaseInit";
import firebase from "firebase";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import { firestorage } from "@/firebaseInit";
import axios from "axios";

export default {
  name: "DeliveryLocation",
  data() {
    return {
      pincode: null,
      locationId: null,
      distance: null,
      areaName: null,
      locations: [],

      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableMessage: null,
      success: false
    };
  },

  created() {
    this.fetchPincodes();
  },

  methods: {
    add() {
      if (this.validate()) {
        this.disable = true;
        let url = domain + "delivery/location";

        let flag = true;

        if (flag == true) {
          if (this.edit == false) {
            axios
              .post(
                url,
                {
                  pincode: this.pincode,
                  areaName: this.areaName,
                  distance: this.distance
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                let data = {
                  pincode: this.pincode,
                  areaName: this.areaName,
                  distance: this.distance
                };
                this.locations.unshift(data);

                this.pincode = null;
                this.areaName = null;
                this.distance = null;

                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                //// to show
                this.message = "Data Upload Failed";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
                console.log(err);
              });
          } else {
            url = domain + "delivery/location/" + this.locationId;
            axios
              .post(
                url,
                {
                  _method: "PUT",
                  pincode: this.pincode,
                  areaName: this.areaName,
                  distance: this.distance
                },
                {
                  withCredentials: true,
                  headers: {
                    "Content-Type": ContentType,
                    "X-Requested-With": XRequestedWith,
                    "X-api-token": firebase.auth().currentUser.uid
                  }
                }
              )
              .then(res => {
                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                });

                this.edit = false;
              })
              .catch(err => {
                /// to show
                this.message = "Data Upload In MYSql Failed";
                this.success = true;
                this.disable = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          }
        } else {
          this.disable = false;
        }
      }
    },

    editLocation(catIndex, subIndex, index) {
      if (this.locations) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.locationId = this.locations[index].id;
        this.pincode = this.locations[index].pincode;
        this.areaName = this.locations[index].coid;
        this.distance = this.locations[index].distance;
      }
    },

    fetchPincodes() {
      let url = domain + "delivery/location";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            this.locations.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    validate() {
      this.errors = [];
      if (this.pincode && this.areaName && this.distance) {
        return true;
      } else {
        if (!this.pincode) {
          Vue.set(this.errors, 1, "Fill valid pincode");
        }
        if (!this.areaName) {
          Vue.set(this.errors, 2, "Fill area");
        }
        if (!this.distance) {
          Vue.set(this.errors, 3, "Distance Please");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>



