<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>
            <span v-if="edit">EDIT IMAGE</span>
            <span v-else>ADD NEW IMAGE</span>
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields
                    marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <!-- image -->
                    <div class="form-row">
                      <div class="col-sm-12 text-center">
                        <div class="switch">
                          <label>
                            Add Path *
                            <input
                              type="checkbox"
                              v-model="imageOption"
                              @click="media = null"
                              :disables="disable"
                            />
                            <span class="lever"></span>
                            Upload
                          </label>
                        </div>

                        <div class="col-12" v-if="imageOption == false">
                          <label>Image Path</label>
                          <input
                            type="text"
                            v-model="image"
                            :disabled="disable"
                            class="form-control form-control-sm"
                          />
                          <span class="col text-center text-danger" v-if="errors[2]">{{ errors[2] }}</span>
                        </div>

                        <div class="col-12 text-center" v-else>
                          <label class="btn btn-sm btn-info text-center" style="color:#fff">
                            <i class="fas fa-image pr-2"></i>
                            <div class="d-inline font-weight-bold">ADD IMAGE</div>
                            <input type="file" class="file" @change="change('image')" ref="image" />
                          </label>
                          <img :src="media" class="img-fluid" v-if="media" />
                          <img :src="image" class="img-fluid" v-else-if="image" />
                        </div>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Type</label>
                        <input
                          type="text"
                          v-model="type"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                          @blur.prevent="checkPriority()"
                        />
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Position in App</label>
                        <input
                          type="number"
                          v-model.number="imagePriority"
                          class="form-control form-control-sm"
                          min="0"
                          required
                          :disabled="disable"
                        />
                        <!-- @blur.prevent="checkPriority()" -->
                        <span class="col text-center text-danger" v-if="errors[3]">{{ errors[3] }}</span>
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-6">
                        <label>
                          Active
                          <input
                            type="checkbox"
                            v-model="active"
                            :disables="disable"
                            true-value="1"
                            false-value="0"
                          />
                        </label>
                      </div>
                    </div>

                    <div class="form-row" v-if="type == 'slider'">
                      <div class="col-sm-12">
                        <label>Additional Info</label>
                      </div>
                    </div>

                    <div class="form-row" v-if="type == 'slider'">
                      <div class="col-sm-6">
                        <label>
                          Slider Type
                          <select
                            v-model="additionalInfo['type']"
                            class="form-control form-control-sm"
                          >
                            <option value="5">
                              Type - Default
                              <small>(Will not open anything on click)</small>
                            </option>
                            <option value="1">
                              Type 1 - Image Click
                              <small>
                                (Image click is a static image click that will
                                take you to a long image defining
                                something)
                              </small>
                            </option>
                            <option value="2">
                              Type 2 - Product
                              <small>
                                (Product click will take you to certain product
                                where you have to add specific details about
                                product)
                              </small>
                            </option>
                            <option value="3">
                              Type 3 - Cocategory
                              <small>
                                (Co category click will take you to certain co
                                category)
                              </small>
                            </option>
                          </select>
                        </label>
                      </div>

                      <!-- image -->
                      <div class="col-sm-6 text-center" v-if="additionalInfo['type'] == 1">
                        <div class="switch">
                          <label>
                            Add Path *
                            <input
                              type="checkbox"
                              v-model="addImageOption"
                              @click="addMedia = null"
                              :disables="disable"
                            />
                            <span class="lever"></span>
                            Upload
                          </label>
                        </div>

                        <div class="col-12" v-if="addImageOption == false">
                          <label>Image Path</label>
                          <input
                            type="text"
                            v-model="additionalInfo['image']"
                            :disabled="disable"
                            class="form-control form-control-sm"
                          />
                          <!-- add validtions for add info -->
                          <!-- <span
                          class="col text-center text-danger"
                          v-if="errors[2]"
                        >
                          {{ errors[2] }}
                          </span>-->
                        </div>

                        <div class="col-12 text-center" v-else>
                          <label class="btn btn-sm btn-info text-center" style="color:#fff">
                            <i class="fas fa-image pr-2"></i>
                            <div class="d-inline font-weight-bold">ADD IMAGE</div>
                            <input
                              type="file"
                              class="file"
                              @change="change('addImage')"
                              ref="addImage"
                            />
                          </label>
                          <img :src="addMedia" class="img-fluid" v-if="addMedia" />
                          <img
                            :src="additionalInfo['imgUrl']"
                            class="img-fluid"
                            v-else-if="additionalInfo['imgUrl']"
                          />
                        </div>
                      </div>

                      <!-- product ID -->
                      <div class="col-sm-6" v-if="additionalInfo['type'] == 2">
                        <label>
                          Product Id
                          <input
                            type="text"
                            v-model="additionalInfo['productId']"
                            class="form-control form-control-sm"
                            required
                            :disabled="disable"
                          />
                        </label>
                      </div>

                      <!-- cocat -->
                      <div class="col-sm-6" v-if="additionalInfo['type'] == 3">
                        <label>CoId*</label>
                        <span
                          v-if="this.cocategory >= 0 && this.cocategory != null"
                        >{{ this.cocategories[this.cocategory].name }}</span>
                        <span v-else>{{additionalInfo['title']}}</span>
                        <br />
                        <button
                          type="button"
                          class="btn btn-outline-success btn-sm btn-block font-weight-bold"
                          data-toggle="modal"
                          data-target="#itemCategories"
                          @click="fetchCategories()"
                        >Choose Item Code</button>

                        <!-- cocat -->
                        <div id="itemCategories" class="modal fade" role="dialog">
                          <div class="modal-dialog modal-dialog-scrollable">
                            <!-- Modal content-->
                            <div class="modal-content">
                              <div class="modal-header">
                                <h5 class="modal-title font-weight-bold">
                                  <i class="fas fa-clipboard-list pr-3"></i>Choose Item Id
                                  <small>(Item id is predefined)</small>
                                </h5>
                                <button type="button" class="close" data-dismiss="modal">&times;</button>
                              </div>
                              <div class="modal-body" style="position:relative;">
                                <h4
                                  v-if="ModalContentLoading"
                                  class="loading-content"
                                  style="background: rgba(250,250,250 ,.9);"
                                >Kriypa Dhirya bnaye rkhe...</h4>
                                <div>
                                  <label>Choose Category</label>
                                  <select
                                    v-model="category"
                                    class="form-control form-control-sm"
                                    @change="getSubCategories"
                                  >
                                    <option
                                      v-for="(category, index) in categories"
                                      :key="index"
                                      :value="index"
                                    >{{ category.name }}</option>
                                  </select>
                                </div>
                                <div>
                                  <label>Choose Sub Category</label>
                                  <select
                                    aria-placeholder="This will get filled when you select category"
                                    v-model="subcategory"
                                    class="form-control form-control-sm"
                                    :disabled="
                                      category == null && !ModalContentLoading
                                    "
                                    @change="getCoCategories"
                                    v-if="subcategories"
                                  >
                                    <option disabled="disabled" selected="true">Choose Subcategory</option>
                                    <option
                                      v-for="(subcategory,
                                      index) in subcategories"
                                      :key="index"
                                      :value="index"
                                    >{{ subcategory.name }}</option>
                                  </select>
                                  <span v-else>
                                    There are no SubCategories in this
                                    yet
                                  </span>
                                </div>
                                <div>
                                  <label>Choose Item Id</label>
                                  <select
                                    v-model="cocategory"
                                    class="form-control form-control-sm"
                                    :disabled="
                                      subcategory == null &&
                                        !ModalContentLoading
                                    "
                                    @change="setCocategory()"
                                    v-if="cocategories"
                                  >
                                    <option
                                      v-for="(cocategory,
                                      index) in cocategories"
                                      :key="index"
                                      :value="index"
                                    >
                                      {{ cocategory.itemId }}
                                      {{ cocategory.name }}
                                    </option>
                                  </select>
                                  <span v-else>This have no items yet</span>
                                </div>
                              </div>
                              <div class="modal-footer">
                                <button
                                  type="button"
                                  class="btn btn-danger btn-sm"
                                  data-dismiss="modal"
                                >
                                  <i class="fas fa-times-circle pr-2"></i>Close
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[0]"
                        >{{ errors[0] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <button type="reset" class="btn btn-sm btn-secondary" @click="edit = false">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </button>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{ errors[1] }}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="pl-2">
            <div class="row" style="height: 80vh; max-height: 80vh; overflow-y: auto;">
              <div class="col-12" v-for="(image, index) in images" :key="image.id">
                <small @click="showImageDetail(index)" style="cursor:pointer;">
                  <i
                    class="fas fa-xs fa-circle text-success"
                    aria-hidden="true"
                    v-if="image.active == 1"
                  ></i>
                  <i class="fas fa-xs fa-circle text-danger" aria-hidden="true" v-else></i>
                  {{ image.type }}
                </small>
                <span class="float-right">
                  <span>Position - {{ image.priority }}</span>
                  <a
                    style="color:#424242;"
                    href="#"
                    title="delete this brand"
                    @click.prevent="deleteImage(index)"
                  >
                    <i class="fa fa-sm fa-times"></i>
                  </a>
                </span>
                <div class="brand-image" @click.prevent="editImage(index)">
                  <img :src="image.path" class="img-fluid" v-if="image.path != null" />
                  <img
                    src="../../assets/doorstepLogo.png"
                    class="img-fluid card-img-top bw-greyscale"
                    v-else
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
      <div
        class="alert alert-success"
        role="alert"
        v-if="showDetail >= 0"
        style="    position: fixed;
    top: 70px;
    left: 400px;
    right: 0px;
    width: 400px;
    height:55vh;
    overflow-y: auto;"
      >
        <h5 class="alert-heading">{{ images[showDetail].type.toUpperCase() }} Image</h5>
        <button
          type="button"
          class="close"
          data-dismiss="alert"
          aria-label="Close"
          @click="hideImageDetail()"
        >
          <span aria-hidden="true">&times;</span>
        </button>
        <div class="row">
          <div class="col-sm-6">
            <div>
              Active:
              <i class="fa fa-check" v-if="images[showDetail].active == '1'"></i>
              <i class="fas fa-times-circle" v-else></i>
            </div>
            <div>
              Created At: {{ images[showDetail].date }}
              {{ images[showDetail].time }}
            </div>
            <div>Position: {{ images[showDetail].priority }}</div>
          </div>
          <div class="col-sm-6">
            <img :src="images[showDetail].path" class="img-fluid" />
          </div>
        </div>
        <hr />
        <span>Additional Info</span>
        <div>Type: {{ images[showDetail].additionalInfo.type }}</div>
        <div v-if="images[showDetail].additionalInfo.imgUrl">
          <img :src="images[showDetail].additionalInfo.imgUrl" class="img-fluid" />
        </div>
        <div v-if="images[showDetail].additionalInfo.coid">
          Cocategory:
          {{ images[showDetail].additionalInfo.coid }} -
          {{ images[showDetail].additionalInfo.title }}
        </div>
        <div v-if="images[showDetail].additionalInfo.productId">
          ProductId:
          {{ images[showDetail].additionalInfo.productId }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import db from "@/firebaseInit";
import firebase from "firebase";
import { firestorage } from "@/firebaseInit";
import store from "@/store/store";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import { inventoryDataMixin } from "@/components/Mixins/Inventory/inventoryDataMixin.js";
import axios from "axios";
export default {
  name: "InventoryImages",
  mixins: [inventoryDataMixin],
  data() {
    return {
      showDetail: -1,
      imageId: 0,
      imagePriority: null,
      image: null,
      imageOption: true,
      media: null,
      addImageOption: true,
      addMedia: null,
      priority: 0,
      type: null,
      active: 1,
      images: [],
      additionalInfo: {
        type: 0,
        imgUrl: null,
        productId: null,
        coid: null,
        title: null,
        serialNum: null
      },

      errors: [],
      edit: false,
      message: null,
      disable: false,
      disableId: false,
      disableMessage: null,
      success: false,
      ModalContentLoading: false
    };
  },

  mounted() {
    this.fetchImages();
  },

  methods: {
    async add() {
      if (this.user && this.validate()) {
        this.disable = true;
        this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";
        if (this.media) {
          await this.uploadImage("image").then(downloadURL => {
            this.image = downloadURL;
          });
        }
        if (this.additionalInfo["type"] == 1 && this.addMedia) {
          await this.uploadImage("addImage").then(downloadURL => {
            this.additionalInfo["imgUrl"] = downloadURL;
          });
        }
        if (this.image) {
          if (
            this.additionalInfo["type"] == 1 &&
            !this.additionalInfo["imgUrl"] &&
            this.type == "slider"
          ) {
            alert("Please Add the image to show on click");
            this.disable = false;
          } else {
            this.uploadData();
          }
        }
      }
    },

    change(type) {
      let files = null;
      if (type == "image") {
        files = this.$refs.image.files;
      } else {
        files = this.$refs.addImage.files;
      }

      if (!files.length) {
        return;
      } else {
        this.createImage(files[0], type);
      }
    },

    checkPriority() {
      this.errors[3] = null;
      if (this.type && this.imagePriority) {
        let flag = 0;
        this.images.forEach(image => {
          if (image.priority == this.imagePriority && image.type == this.type) {
            if (this.edit == true && image.id == this.imageId) {
              //
            } else {
              Vue.set(this.errors, 3, "Two images can't have same priority");
              flag = 1;
              return false;
            }
          }
        });
        if (flag == 0) {
          return true;
        }
      }
      return false;
    },

    createImage(file, type) {
      let reader = new FileReader();
      reader.onload = e => {
        if (type == "image") {
          this.media = e.target.result;
        } else {
          this.addMedia = e.target.result;
        }
      };
      reader.readAsDataURL(file);
    },

    deleteImage(index) {
      if (confirm("You are about to delete an image")) {
        this.disable = true;
        let url = domain + "images/" + this.images[index].id;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              this.images.splice(index, 1);
              this.message = "Data Delete Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            } else {
              this.message = "Something Went wrong";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            }
          });
      }
    },

    editImage(index) {
      if (this.images) {
        //it's editing not adding
        this.edit = true;
        //details
        this.imageId = this.images[index].id;
        this.active = this.images[index].active;
        this.type = this.images[index].type;
        this.imagePriority = this.images[index].priority;
        this.image = this.images[index].path;
        this.imageOption = true;
        this.additionalInfo = this.images[index].additionalInfo;
        this.media = null;
      }
    },

    fetchImages() {
      let url = domain + "images";
      axios
        .get(url, {
          headers: {
            from: "webapp"
          }
        })
        .then(res => {
          if (res.data.length) {
            res.data.forEach(doc => {
              let dateTime = this.getDateTime(doc.created_at);
              if (dateTime) {
                let date = dateTime[0];
                let time = dateTime[1];
                doc.date = date;
                doc.time = time;
              } else {
                doc.date = null;
                doc.time = null;
              }
              if (doc.additionalInfo == false) {
                doc.additionalInfo = this.additionalInfo;
              }
              this.images.push(doc);
            });
          }
        });
    },

    showImageDetail(index) {
      this.showDetail = index;
    },

    hideImageDetail() {
      this.showDetail = -1;
    },

    uploadImage(type) {
      if (type == "image") {
        let file = this.$refs.image.files[0];
        this.image = null;
        return firestorage
          .ref("images/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            return null;
          });
      } else {
        let file = this.$refs.addImage.files[0];
        this.additionalInfo["imageUrl"] = null;
        return firestorage
          .ref("images/" + file.name)
          .put(file)
          .then(res => {
            //downloading url
            let mediaURL = res.ref.getDownloadURL();
            return mediaURL;
          })
          .catch(err => {
            return null;
          });
      }
    },

    uploadData() {
      if (this.type == "slider") {
        if (this.additionalInfo["type"] == 2) {
          this.additionalInfo["imgUrl"] = "qwerty";
          this.additionalInfo["coid"] = "qwerty";
          this.additionalInfo["serialNum"] = "qwerty";
          this.additionalInfo["title"] = "qwerty";
        } else if (this.additionalInfo["type"] == 3) {
          this.additionalInfo["imgUrl"] = "qwerty";
          this.additionalInfo["productId"] = "qwerty";
          if (
            this.cocategory >= 0 &&
            (this.additionalInfo["coid"] == undefined ||
              this.additionalInfo["coid"] == null)
          ) {
            this.additionalInfo["coid"] = this.cocategories[
              this.cocategory
            ].coid;
            this.additionalInfo["serialNum"] = this.cocategories[
              this.cocategory
            ].serialNum;
            this.additionalInfo["title"] = this.cocategories[
              this.cocategory
            ].name;
          }
        }
      } else {
        this.additionalInfo = {
          type: 0,
          imgUrl: "qwerty",
          productId: "qwerty",
          coid: "qwerty",
          title: "qwerty",
          serialNum: "qwerty"
        };
      }

      if (this.edit != true) {
        {
          let url = domain + "images";
          axios
            .post(
              url,
              {
                path: this.image,
                type: this.type,
                active: this.active,
                priority: this.imagePriority,
                additionalInfo: {
                  type: this.additionalInfo["type"],
                  imgUrl: this.additionalInfo["imgUrl"],
                  productId: this.additionalInfo["productId"],
                  coid: this.additionalInfo["coid"],
                  title: this.additionalInfo["title"],
                  serialNum: this.additionalInfo["serialNum"]
                }
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              // this.images.unshift(res.data);

              this.fetchImages();

              //// to show

              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              this.message =
                "Maaf kijiye kuch gadbad ho gya, kahi kuch jhol ho gya";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      } else {
        {
          let url = domain + "images/" + this.imageId;
          axios
            .post(
              url,
              {
                _method: "PUT",
                path: this.image,
                type: this.type,
                active: this.active,
                priority: this.imagePriority,
                additionalInfo: {
                  type: this.additionalInfo["type"],
                  imgUrl: this.additionalInfo["imgUrl"],
                  productId: this.additionalInfo["productId"],
                  coid: this.additionalInfo["coid"],
                  title: this.additionalInfo["title"],
                  serialNum: this.additionalInfo["serialNum"]
                }
              },
              {
                withCredentials: true,
                headers: {
                  "Content-Type": ContentType,
                  "X-Requested-With": XRequestedWith,
                  "X-api-token": firebase.auth().currentUser.uid
                }
              }
            )
            .then(res => {
              // this.allcat = null;
              // this.homecat = null;
              // this.subcatcode = null;
              // this.name = null;
              // this.image = null;
              // this.media = null;
              this.images.forEach((doc, index) => {
                if (doc.id == res.data.id) {
                  Vue.set(this.images, index, res.data);
                }
              });
              //// to show

              this.message = "Data Upload Success";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            })
            .catch(err => {
              console.log(err);

              this.message =
                "Maaf kijiye kuch gadbad ho gya, kahi kuch jhol ho gya";
              this.success = true;
              this.disable = false;

              setTimeout(() => {
                this.success = false;
                this.message = null;
              }, 2000);
            });
        }
      }
    },

    validate() {
      this.errors = [];
      //  && this.checkPriority()
      if (this.image || this.media) {
        if (
          this.type == "slider" &&
          !this.additionalInfo &&
          this.imagePriority < 0
        ) {
          Vue.set(this.errors, 1, "Something is missing");
          return false;
        }
        return true;
      } else {
        if (!this.image) {
          Vue.set(this.errors, 2, "Upload Image please");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}

.brand-image {
  cursor: pointer;
}

.bw-greyscale {
  -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
  filter: grayscale(100%);
}
</style>



