<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 mt-4 mb-4">
        <div class="card content border-1 shadow-sm mt-2 fixed-card">
          <div class="card-body">
            <!-- content -->
            <div>
              <!-- header -->
              <div class="row mt-4 mb-4">
                <div class="col-6 content-header">
                  <div class="pl-4">
                    <i class="far fa-list-alt"></i>
                    <h5 class="pl-2 d-inline-block">
                      <strong>User's Carts</strong>
                    </h5>
                  </div>
                </div>
                <div class="col-6">
                  <div class="float-right">
                    <select
                      v-model="filterPeriod"
                      class="custom-select custom-select-sm"
                      @change.prevent="fetchItems(1)"
                    >
                      <option value="0">Today</option>
                      <option value="1">Yesterday</option>
                      <option value="7">Last 7 Days</option>
                      <option value="30">Last 30 Days</option>
                    </select>
                  </div>
                </div>
              </div>
              <hr class="pl-4 pr-4" />
              <div class="table-responsive table-scroll mt-4">
                <table class="table table-hover table-sm table-borderless table-content text-left">
                  <thead>
                    <tr>
                      <th>S. No.</th>
                      <th>Image</th>
                      <th>Name</th>
                      <th>Quantity</th>
                      <th>Cost Price</th>
                      <th>MRP</th>
                      <th>Current Price</th>
                      <th>No of carts</th>
                      <th>Total Qty in carts</th>
                    </tr>
                  </thead>
                  <tbody v-if="carts.length > 0">
                    <tr v-for="(cart, index) in carts" :key="cart.id">
                      <td>{{index+1}}</td>
                      <td>
                        <img :src="cart.image" width="150" class="img-fluid" />
                      </td>
                      <td>{{cart.name}}</td>
                      <td>{{cart.qty}} {{cart.unit}}</td>
                      <td>{{cart.msp}}</td>
                      <td>{{cart.MRP}}</td>
                      <td>{{cart.currentPrice}}</td>
                      <td>{{cart.totalCarts}}</td>
                      <td>{{cart.totalQty}}</td>
                    </tr>
                  </tbody>
                  <tbody v-else>
                    <tr>
                      <td colspan="9" class="text-center">
                        <div>
                          <img
                            class="card-img-top img-fluid"
                            src="https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fmedia.istockphoto.com%2Fvectors%2Fvector-cartoon-of-skeleton-arm-is-sticking-out-of-the-grave-holding-vector-id666480498%3Fk%3D6%26m%3D666480498%26s%3D170667a%26w%3D0%26h%3DRaDgNagcF-bDMgVwRXq9lMMh-D7Wnvj2wzFr6D_iiJM%3D&f=1&nofb=1"
                            alt="Card image cap"
                            style="width:20em"
                          />
                          <div class="card-body">
                            <p class="card-text">Oops! Nothing is searched. Do something!</p>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <Loader :disable="disable" :success="success" :message="message" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- modal -->
    <div id="users" class="modal fade" role="dialog">
      <div class="modal-dialog modal-dialog-scrollable">
        <!-- Modal content-->
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title font-weight-bold">
              <i class="fas fa-clipboard-list pr-3"></i>Users
            </h5>
            <button type="button" class="close" data-dismiss="modal">&times;</button>
          </div>
          <div class="modal-body" style="position:relative;">
            <table class="table table-bordered">
              <thead>
                <th>Sr.no</th>
                <th>Name</th>
                <th>email</th>
                <th>Number</th>
                <th>Quantity</th>
              </thead>
              <tbody>
                <tr v-for="(user, index) in modalUser" :key="user.uuid">
                  <td>{{index+1}}</td>
                  <td>
                    <span v-if="user.name!='Fasdorian'">{{user.name}}</span>
                    <span v-else>N/A</span>
                  </td>
                  <td>
                    <span v-if="user.email">{{user.email}}</span>
                    <span v-else>N/A</span>
                  </td>
                  <td>
                    <span v-if="user.mobileNumber">{{user.mobileNumber}}</span>
                    <span v-else>N/A</span>
                  </td>
                  <td>{{user.carts.length}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-danger btn-sm" data-dismiss="modal">
              <i class="fas fa-times-circle pr-2"></i>Close
            </button>
          </div>
        </div>
      </div>
    </div>
    <Footer @fetchItems="fetchItems" />
  </div>
</template>

<script>
import Footer from "../Footer.vue";
import { domain } from "@/store/variable";
import axios from "axios";
import { bus } from "@/main";
import store from "@/store/store";

export default {
  name: "userCartList",
  components: {
    Footer
  },

  data() {
    return {
      modalUser: {},
      searches: [],
      filterPeriod: 30,
      disable: false,
      success: false,
      message: null
    };
  },

  computed: {
    size() {
      return store.state.inventoryPageSize;
    }
  },

  created() {
    bus.$on("fetchItems", data => {
      this.fetchItems(data["page"]);
    });
  },

  mounted() {
    this.fetchItems();
  },

  methods: {
    fetchItems(page) {
      this.loading = true;
      this.searches = [];
      this.modalUser = {};
      let url = domain + "user/cart";
      if (!page) {
        page = 1;
      }
      axios
        .get(url, {
          params: {
            products: "fetch",
            user: "fetch",
            compare: ["created_at", ">=", this.filterPeriod],
            limit: this.size,
            page: page,
            order: this.orderItems,
            orderWay: this.orderWay
          }
        })
        .then(res => {
          if (res.data.data.length) {
            res.data.data.forEach(doc => {
              let items = [];
              doc.products.forEach(product => {
                product.totalCarts = [];
                product.totalQty = 0;
                let flag = 0;
                this.carts.forEach(item => {
                  console.log(date);
                  item.totalCarts.push(doc.user);
                  item.totalQty = item.totalQty + product.pivot.quantity;
                  flag = 1;
                });
                if (flag == 0) {
                  product.totalCarts = [doc.user];
                  product.totalQty = product.pivot.quantity;

                  this.carts.push(product);
                }
              });
            });
            store.dispatch("patchinventoryPageCount", res.data.last_page);
            store.dispatch("patchinventoryPageNumber", res.data.current_page);
            store.dispatch("patchinventoryTotalItems", res.data.total);
            this.loading = false;
          }
        })
        .catch(err => {
          this.loading = false;
        });
    },

    setModalData(index) {
      this.modalUser = this.searches[index].totalUsersSearched;
    }
  }
};
</script>

<style src="@/assets/css/tableContent.css" scoped></style>



