<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-1"></div>
        <div class="col-lg-5 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>
            <span v-if="edit">EDIT SUBCATEGORY - {{ name }}</span>
            <span v-else>ADD NEW SUBCATEGORY</span>
          </h5>
          <div class="row">
            <div class="col-sm-12">
              <div class="row p-3">
                <div class="col-sm-12">
                  <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                    <i class="fas fa-exclamation-triangle pr-2"></i>Fields
                    marked with ( * ) is important and is to be filled
                  </div>
                  <form class="form pb-5 mb-4">
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>SubcatId*</label>
                        {{ subcatid }}
                        <select
                          v-model="subcatid"
                          class="form-control form-control-sm"
                          :disabled="disable"
                        >
                          <option
                            :value="category.subcatcode"
                            class="font-weight-bold"
                            v-for="category in categories"
                            :key="category.subcatcode"
                          >
                            {{ category.name }} -
                            {{ category.subcatcode }}
                          </option>
                        </select>
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[2]"
                        >{{ errors[2] }}</span>
                      </div>
                    </div>
                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Name</label>
                        <input
                          type="text"
                          v-model="name"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[3]"
                        >{{ errors[3] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Coid</label>
                        <input
                          type="text"
                          v-model="coid"
                          class="form-control form-control-sm"
                          required
                          :disabled="disable || disableId"
                        />
                        <span
                          class="error text-danger font-weight-bold"
                          v-if="errors[4]"
                        >{{ errors[4] }}</span>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Display Offer</label>
                        <input
                          type="number"
                          v-model.number="displayOffer"
                          class="form-control form-control-sm"
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col-sm-12">
                        <label>Active</label>
                        <input
                          type="checkbox"
                          v-model="active"
                          true-value="1"
                          false-value="0"
                          class="form-control-sm align-self-center"
                          required
                          :disabled="disable"
                        />
                      </div>
                    </div>

                    <div class="form-row">
                      <!-- image -->
                      <div class="col-sm-12 text-center">
                        <div class="switch">
                          <label>
                            Add Path *
                            <input
                              type="checkbox"
                              v-model="imageOption"
                              @click="media = null"
                              :disabled="disable"
                            />
                            <span class="lever"></span>
                            Upload
                          </label>
                        </div>

                        <div class="col-12" v-if="imageOption == false">
                          <label>Image Path</label>
                          <input
                            type="text"
                            v-model="image"
                            :disabled="disable"
                            class="form-control form-control-sm"
                          />
                        </div>

                        <div class="col-12 text-center" v-else>
                          <label class="btn btn-sm btn-info text-center" style="color:#fff">
                            <i class="fas fa-image pr-2"></i>
                            <div class="d-inline font-weight-bold">ADD IMAGE</div>
                            <input type="file" class="file" @change="change('image')" ref="image" />
                          </label>
                          <img :src="media" class="img-fluid" v-if="media" />
                          <img :src="image" class="img-fluid" v-else-if="image" />
                        </div>
                      </div>
                    </div>

                    <div class="form-row">
                      <div class="col text-center">
                        <button
                          class="btn btn-sm btn-go-green font-weight-bold d-inline"
                          type="submit"
                          @click.prevent="add"
                          :disabled="disable"
                        >
                          <i class="fas fa-save pr-1"></i>
                          SAVE
                        </button>
                        <router-link to="/inventory" class="btn btn-sm btn-secondary">
                          <i class="fas fa-ban pr-1"></i>
                          <div class="font-weight-bold d-inline">CANCEL</div>
                        </router-link>
                      </div>
                    </div>

                    <div class="form-row" v-if="errors[1]">
                      <div class="col text-center text-danger">{{ errors[1] }}</div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-5 p-0 ml-2">
          <div class="card shadow mb-4" v-if="cocategories.length > 0">
            <h5 class="card-header font-weight-bold" data-toggle="collapse" data-target="#details">
              <i class="fas fa-list pl-1 pr-3"></i>DETAILS
            </h5>
            <span id="details" class="card-body collapse">
              <i class="fas fa-list pl-1 pr-3"></i>Category
              <div class="row">
                <div class="col-sm-12">
                  <div class="row p-3">
                    <div class="col-sm-12">
                      <table>
                        <tr>
                          <th>Subcat Code</th>
                          <th class="pl-2">Name</th>
                        </tr>
                        <tr>
                          <td>{{ category.subcatcode }}</td>
                          <td class="pl-2">{{ category.name }}</td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
              <i class="fas fa-list pl-1 pr-3"></i>
              Co Categories
              <div class="row">
                <div class="col-sm-12">
                  <div class="row p-3">
                    <div class="col-sm-12">
                      <table>
                        <tr>
                          <th>Item Id</th>
                          <th class="pl-2">Name</th>
                        </tr>
                        <tr v-for="(cocategory, index) in cocategories" :key="cocategory.item_id">
                          <td>{{ cocategory.item_id }}</td>
                          <td class="pl-2">{{ cocategory.name }}</td>
                        </tr>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </span>
          </div>

          <div class="card shadow">
            <h5 class="card-header font-weight-bold">
              <i class="fas fa-list pl-1 pr-3"></i>SUB CATEGORIES
            </h5>
            <div class="row">
              <div class="col-sm-12">
                <div class="row p-3">
                  <div class="col-sm-12">
                    <div
                      class="card pb-2 border-0 mb-2"
                      v-for="(category, catIndex) in subcategories"
                      :key="category.subcatcode"
                    >
                      <h5
                        class="card-header font-weight-bold justify-content-center"
                        style="background:#ffffff"
                      >{{ category.name }}</h5>
                      <div>
                        <table>
                          <thead>
                            <tr>
                              <th></th>
                              <th>Subcatcode</th>
                              <th class="pl-2">CoId</th>
                              <th class="pl-2">Name</th>
                              <th class="pl-2">Actions</th>
                            </tr>
                          </thead>
                          <tbody v-if="category.subcategories.length > 0">
                            <tr
                              v-for="(subcategory,
                              index) in category.subcategories"
                              :key="subcategory.coid"
                            >
                              <td>
                                <i
                                  class="fas fa-circle text-success"
                                  aria-hidden="true"
                                  v-if="subcategory.active == 1"
                                ></i>
                                <i class="fas fa-circle text-danger" aria-hidden="true" v-else></i>
                              </td>
                              <td>{{ subcategory.subcatid }}</td>
                              <td>{{ subcategory.coid }}</td>
                              <td class="pl-2">{{ subcategory.name }}</td>
                              <td class="pl-2">
                                <a
                                  style="color:#424242;"
                                  href="#"
                                  title="delete this item"
                                  @click.prevent="deleteCo(catIndex, index)"
                                >
                                  <i class="fa fa-trash"></i>
                                </a>
                                <a
                                  style="color:#424242;"
                                  href="#"
                                  title="delete this item"
                                  @click.prevent="
                                    editSubCategory(catIndex, index)
                                  "
                                >
                                  <i class="fa fa-pencil-alt"></i>
                                </a>
                              </td>
                            </tr>
                          </tbody>
                          <tbody v-else>
                            <td colspan="5" class="text-center">No SubCat Available</td>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-1"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Vue from "vue";
import db from "@/firebaseInit";
import { firestorage } from "@/firebaseInit";
import firebase from "firebase";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import axios from "axios";

export default {
  name: "SubCategory",
  data() {
    return {
      coid: null,
      subcatid: null,
      name: null,
      active: "1",
      displayOffer: 0,

      image: null,
      imageOption: false,
      media: null,

      subcategories: [],
      categories: [],
      category: [],
      cocategories: [],
      errors: [],
      disable: false,
      message: null,
      disableMessage: null,
      success: false,
      edit: false,
      disableId: false
    };
  },

  created() {
    this.fetchSubCategories();
    this.fetchCategories();
  },

  methods: {
    add() {
      this.disable = true;
      this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";

      if (this.media) {
        this.uploadImage()
          .then(downloadURL => {
            this.image = downloadURL;
            return this.image;
          })
          .then(() => {
            this.uploadData();
          });
      } else {
        this.uploadData();
      }
    },

    change() {
      let files = null;
      files = this.$refs.image.files;

      if (!files.length) {
        return;
      } else {
        this.createImage(files[0]);
      }
    },

    createImage(file) {
      let reader = new FileReader();
      reader.onload = e => {
        this.media = e.target.result;
      };
      reader.readAsDataURL(file);
    },

    deleteCo(catIndex, index) {
      if (
        confirm(
          "You are about to delete " +
            this.subcategories[catIndex].subcategories[index].name
        )
      ) {
        this.disable = true;
        let url =
          domain +
          "subcat/" +
          this.subcategories[catIndex].subcategories[index].coid;
        axios
          .post(
            url,
            {
              _method: "delete"
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            if (res.data) {
              db.collection("subcategory ")
                .where(
                  "coid",
                  "==",
                  this.subcategories[catIndex].subcategories[index].coid
                )
                .get()
                .then(res => {
                  res.forEach(doc => {
                    doc.ref.delete();
                    return "done";
                  });
                  this.message = "Data Upload Success";
                  this.success = true;
                  this.disable = false;

                  this.edit = false;
                  this.disableId = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                })
                .catch(err => {
                  this.message = "Data Upload Firebase Failed";
                  this.success = true;
                  this.disable = false;

                  this.edit = false;
                  this.disableId = false;

                  setTimeout(() => {
                    this.success = false;
                    this.message = null;
                  }, 2000);
                });
              this.subcategories[catIndex].subcategories.splice(index, 1);
            }
          })
          .catch(err => {
            this.message = "Data Upload Mysql Failed";
            this.success = true;
            this.disable = false;

            this.edit = false;
            this.disableId = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    editSubCategory(catIndex, index) {
      if (this.subcategories) {
        //it's editing not adding
        this.edit = true;
        this.disableId = true;
        //details
        this.coid = this.subcategories[catIndex].subcategories[index].coid;
        this.name = this.subcategories[catIndex].subcategories[index].name;
        this.active = this.subcategories[catIndex].subcategories[index].active;
        this.subcatid = this.subcategories[catIndex].subcategories[
          index
        ].subcatid;
        this.displayOffer = this.subcategories[catIndex].subcategories[
          index
        ].displayOffer;
        this.image = this.subcategories[catIndex].subcategories[index].image;
        this.imageOption = true;
        this.media = null;

        this.fetchCoCategories(
          this.subcategories[catIndex].subcategories[index].coid
        );
        this.fetchCategory(
          this.subcategories[catIndex].subcategories[index].subcatid
        );
      }
    },

    fetchCategory(subcat) {
      let url = domain + "cat";
      axios
        .get(url, {
          params: {
            subcatcode: subcat
          }
        })
        .then(res => {
          this.category = res.data[0];
          // res.data[0].forEach(doc => {
          //    = doc;
          // });
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchCategories() {
      let url = domain + "cat";
      axios
        .get(url)
        .then(res => {
          res.data.forEach(doc => {
            this.categories.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchCoCategories(coid) {
      let url = domain + "cocat";
      axios
        .get(url, {
          params: {
            coid: coid
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            this.cocategories.push(doc);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    fetchSubCategories() {
      let url = domain + "cat";
      axios
        .get(url, {
          params: {
            subcategories: "budbak"
          }
        })
        .then(res => {
          res.data.forEach(doc => {
            let subcategories = [];
            doc.subcategories.forEach(subcat => {
              let set = {
                subcatid: subcat.subcatid,
                coid: subcat.coid,
                name: subcat.name,
                image: subcat.image,
                active: subcat.active,
                displayOffer: subcat.displayOffer
              };

              subcategories.push(set);
            });

            let category = {
              subcatcode: doc.subcatcode,
              name: doc.name,
              active: doc.active,
              subcategories: subcategories
            };
            this.subcategories.push(category);
          });
        })
        .catch(err => {
          console.log(err);
        });
    },

    goBack() {
      window.history.length > 1
        ? this.$router.go(-1)
        : this.$router.push("/inventory");
    },

    uploadImage() {
      let file = this.$refs.image.files[0];
      this.image = null;
      return firestorage
        .ref("o/" + file.name)
        .put(file)
        .then(res => {
          //downloading url
          let mediaURL = res.ref.getDownloadURL();
          return mediaURL;
        })
        .catch(err => {
          console.log(err);
          return null;
        });
    },

    uploadData() {
      if (this.edit != true) {
        let url = domain + "subcat";

        axios
          .post(
            url,
            {
              coid: this.coid,
              subcatid: this.subcatid,
              name: this.name,
              image: this.image,
              active: this.active,
              displayOffer: this.displayOffer
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            // data
            this.subcategories.forEach((cat, catIndex) => {
              if (cat.subcatcode == this.subcatid) {
                let subcat = {
                  subcatid: res.data.subcatid,
                  coid: res.data.coid,
                  name: res.data.name,
                  image: res.data.image,
                  active: res.data.active,
                  displayOffer: res.data.displayOffer
                };
                this.subcategories[catIndex].subcategories.unshift(subcat);
              }
            });

            db.collection("subcategory ")
              .add({
                coid: this.coid,
                subcatid: this.subcatid,
                name: this.name,
                image: this.image,
                active: this.active,
                displayOffer: this.displayOffer,
                uploadedAt: new Date(),
                updatedAt: new Date()
              })
              .then(res => {
                this.coid = null;
                this.subcatid = null;
                this.name = null;
                this.image = null;
                this.media = null;
                this.active = "1";
                this.displayOffer = 0;

                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                this.edit = false;
                this.disableId = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                console.log(err);
                this.coid = null;
                this.subcatid = null;
                this.name = null;
                this.image = null;
                this.media = null;
                this.active = "1";
                this.displayOffer = 0;

                //// to show
                this.message = "Data Upload Firebase Failed";
                this.success = true;
                this.disable = false;

                this.edit = false;
                this.disableId = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          })
          .catch(err => {
            // delete in firebase

            /// to show
            this.message = "Data Upload In MYSql Failed";
            this.success = true;
            this.disable = false;

            this.edit = false;
            this.disableId = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      } else {
        let url = domain + "subcat/" + this.coid;

        axios
          .post(
            url,
            {
              _method: "PUT",
              subcatid: this.subcatid,
              name: this.name,
              image: this.image,
              active: this.active,
              displayOffer: this.displayOffer
            },
            {
              withCredentials: true,
              headers: {
                "Content-Type": ContentType,
                "X-Requested-With": XRequestedWith,
                "X-api-token": firebase.auth().currentUser.uid
              }
            }
          )
          .then(res => {
            this.subcategories.forEach((cat, catIndex) => {
              cat.subcategories.forEach((doc, subIndex) => {
                if (doc.coid == this.coid) {
                  let subcat = {
                    subcatid: res.data.subcatid,
                    coid: res.data.coid,
                    name: res.data.name,
                    image: res.data.image,
                    displayOffer: res.data.displayOffer,
                    active: res.data.active
                  };
                  Vue.set(
                    this.subcategories[catIndex].subcategories,
                    subIndex,
                    subcat
                  );
                }
              });
            });
            //firebase
            db.collection("subcategory ")
              .where("coid", "==", this.coid)
              .get()
              .then(snap => {
                return snap.docs[0].ref.update({
                  subcatid: this.subcatid,
                  name: this.name,
                  image: this.image,
                  active: this.active,
                  displayOffer: this.displayOffer,
                  uploadedAt: new Date(),
                  updatedAt: new Date()
                });
              })
              .then(res => {
                this.coid = null;
                this.subcatid = null;
                this.name = null;
                this.image = null;
                this.media = null;
                this.active = "1";
                this.displayOffer = 0;
                //this.subcategories.push(res);

                //// to show
                this.message = "Data Upload Success";
                this.success = true;
                this.disable = false;

                this.edit = false;
                this.disableId = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              })
              .catch(err => {
                this.coid = null;
                this.subcatid = null;
                this.name = null;
                this.image = null;
                this.media = null;
                this.active = "1";
                this.displayOffer = 0;
                //this.subcategories.push(res);

                //// to show
                this.message = "Data Upload Firebase Failed";
                this.success = true;
                this.disable = false;

                this.edit = false;
                this.disableId = false;

                setTimeout(() => {
                  this.success = false;
                  this.message = null;
                }, 2000);
              });
          })
          .catch(err => {
            /// to show
            this.message = "Data Upload In MYSql Failed";
            this.success = true;
            this.disable = false;

            this.edit = false;
            this.disableId = false;

            setTimeout(() => {
              this.success = false;
              this.message = null;
            }, 2000);
          });
      }
    },

    validate() {
      this.errors = [];
      if (this.subcatid && this.name && this.coid) {
        return true;
      } else {
        if (!this.subcatid) {
          Vue.set(this.errors, 4, "Fill valid Item Id");
        }
        if (!this.name) {
          Vue.set(this.errors, 3, "Fill Name");
        }
        if (!this.coid) {
          Vue.set(this.errors, 2, "Choose Subcategory");
        }

        Vue.set(this.errors, 1, "Something is missing");
        return false;
      }
    }
  }
};
</script>

<style>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.modal-dialog {
  overflow-y: initial !important;
}
.modal-body {
  overflow-y: auto;
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
