import {
    domain,
    utcHours,
    utcMins,
    deliveryTime
} from "@/store/variable";
import store from "@/store/store";
import axios from "axios";


export const orderMixin = {

    data() {
        return {
            disable: false,
            orders: [],
        }
    },

    computed: {
        size() {
            return store.state.orderPageSize;
        }
    },

    mounted() {
        this.fetchOrders();
    },

    methods: {
        fetchOrders(page) {
            let url = domain + "order";
            if (!page) {
                page = 1;
            }
            this.disable = true;
            axios
                .get(url, {
                    params: {
                        status: this.status,
                        user: "budhbak",
                        products: "budhbak",
                        payment_mode: "budhbak",
                        userAddress: "budhbak",
                        order: "updated_at",
                        orderWay: "desc",
                        page: page,
                        limit: this.size
                    }
                })
                .then(res => {
                    let ordersList = [];
                    res.data.data.forEach(doc => {

                        let orderTime = this.getDateTime(doc.orderTime);
                        let date = orderTime[0];
                        let time = orderTime[1];
                        let estDeliveryTime = this.getExpectedDeliveryTime(doc.orderTime);


                        let order = {
                            orderId: doc.id,
                            products: doc.products,
                            orderedItemsCount: doc.orderedItemsCount,
                            availableItems: doc.availableItems,
                            paymentMode: doc.payment_mode,
                            orderDate: date,
                            orderTime: time,
                            payment: doc.totalPayableAmount,
                            paidAmount: doc.paidAmount,
                            placedFrom: doc.placedFrom,
                            orderStatus: doc.order_status,
                            user: doc.user,
                            ordererLocation: doc.ordererLocation,
                            estDeliveryTime: estDeliveryTime,
                            deliveryAddress: doc.user_address,
                            refundId: doc.refundId,
                            checked: false
                        };
                        ordersList.push(order);

                        store.dispatch("patchorderPageCount", res.data.last_page);
                        store.dispatch("patchorderPageNumber", res.data.current_page);
                        store.dispatch("patchtotalOrders", res.data.total);
                    });

                    this.orders = ordersList;
                });

            this.disable = false;
        },

        getExpectedDeliveryTime(orderTime) {
            if (orderTime) {
                let dateTime = new Date(orderTime);
                dateTime.setHours(dateTime.getHours() + utcHours);
                dateTime.setMinutes(dateTime.getMinutes() + utcMins + deliveryTime);
                let month = dateTime.getMonth() + 1;
                let date =
                    dateTime.getDate() +
                    "/" +
                    month +
                    "/" +
                    dateTime.getFullYear();
                var AmOrPm = dateTime.getHours() >= 12 ? "Pm" : "Am";
                var hours = dateTime.getHours() % 12 || 12;
                let time =
                    hours +
                    ":" +
                    dateTime.getMinutes() +
                    ":" +
                    dateTime.getSeconds() +
                    " " +
                    AmOrPm;

                return [date, time];
            }
        }

    },
}



