<template>
  <div v-if="message">
    <div
      class="alert alert-warning alert-dismissible fade show"
      role="alert"
      v-if="type=='warning'"
    >
      {{message}}
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <div
      class="alert alert-danger alert-dismissible fade show border-dotted border-danger"
      role="alert"
      v-if="type=='danger'"
    >
      {{message}}
      <button type="button" class="close" data-dismiss="alert" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "AlertBox",
  props: {
    message: String,
    type: String
  }
};
</script>



