<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col">
        <a href="#" @click.prevent="goBack()">
          <i
            class="fas fa-arrow-alt-circle-left text-dark mt-4 ml-3"
            style="font-size:28px; cursor:pointer;"
          ></i>
        </a>
      </div>
    </div>

    <div class="container">
      <!-- content -->
      <div class="row add-item-container">
        <div class="col-lg-2"></div>
        <div class="col-lg-8 col-md-12 card shadow p-0">
          <h5 class="card-header font-weight-bold">
            <i class="fas fa-plus-circle pl-1 pr-3"></i>PUSH NOTIFICATIONS
          </h5>
          <div class="row p-3">
            <div class="col-sm-12">
              <div class="bg-light-red-bg text-danger font-weight-bold p-2 ml-1">
                <i class="fas fa-exclamation-triangle pr-2"></i>Fields marked
                with ( * ) is important and is to be filled
              </div>

              <form class="form pb-5 mb-4">
                <!-- Title -->
                <div class="form-row">
                  <div class="col-sm-12">
                    <label class="active">
                      Title
                      <small>(Title of notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.title"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <small v-if="content.title==null" class="float-right">0/30</small>
                    <small
                      v-else-if="content.title!=null && content.title.length<=30"
                      class="float-right"
                    >{{ content.title.length }}/30</small>
                    <small
                      v-else-if="content.title.length>30"
                      class="float-right text-danger"
                    >{{ 30-content.title.length }}/30</small>
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[0]"
                    >{{ errors[0] }}</span>
                  </div>
                </div>

                <!--  subtitle -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label class="active" for="all category">
                      Subtitle
                      <small>(Subtitle of notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.subtitle"
                      required
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[1]"
                    >{{ errors[1] }}</span>
                  </div>
                </div>

                <!-- body -->
                <div class="form-row">
                  <div class="col">
                    <label>
                      Message *
                      <small>(Message of the notification)</small>
                    </label>
                    <textarea
                      placeholder="Body of the notification"
                      v-model="content.message"
                      class="form-control form-control-sm"
                    ></textarea>
                    <small class="float-right">
                      <small>Characters entered:</small>
                      <small v-if="content.message==null">
                        <b>0</b>
                      </small>
                      <small v-else-if="content.message.length<=62">
                        <b>{{content.message.length}}</b>
                      </small>
                      <small v-else-if="content.message.length<=110" class="text-info">
                        <b>{{content.message.length}}</b>
                      </small>
                      <small v-else class="text-danger">
                        <b>{{content.message.length}}</b>
                      </small>
                      <small class="pl-2">With Image: 62</small>
                      <small class="pl-2">Without Image: 110</small>
                    </small>

                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[2]"
                    >{{ errors[2] }}</span>
                  </div>
                </div>

                <!-- schedule , interval -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>
                      Schedule*
                      <small>(Do you wanna repeat it?)</small>
                    </label>
                    <select v-model="schedule" class="form-control form-control-sm">
                      <option
                        v-for="timer in schedules"
                        :key="timer.id"
                        :value="timer.id"
                        class="font-weight-bold"
                      >
                        {{ timer.name }}
                        <span v-if="timer.at">- {{ timer.at }}</span>
                      </option>
                    </select>
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[4]"
                    >{{ errors[4] }}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>
                      Interval*
                      <small>(After how long do you want it to repeat)</small>
                    </label>
                    <select
                      v-model="interval"
                      class="form-control form-control-sm"
                      @change="checkDates()"
                    >
                      <option
                        v-for="inter in intervals"
                        :key="inter.id"
                        :value="inter.id"
                        class="font-weight-bold"
                      >{{ inter.name }}</option>
                    </select>
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[5]"
                    >{{ errors[5] }}</span>
                  </div>
                </div>

                <!-- type , send to -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>
                      Type *
                      <small>(To let you define the type of notification)</small>
                    </label>
                    <select v-model="type" class="form-control form-control-sm">
                      <option
                        v-for="typo in types"
                        :key="typo.id"
                        :value="typo.id"
                        class="font-weight-bold"
                      >{{ typo.name }}</option>
                    </select>
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[6]"
                    >{{ errors[6] }}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>
                      Send To*
                      <small>(Receiver of notification)</small>
                    </label>
                    <select v-model="sendTo" class="form-control form-control-sm">
                      <option
                        v-for="receivers in receivers"
                        :key="receivers.id"
                        :value="receivers.id"
                        class="font-weight-bold"
                      >{{ receivers.name }}</option>
                    </select>
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[7]"
                    >{{ errors[7] }}</span>
                  </div>
                </div>

                <!-- Coid,  proid, orderid, actitle -->
                <div class="form-row">
                  <!-- cocat -->
                  <div class="col-sm-6" v-if="type == 2 || type == 3">
                    <label>CoId*</label>
                    <span
                      v-if="this.cocategory >= 0 && this.cocategory != null"
                    >{{ this.cocategories[this.cocategory].name }}</span>
                    <br />
                    <button
                      type="button"
                      class="btn btn-outline-success btn-sm btn-block font-weight-bold"
                      data-toggle="modal"
                      data-target="#itemCategories"
                      @click="fetchCategories()"
                    >Choose CoCategory</button>

                    <!-- cocat -->
                    <div id="itemCategories" class="modal fade" role="dialog">
                      <div class="modal-dialog modal-dialog-scrollable">
                        <!-- Modal content-->
                        <div class="modal-content">
                          <div class="modal-header">
                            <h5 class="modal-title font-weight-bold">
                              <i class="fas fa-clipboard-list pr-3"></i>Choose
                              Item Id
                              <small>(Item id is predefined)</small>
                            </h5>
                            <button type="button" class="close" data-dismiss="modal">&times;</button>
                          </div>
                          <div class="modal-body" style="position:relative;">
                            <h4
                              v-if="ModalContentLoading"
                              class="loading-content"
                              style="background: rgba(250,250,250 ,.9);"
                            >Kriypa Dhirya bnaye rkhe...</h4>
                            <div>
                              <label>Choose Category</label>
                              <select
                                v-model="category"
                                class="form-control form-control-sm"
                                @change="getSubCategories"
                              >
                                <option
                                  v-for="(category, index) in categories"
                                  :key="index"
                                  :value="index"
                                >{{ category.name }}</option>
                              </select>
                            </div>
                            <div>
                              <label>Choose Sub Category</label>
                              <select
                                aria-placeholder="This will get filled when you select category"
                                v-model="subcategory"
                                class="form-control form-control-sm"
                                :disabled="
                                  category == null && !ModalContentLoading
                                "
                                @change="getCoCategories"
                                v-if="subcategories"
                              >
                                <option disabled="disabled" selected="true">Choose Subcategory</option>
                                <option
                                  v-for="(subcategory, index) in subcategories"
                                  :key="index"
                                  :value="index"
                                >{{ subcategory.name }}</option>
                              </select>
                              <span v-else>There are no SubCategories in this yet</span>
                            </div>
                            <div>
                              <label>Choose Item Id</label>
                              <select
                                v-model="cocategory"
                                class="form-control form-control-sm"
                                :disabled="
                                  subcategory == null && !ModalContentLoading
                                "
                                @change="setCocategory()"
                                v-if="cocategories"
                              >
                                <option
                                  v-for="(cocategory, index) in cocategories"
                                  v-if="cocategory.active"
                                  :key="index"
                                  :value="index"
                                >
                                  <!-- <i
                                    class="fas fa-circle text-success"
                                    aria-hidden="true"
                                    v-if="cocategory.active == '1'"
                                  ></i>
                                  <i
                                    class="fas fa-circle text-danger"
                                    aria-hidden="true"
                                    v-else
                                  ></i>-->
                                  {{ cocategory.itemId }}
                                  {{ cocategory.name }}
                                </option>
                              </select>
                              <span v-else>This have no items yet</span>
                            </div>
                          </div>
                          <div class="modal-footer">
                            <button
                              type="button"
                              class="btn btn-danger btn-sm"
                              data-dismiss="modal"
                            >
                              <i class="fas fa-times-circle pr-2"></i>Close
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[0]"
                    >{{ errors[0] }}</span>
                  </div>
                  <div class="col-sm-6" v-if="type == 1">
                    <label class="active">
                      ProId
                      <small>((ProductId to be item in notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.proid"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[1]"
                    >{{ errors[1] }}</span>
                  </div>
                  <div class="col-sm-6" v-if="type == 6">
                    <label class="active">
                      OrderId
                      <small>(OrderId in notification)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.orderid"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    >
                  </div>
                  <div class="col-sm-6" v-if="type != 2 && type == 3">
                    <label class="active">
                      Actitle
                      <small>(Activity title to take you somewhere)</small>
                    </label>
                    <input
                      type="text"
                      v-model="content.actitle"
                      :disabled="disable"
                      class="form-control form-control-sm"
                    />
                    <!-- <span class="error text-danger font-weight-bold" v-if="errors[1]">{{errors[1]}}</span> -->
                  </div>
                </div>

                <!-- start , expiry date -->
                <div class="form-row">
                  <div class="col-sm-6">
                    <label>
                      Start Date*
                      <small>(When this notification should start)</small>
                    </label>
                    <input type="date" v-model="startDate" class="form-control form-control-sm" />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[8]"
                    >{{ errors[8] }}</span>
                  </div>
                  <div class="col-sm-6">
                    <label>
                      Expiry Date*
                      <small>(When this notification should expire)</small>
                    </label>
                    <input type="date" v-model="expiryDate" class="form-control form-control-sm" />
                    <span
                      class="error text-danger font-weight-bold"
                      v-if="errors[9]"
                    >{{ errors[9] }}</span>
                  </div>
                </div>

                <!-- image -->
                <div class="form-row">
                  <!-- image -->
                  <div class="col-sm-6 text-center">
                    <div class="switch">
                      <label>
                        Add Path *
                        <input
                          type="checkbox"
                          v-model="imageOption"
                          @click="media[0] = null"
                          :disables="disable"
                        />
                        <span class="lever"></span>
                        Upload
                      </label>
                    </div>

                    <div class="col-12" v-if="imageOption == false">
                      <label>Image Path</label>
                      <input
                        type="text"
                        v-model="image"
                        :disabled="disable"
                        class="form-control form-control-sm"
                      />
                    </div>

                    <div class="col-12 text-center" v-else>
                      <label class="btn btn-sm btn-info text-center" style="color:#fff">
                        <i class="fas fa-image pr-2"></i>
                        <div class="d-inline font-weight-bold">ADD IMAGE</div>
                        <input type="file" class="file" @change="change('image')" ref="image" />
                      </label>
                      <div class="d-inline-block" v-if="media[0]">
                        <a
                          class="float-right"
                          style="color:#424242;"
                          href="#"
                          title="delete this brand"
                          @click.prevent="removeImage('image', 0)"
                        >
                          <i class="fa fa-sm fa-times"></i>
                        </a>
                        <img :src="media[0]" class="img-fluid" />
                      </div>
                      <div class="d-inline-block" v-else-if="image">
                        <a
                          class="float-right"
                          style="color:#424242;"
                          href="#"
                          title="delete this brand"
                          @click.prevent="removeImage('image', 'image')"
                        >
                          <i class="fa fa-sm fa-times"></i>
                        </a>
                        <img :src="image" class="img-fluid" v-if="image" />
                      </div>
                    </div>
                  </div>
                </div>

                <!-- buttons -->
                <div class="form-row">
                  <div class="col text-center">
                    <button
                      class="btn btn-sm btn-go-green font-weight-bold d-inline"
                      type="submit"
                      @click.prevent="save"
                      :disabled="disable"
                    >
                      <i class="fas fa-save pr-1"></i>
                      SAVE
                    </button>
                    <router-link to="#" class="btn btn-sm btn-secondary">
                      <i class="fas fa-ban pr-1"></i>
                      <div class="font-weight-bold d-inline">CANCEL</div>
                    </router-link>
                  </div>
                </div>

                <div class="form-row" v-if="errors[10]">
                  <div class="col text-center text-danger">{{ errors[10] }}</div>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div class="col-lg-2"></div>
      </div>

      <Loader :disable="disable" :success="success" :message="message" />
    </div>
  </div>
</template>

<script>
import { pushNotificationMixin } from "@/components/Mixins/pushNotificationMixin.js";
import {
  domain,
  ContentType,
  AccessControlAllowOrigin,
  XRequestedWith
} from "@/store/variable";
import firebase from "firebase/app";
import axios from "axios";

export default {
  name: "SendPushNotification",
  mixins: [pushNotificationMixin],

  data() {
    return {
      disable: false,
      success: false,
      message: null
    };
  },

  methods: {
    uploadData() {
      let url = domain + "user/notification/push";
      if (this.type == 2 || this.type == 3) {
        this.content.itemId = this.cocategories[this.cocategory].itemId;
      }
      axios
        .post(
          url,
          {
            type: this.type,
            sendTo: this.sendTo,
            schedule: this.schedule,
            interval: this.interval,
            content: this.content,
            image: this.image,
            startDate: this.startDate,
            expiryDate: this.expiryDate
          },
          {
            withCredentials: true,
            headers: {
              "Content-Type": ContentType,
              "X-Requested-With": XRequestedWith,
              "X-api-token": firebase.auth().currentUser.uid
            }
          }
        )
        .then(res => {
          //// to show
          this.message = "Success";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        })
        .catch(err => {
          //// to show
          this.message = "Data Upload Failed";
          this.success = true;
          this.disable = false;

          setTimeout(() => {
            this.success = false;
            this.message = null;
          }, 2000);
        });
    },

    async save() {
      if (this.user && this.validateData()) {
        this.disable = true;
        this.disableMessage = "Uploading Data, Kripya Shanti banaye rkhe...";
        if (this.media[0]) {
          await this.uploadImage("image").then(downloadURL => {
            this.image = downloadURL;
          });
        }

        this.uploadData();
      }
    }
  }
};
</script>
<style scoped>
.form-row {
  padding-top: 1em;
}

.file {
  display: none;
}

.form-control-sm {
  font-weight: 600;
}

.media img {
  width: 17em;
}

.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

label {
  font-weight: 600;
  font-size: 13px;
  opacity: 0.9;
}
.add-item-container {
  margin-top: 20px !important;
}
.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #28a745 !important;
  outline: 0;
  -webkit-box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
  box-shadow: 0 0 0 0.2rem rgba(40, 167, 69, 0.3) !important;
}
</style>



